import { SHOW_FOOT_BAR, HIDE_FOOT_BAR } from './mutationTypes'

const actions = {
  showFootBar: ({ commit }) => {
    commit(SHOW_FOOT_BAR)
  },
  hideFootBar: ({ commit }) => {
    commit(HIDE_FOOT_BAR)
  },
}

export default actions



