import { mapActions } from 'vuex'

export default {
  props: {
    photoArray: {
      type: Array,
      defalut: () => {},
    },
    clockInAddressId: {
      type: String,
      defalut: '',
    },
    clockInAddress: {
      type: String,
      defalut: '',
    }
  },
  methods: {
    ...mapActions('attendance', [
      'submitClockIn',
    ]),
    bottomBtnOnClick() {
      const { clockInAddressId, clockInAddress } = this
      if ((this.clockInAddress === '定位失败') || (this.clockInAddress === '定位中...')) {
        this.$vux.alert.show({
          title: '提示',
          content: `定位失败，无法完成签到，请等候重新定位`,
        })
        return
      }
      if (this.clockInAddress == undefined || this.clockInAddress === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择签到位置`,
        })
        return
      }
      const photoSidList = this.photoArray.map(photoEle => {
        return photoEle.photoSid
      })
      if (Array.isArray(photoSidList) && photoSidList.length == 0) {
        this.$vux.alert.show({
          title: '提示',
          content: `请拍摄签到照片`,
        })
        return
      }

      this.submitClockIn({ clockInAddressId, clockInAddress, photoSidList }).then(resp => {
        this.$vux.alert.show({
          title: '提示',
          content: `签到完成`,
        })
        this.$router.push('/attendance')
      })
      // }).catch((error) => {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `${error}`,
      //   })
      // })
    },
  },
}



