import { mapGetters, mapActions, mapMutations } from 'vuex'
import CommonSelect from '@/components/commonSelect'
let keybordHeight = '0'

export default {
  components: {
    CommonSelect,
  },
  data() {
    return {
      test: '',
      STORE_LIST: 'store',
      SUGGESTED_LIST: 'suggested',

      selectType: 'store', // 目前選擇的篩選器(集團store/建議suggested)
      searchType: 'store',
      searchTextStr: '', // 搜索終端輸入文字
      store: {
        // 集團終端篩選器
        showOption: false, // 是否顯示集團終端篩選器
        forthList: [], // 四級地清單
        name: '', // 门店信息輸入文字
        owner: '', // 老板信息輸入文字
        selectForth: '', // 選擇的四級地
      },
      suggested: {
        // 建議終端篩選器
        showOption: false, // 是否顯示建議終端篩選器
        bigList: [], // 設施大類清單
        midList: [], // 設施中類清單
        subList: [], // 設施小類清單
        selectBig: '', // 選擇的設施大類
        selectMid: '', // 選擇的設施中類
        selectSub: '', // 選擇的設施小類
      },
      keybordHeight: keybordHeight,
    }
  },
  mounted() {
    window.getAndroidStatus = this.androidKeyboard
    this.queryForthCityList()
    // this.getForthList() // 取得四級地清單
    this.getCategoryList() // 取得設施大中小類清單
    this.store.fothList = this.getForthCityList
  },
  computed: {
    ...mapGetters('store', ['getForthCityList']),
    searchText: {
      get() {
        return this.searchTextStr
      },
      set(newValue) {
        this.searchTextStr = newValue
        if (newValue.length > 0) {
          this.checkFilterData(this.selectType)
        }
      },
    },
  },
  methods: {
    ...mapActions('store', ['queryPoiLevel', 'queryForthCityList']),
    searchBtnOnClick() {
      this.getCategoryList()
    },
    /* android 通知H5 頁面鍵盤高度 */
    androidKeyboard(height, pageUrl) {
      /* 此頁面輸入框不做任何處理 */
      return
    },
    checkFilterData(type, hasSearchCriteria = true) {
      // 檢核篩選器資料，再執行sendFilterData()
      if (this.searchType !== type) {
        // 若切換終端類型查詢，先清空篩選器選項及搜索終端輸入文字
        this.searchText = ''
      }
      if (type === this.STORE_LIST) {
        // 搜尋集團終端時，清空建議終端篩選器選項
        this.suggested.selectBig = ''
        this.suggested.selectMid = ''
        this.suggested.selectSub = ''
        this.$refs.bigSelect.selectedStr = '请选择'
        this.$refs.midSelect.selectedStr = '请选择'
        this.$refs.subSelect.selectedStr = '请选择'
      } else {
        // 搜尋建議終端時，清空集團終端篩選器選項
        this.store.selectForth = ''
        this.store.name = ''
        this.store.owner = ''
        this.$refs.forthSelect.selectedStr = '请选择'
      }
      this.sendFilterData(type, hasSearchCriteria)
    },
    showStoreOption() {
      // 顯示集團終端篩選器
      /* 第一次進入文字預設顯示：'全部' */
      if (this.$refs.forthSelect.selectedStr === '请选择') {
        this.$refs.forthSelect.selectedStr = '全部'
      }
      this.selectType = this.STORE_LIST
      this.store.showOption = !this.store.showOption
      this.suggested.showOption = false
      if (!this.store.showOption && this.searchType === this.SUGGESTED_LIST) {
        this.selectType = this.SUGGESTED_LIST
      }
    },
    showSuggestedOption() {
      // 顯示建議終端篩選器
      this.selectType = this.SUGGESTED_LIST
      this.suggested.showOption = !this.suggested.showOption
      this.store.showOption = false
      if (!this.suggested.showOption && this.searchType === this.STORE_LIST) {
        this.selectType = this.STORE_LIST
      }
    },
    // getForthList(selectedStr = '') { // 取得四級地清單
    // 假資料
    // this.store.forthList = ['閩行區1', '閩行區2', '閩行區3']
    // this.store.showOption = true
    // this.sendFilterData('store') // 傳送篩選器資料
    // return
    // console.log('selectedStr')
    // console.log(selectedStr)
    // this.queryForthCityList().then((resp) => {
    //   console.log('queryForthCityList')
    //   console.log(resp)
    //   if (resp.status == 200) {
    //     this.store.fothList
    //   }
    // })
    // this.$http({
    //   method: 'get',
    //   url: '/getForthCityList',
    // }).then((resp) => {
    //   const respData = resp.data
    //   if (respData.code === 'c200') {
    //     this.store.forthList = []
    //     respData.body.forEach((item) => {
    //       this.store.forthList.push(item.forthCityName)
    //     })
    //     // 取得四級地清單後發第一次請求取得終端清單
    //     this.store.showOption = true
    //     this.sendFilterData(this.STORE_LIST) // 傳送篩選器資料
    //   } else {
    //     // 請求失敗，尚未完成
    //   }
    // })
    // },
    getSelectForth(value) {
      // 取得選擇的四級地
      /* 增加全部選項，讓使用者可以回到預設狀態 */
      if (value === '全部') {
        value = ''
      }
      this.store.selectForth = value
    },
    getCategoryList(big = '', mid = '') {
      // 取得設施大中小類清單
      // 假資料
      // if (!big) { // 查詢大類清單
      //   this.suggested.bigList = ['汽车服务', '汽车销售', '汽车维修']
      // } else if (big && !mid) { // 查詢中類清單
      //   this.suggested.midList = ['购物相关场所', '商场', '便民商店/便利店']
      // } else if (big && mid) { // 查詢小類清單
      //   this.suggested.subList = ['7-ELEVEn便利店', 'OK便利店', '家电电子卖场']
      // }
      // return
      //
      // this.$http({
      //   method: 'get',
      //   url: '/getPoiTypeCategories',
      //   data: {
      //     param: [big, mid], // param名稱待確認！
      //   },
      // }).then((resp) => {
      //   const respData = resp.data
      //   if (respData.code === 'c200') {
      //     if (respData.body.categoryType === '0') { // 查詢大類清單
      //       this.suggested.bigList = respData.body.categoryNameList
      //     } else if (respData.body.categoryType === '1') { // 查詢中類清單
      //       this.suggested.midList = respData.body.categoryNameList
      //     } else if (respData.body.categoryType === '2') { // 查詢小類清單
      //       this.suggested.subList = respData.body.categoryNameList
      //     }
      //   } else {
      //     // 請求失敗，尚未完成
      //   }
      // })
      this.queryPoiLevel({
        requestLV1: big,
        requestLV2: mid,
      })
        .then((resp) => {
          if (resp.status == 200) {
            const respData = resp.data.data.data
            if (big === '') {
              // 查詢大類清單
              this.suggested.bigList = respData
            } else if (big !== '' && mid === '') {
              // 查詢中類清單
              this.suggested.midList = respData
            } else if (big !== '' && mid !== '') {
              // 查詢小類清單
              this.suggested.subList = respData
            }
          }
        })
        .catch((error) => {
          // this.$vux.alert.show({
          //   title: '提示',
          //   content: `${error}`,
          // })
        })
    },
    getSelectBig(value) {
      // 取得選擇的設施大類
      this.suggested.selectBig = value
      this.getCategoryList(this.suggested.selectBig, '')
    },
    getSelectMid(value) {
      // 取得選擇的設施中類
      this.suggested.selectMid = value
      this.getCategoryList(this.suggested.selectBig, this.suggested.selectMid)
    },
    getSelectSub(value) {
      // 取得選擇的設施小類
      this.suggested.selectSub = value
    },
    searchKeyword() {
      // 輸入關鍵字後按enter
      this.store.showOption = true
      this.suggested.showOption = true
      this.sendFilterData(this.searchType)
    },
    sendFilterData(type, hasSearchCriteria = true) {
      // 傳送篩選器資料
      this.searchType = type
      let filterData = {}
      if (type === 'store') {
        // 集團終端
        filterData = {
          type,
          options: {
            keyword: this.searchText,
            forthName: this.store.selectForth,
            storeName: this.store.name,
            storeOwner: this.store.owner,
          },
          hasSearchCriteria,
        }
        this.store.showOption = true
        this.showStoreOption()
      } else {
        // 建議終端
        filterData = {
          type,
          options: {
            keyword: this.searchText,
            bigCategory: this.suggested.selectBig,
            midCategory: this.suggested.selectMid,
            subCategory: this.suggested.selectSub,
          },
          hasSearchCriteria,
        }
        this.suggested.showOption = true
        this.showSuggestedOption()
      }
      this.$emit('to-parent', filterData)
    },
  },
}



