/* eslint-disable */
import commonHeader from '@/components/commonHeader'
import PhotoCollector from '@/components/PhotoCollector'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    commonHeader,
    PhotoCollector,
  },
  data() {
    return {
      title: '下班签到',
      height: document.body.offsetHeight - 63 - 45, //列表清单高度
      photoArray: [],
    };
  },
  mounted() {
    this.$navbar.attendanceCheckOutStyle()
  },
  methods: {
    ...mapActions('attendance', [
      'submitClockOut',
    ]),
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    takePhoto(photoType) {
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    bottomBtnOnClick() {
      const photoSidList = this.photoArray.map(photoEle => {
        return photoEle.photoSid
      })
      if (Array.isArray(photoSidList) && photoSidList.length < 1) {
        this.$vux.alert.show({
          title: '提示',
          content: `请拍照以完成签退`,
        })
        return        
      }

      this.submitClockOut(photoSidList).then(resp => {

        const self = this
        this.$vux.confirm.show({
          title: '提示',
          content: '签退完成，是否需要数据同步？',
          onConfirm() {
            self.$navbar.syncDataPreviousPage = '/attendance'
            self.$router.push('sync_data')
          },
          onCancel() {
            self.$router.push('/attendance')
          },
        })

        // this.$vux.alert.show({
        //   title: '提示',
        //   content: `签退完成`,
        // })
        // this.$router.push('/attendance')
      })
      // }).catch((error) => {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `${error}`,
      //   })
      // })
    },
    back() {
      window.history.back()
    },
  }
};



