
import FastClick from 'fastclick'
import Vue from 'vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'


import App from './App'
import router from './router'
import store from './store'

import devicePlugin from './plugins/device'
import ajaxPlugin from './plugins/ajax'
import footBarPlugin from './plugins/footbar'
import filterPlugin from './plugins/filter'
import nativePlugin from './plugins/native'
import navBarPlugin from './plugins/navbar'
import amapPlugin from './plugins/amap'

import '../static/css/common.css'

Vue.use(devicePlugin)
Vue.use(ajaxPlugin)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)
Vue.use(DatetimePlugin)
Vue.use(footBarPlugin)
Vue.use(filterPlugin)
Vue.use(nativePlugin)
Vue.use(navBarPlugin)
Vue.use(amapPlugin)


Vue.config.productionTip = false
Vue.component('Scroller', Scroller)
FastClick.attach(document.body)

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
render: h => h(App),
created () {



if (!String.prototype.includes) {
String.prototype.includes = function(search, start) {
'use strict';
if (typeof start !== 'number') {
start = 0;
}

if (start + search.length > this.length) {
return false;
} else {
return this.indexOf(search, start) !== -1;
}
};
}




if (!Array.prototype.some) {
Array.prototype.some = function(fun/*, thisArg*/) {
'use strict'
if (this == null) {
throw new TypeError('Array.prototype.some called on null or undefined')
}
if (typeof fun !== 'function') {
throw new TypeError()
}
var t = Object(this)
var len = t.length >>> 0
var thisArg = arguments.length >= 2 ? arguments[1] : void 0;
for (var i = 0; i < len; i++) {
if (i in t && fun.call(thisArg, t[i], i, t)) {
return true
}
}
return false
}
}



if (!Array.prototype.find) {
Object.defineProperty(Array.prototype, 'find', {
value: function(predicate) {

if (this == null) {
throw new TypeError('"this" is null or not defined');
}

var o = Object(this);


var len = o.length >>> 0;


if (typeof predicate !== 'function') {
throw new TypeError('predicate must be a function');
}


var thisArg = arguments[1];


var k = 0;


while (k < len) {




var kValue = o[k];
if (predicate.call(thisArg, kValue, k, o)) {
return kValue;
}

k++;
}


return undefined;
},
configurable: true,
writable: true
});
}


},
mounted () {





},
methods: {






































































}
})



