export default {
  props: {
    list: {
      type: Array,
      default() {
        return []
      },
    },
  },
  methods: {
    sendEventData(eventSid, eventDate, storeName) { // 傳送點選的eventSid
      const eventData = {
        eventSid,
        eventDate,
        storeName
      }
      this.$emit('to-parent', eventData)
    },
  },
}



