    //       storeState: '',
    //     }]
    //   }
    //   return
    //   const urlPath = this.type === STORE_LIST ? '/searchStore' : '/searchSuggestedPOI'
    //   this.options.pageSize = 20
    //   this.options.pageNumber = 1
    //   this.$http({
    //     method: 'get',
    //     url: urlPath,
