import {
  TAKE_PICTURE,
  QUERY_VISIT_DETAIL,
  QUERY_DISPLAY_TYPE,
  QUERY_STORE_PRODUCT_LIST,
  SET_STORE_PRODUCT_DETAIL_VISIBLE,
  CALCULATE_PRODUCT_DETAIL_TOTAL,
  SET_PHOTO_SPECIAL_DISPLAY_TYPE,
  SUBMIT_PHOTO_SPECIAL_DISPLAY_TYPE,
  CLEAR_PRODUCT_DETAIL_TOTAL,
} from './mutationTypes'

const mutations = {
  [TAKE_PICTURE](state, payload) {
    // state.empInfo = {
    //   empId: payload.empId,
    //   empName: payload.empName,
    // }
  },
  [QUERY_VISIT_DETAIL](state, payload) {
    state.visitDetail = payload
    state.specialVisitPhotoInfoListArray = [] // specialVisit 本身是多個 List 的集合，所以在push之前要先清空
    for (const visitPhotoEle of payload.visitPhotoInfo) {
// 照片採集 門店照
      if (visitPhotoEle.photoDisplayType === 'DOOR_WAY') {
        state.doorWayPhotoInfoList = visitPhotoEle.photoInfoList
// 照片採集 其他照
      } else if (visitPhotoEle.photoDisplayType === 'UN_VISIT') {
        state.unVisitPhotoInfoList = visitPhotoEle.photoInfoList
// 照片採集 其他照
      } else if (visitPhotoEle.photoDisplayType === 'OTHER') {
        state.otherPhotoInfoList = visitPhotoEle.photoInfoList
// 特陳檢核
      } else if (visitPhotoEle.photoDisplayType === 'SPECIAL_DISPLAY') {
// 特陳檢核 specialDisplayTypeSid 為空
        if (visitPhotoEle.specialDisplayTypeSid === '') {
          const uncategorizedPhotoInfo = Object.assign({}, visitPhotoEle) // Object.assign 淺拷貝
          // 添加 specialDisplayTypeName 為 '未分类'
          uncategorizedPhotoInfo.specialDisplayTypeName = '未分类'
          state.uncategorizedPhotoInfo = uncategorizedPhotoInfo
          state.specialVisitPhotoInfoListArray.push(uncategorizedPhotoInfo)
        } else {
// 特陳檢核 其餘 typeSid 不為空的各組照片，編成一本
          state.specialVisitPhotoInfoListArray.push(visitPhotoEle)
        }
      }
    }
  },
  [QUERY_DISPLAY_TYPE](state, payload) {
    state.displayTypeList = payload
  },
  [QUERY_STORE_PRODUCT_LIST](state, payload) {
    state.storeProductList.totalStockQty = parseFloat(payload.totalStockQty)
    state.storeProductList.totalOrderQty = parseFloat(payload.totalOrderQty)
    state.storeProductList.productStockOrderList = payload.productStockOrderList
    for (const productEle of state.storeProductList.productStockOrderList) {
      productEle.subStockQty = parseFloat(productEle.subStockQty)
      productEle.subOrderQty = parseFloat(productEle.subOrderQty)
      for (const productDetailItem of productEle.productList) {
        productDetailItem.stockQty = parseFloat(productDetailItem.stockQty)
        productDetailItem.orderQty = parseFloat(productDetailItem.orderQty)
      }
    }
  },
  [SET_STORE_PRODUCT_DETAIL_VISIBLE](state, payload) {
    state.storeProductList.productStockOrderList[payload.index].isDetailVisible = payload.isDetailVisible
  },
  [CALCULATE_PRODUCT_DETAIL_TOTAL](state, payload) { // param:{ index, detailIndex, stockOrOrder, newValue }
    const calculationProduct = state.storeProductList.productStockOrderList[payload.index]
    const calculationProductDetailItem = calculationProduct.productList[payload.detailIndex]
    const exceptMyself = {}
    exceptMyself.productSubStock = calculationProduct.subStockQty - calculationProductDetailItem.stockQty
    exceptMyself.totalStockQty = parseFloat(state.storeProductList.totalStockQty) - calculationProductDetailItem.stockQty
    exceptMyself.productSubOrder = calculationProduct.subOrderQty - calculationProductDetailItem.orderQty
    exceptMyself.totalOrderQty = parseFloat(state.storeProductList.totalOrderQty) - calculationProductDetailItem.orderQty
    // 直接輸入
    if (payload.newValue !== '' || payload.newValue != NaN) {
      const newValue =  parseFloat(payload.newValue)
      if (payload.stockOrOrder === 'stock') {
        // calculationProductDetailItem.stockQty = payload.newValue
        calculationProductDetailItem.stockQty = parseFloat((newValue).toFixed(2))
        // calculationProduct.subStockQty = exceptMyself.productSubStock + payload.newValue == 0 ? -1 : payload.newValue // 明細品項底部 庫存總和
        // state.storeProductList.totalStockQty = exceptMyself.totalStockQty + payload.newValue == 0 ? -1 : payload.newValue // 頁面底部 庫存總和
        calculationProduct.subStockQty = (exceptMyself.productSubStock + newValue).toFixed(2) // 明細品項底部 庫存總和
        state.storeProductList.totalStockQty = (exceptMyself.totalStockQty + newValue).toFixed(0) // 頁面底部 庫存總和
      } else if (payload.stockOrOrder === 'order') {
        calculationProductDetailItem.orderQty = parseFloat((newValue).toFixed(2))
        // calculationProduct.subOrderQty = exceptMyself.productSubOrder + payload.newValue == 0 ? -1 : payload.newValue // 明細品項底部 訂單總和
        // state.storeProductList.totalOrderQty = exceptMyself.totalOrderQty + payload.newValue == 0 ? -1 : payload.newValue // 頁面底部 訂單總和
        calculationProduct.subOrderQty = (exceptMyself.productSubOrder + newValue).toFixed(2) // 明細品項底部 訂單總和
        state.storeProductList.totalOrderQty = (exceptMyself.totalOrderQty + newValue).toFixed(0) // 頁面底部 訂單總和
      }
    }
    // 將 productId重複 的部分 過濾掉
    state.calculationStockOrderList = state.calculationStockOrderList.filter(product => product.productId !== calculationProductDetailItem.productId)
    // 新增到 submit 用的 array 裡
    state.calculationStockOrderList.push({
      visitSid: state.visitDetail.visitSid,
      productId: calculationProductDetailItem.productId,
      stockQty: calculationProductDetailItem.stockQty,
      orderQty: calculationProductDetailItem.orderQty,
    })
  },
  [CLEAR_PRODUCT_DETAIL_TOTAL](state, payload) {
    state.calculationStockOrderList = []
  },
  [SET_PHOTO_SPECIAL_DISPLAY_TYPE](state, payload) { // param:{ specialDisplayTypeSid, photoList }
    const { specialDisplayTypeSid, photoList } = payload
    state.photoSpecialDisplayType.specialDisplayTypeSid = specialDisplayTypeSid
    state.photoSpecialDisplayType.photoList = photoList
  },
  [SUBMIT_PHOTO_SPECIAL_DISPLAY_TYPE](state, payload) { // param:{ specialDisplayTypeSid, photoList }
    const { isSubmit } = payload
    state.photoSpecialDisplayType.isSubmit = isSubmit
  },
}

export default mutations



