import device from '../plugins/device'
import { mapGetters } from 'vuex'

export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    middleTitle: {
      type: String,
      default: '',
    },
    leftBtnTitle: {
      type: String,
      default: '返回',
    },
    leftBtnImageUrl: {
      type: String,
      default: '/home',
    },
    leftBtnLinkTo: {
      type: String,
      default: '/home',
    },
    rightBtnTitle: {
      type: String,
      default: '下頁 >>',
    },
    rightBtnImageUrl: {
      type: String,
      default: '',
    },
    rightBtnLinkTo: {
      type: String,
      default: '/store',
    },
    // rightBtnEvent: {
    //   type: Function,
    //   default: () => {
    //     console.log('rightBtnLinkTo')
    //   },
    // },
  },
  data() {
    return {
      rightBtnStyle: '',
    }
  },
  computed: {
    ...mapGetters('navBar', [
      'getNavBarLeftBtnLinkTo',
    ]),
  },
  mounted() {
    this.rightBtnStyle = `right-btn-block_img-text`
    // alert(`height:${document.body.offsetHeight}`)
    // alert(`width:${document.body.offsetWidth}`)
    if (device.isAndroid && this.middleTitle !== '首页') {
      window.navbarBackBtnTouchUpInside = this.leftBtnClick  // Native 是在 android 端已經註冊好的 key，nativeFunctionHandler 是 android 的 webView 所在的 class 內部持有的方法
    }
  },
  methods: {
    rightBtnClick(rightBtnLinkTo) {
      this.$parent.$emit('navbarRightBtnClick', 'im param')
      // this.$parent >> el: App
      // $emit is not allow promise object
      // this.$parent.$emit('navbarRightBtnClick','im param').then(resp => {
      //   console.log('call back?')
      //   console.log(resp)
      // })

      // if (rightBtnLinkTo !== '') {
        this.$router.push(`${rightBtnLinkTo}`)
      // } else if (typeof this.rightBtnEvent === 'function') {
      //   console.log('this.rightBtnEvent')
      //   console.log(typeof this.rightBtnEvent)
      //   this.rightBtnEvent
      // }
    },
    leftBtnClick(leftBtnLinkTo) {
      if (this.leftBtnTitle !== '') {
        this.getNavBarLeftBtnLinkTo !== '' ? this.$router.push(`${this.getNavBarLeftBtnLinkTo}`) : this.$router.go(-1)
      }
    },
  },
}



