const getters = {
  getStoreList(state) {
    return state.storeList
  },
  getPageInfo(state) {
    return state.pageInfo
  },
  getSelectedStoreDetail(state) {
    return state.storeDetailEle
  },
  getWechatDefaultMarketPlaceList(state) {
    return state.defaultMarketPlaceList
  },
  getWechatMarketPlaceList(state) {
    return state.marketPlaceList
  },
  getSelectedMarketPlaceEle(state) {
    return state.selectedMarketPlaceEle
  },
  getForthCityList(state) {
    return state.forthCityList
  },
  getSelectedListType(state) {
    return state.selectedListType
  },
}

export default getters



