import { mapGetters, mapActions, mapMutations } from 'vuex'
import LaunchOrderProductListCalculator from '@/pages/normalVisit/components/LaunchOrderProductListCalculator'
import {
  SET_STORE_PRODUCT_DETAIL_VISIBLE,
  CALCULATE_PRODUCT_DETAIL_TOTAL,
} from '../module/mutationTypes'

let keybordHeight = '0'

export default {
  components: {
    // LaunchOrderProductListCell,
    // LaunchOrderProductListCellDetail,
    LaunchOrderProductListCalculator,
  },
  data() {
    return {
      calculatorSelectedItem: {},

      // isDetailVisible: false,
      searchText: '',
      detailTitle: '贝比妈妈米饼_50g',
      calculatorInventoryNumber: 50,
      calculatorOrderNumber: 50,
      productArray: [{}, {}, {}, {}, {}, {}, {}, {}, {}],
      detailArray: [],
      keybordHeight: keybordHeight,
      inputType: '',
      totalStockQty:'',
      totalOrderQty:'',
    }
  },
  computed: {
    ...mapGetters('normalVisit', ['getStoreProductList']),
    filteredList() {
      
      const filteredList = this.getStoreProductList.productStockOrderList.filter((productEle) => {
        if (this.searchText === '') {
          return true
        }
        console.log(`必須`)
        console.log(productEle.productList)
        const isIncludingName = productEle.productGroupName.includes(this.searchText) // 魅族手機不支援 includes()，專案內新增在 main.js
        const isIncludingDetailName = productEle.productList.some((detailItem) => { // 魅族手機不支援 some()，專案內新增在 main.js
          return detailItem.productName.includes(this.searchText)
        })
        return isIncludingName || isIncludingDetailName
      })
      if (Array.isArray(filteredList) && filteredList.length > 0) {
        this.SET_STORE_PRODUCT_DETAIL_VISIBLE({ index: 0, isDetailVisible: true })
      }
      return filteredList
    },
  },
  beforeRouteLeave(to, from, next) {
    // has access to `this` component instance}
    this.submitStockAndOrder().then(() => {
      this.CLEAR_PRODUCT_DETAIL_TOTAL()
    })
    next()
  },
  mounted() {
    window.getAndroidStatus = this.androidKeyboard
    this.calculatorSelectedItem.mounted = true
    // this.queryStoreProductList()
    // this.productArray = [{},{},{},{},{},{},{},{},{}]

    // this.totalStockQty = this.getStoreProductList.totalStockQty
    // this.totalOrderQty = this.getStoreProductList.totalOrderQty

    setTimeout(() => { this.computed_qut() }, 200)

  },
  methods: {
    ...mapMutations('normalVisit', [
      'SET_STORE_PRODUCT_DETAIL_VISIBLE', // map `this.SET_STORE_PRODUCT_DETAIL_VISIBLE()` to `this.$store.commit('SET_STORE_PRODUCT_DETAIL_VISIBLE')`
      'CALCULATE_PRODUCT_DETAIL_TOTAL',
      'CLEAR_PRODUCT_DETAIL_TOTAL',
    ]),
    ...mapActions('normalVisit', ['queryStoreProductList', 'submitStockAndOrder']),
    /* android 通知H5 頁面鍵盤高度 */
    androidKeyboard(height, pageUrl) {
      let inputType = this.inputType
      if (inputType === 'top') {
        /* 搜索輸入框不做任何處理 */
        return
      } else if (inputType === 'calc') {
        let page = document.getElementById('app')
        if (height === '0') {
          // 鍵盤收起
          page.style.top = '0'
        } else {
          page.style.top = '-' + height + 'px'
        }
      }
    },
    clickInput(type) {
      this.inputType = type
    },
    showDetail(index, isDetailVisible) {
      this.SET_STORE_PRODUCT_DETAIL_VISIBLE({ index, isDetailVisible })
      // this.$store.commit(`normalVisit/normalVisit/SET_STORE_PRODUCT_DETAIL_VISIBLE`,{ index, isDetailVisible})
      // this.getStoreProductList[index].isDetailVisible = visible
      // this.$set(this.getStoreProductList, index, this.getStoreProductList[index]) // https://cn.vuejs.org/v2/guide/list.html 注意事項！
    },
    calculatorBtnHasBeenClicked(operator, detailIndex, index, stockOrOrder, newValue) {
      if (newValue) {
        this.CALCULATE_PRODUCT_DETAIL_TOTAL({
          operator,
          detailIndex,
          index,
          stockOrOrder,
          newValue,
        })
        // this.$store.commit(`normalVisit/${CALCULATE_PRODUCT_DETAIL_TOTAL}`,{ operator, detailIndex, index, stockOrOrder, newValue })
      } else {
        this.CALCULATE_PRODUCT_DETAIL_TOTAL({ operator, detailIndex, index, stockOrOrder })
        // this.$store.commit(`normalVisit/${CALCULATE_PRODUCT_DETAIL_TOTAL}`,{ operator, detailIndex, index, stockOrOrder })
      }
    },
    calculatorHasBeenSelected(index, detailIndex, stockOrOrder) {
      if (this.calculatorSelectedItem.mounted) {
        this.calculatorSelectedItem.index = index
        this.calculatorSelectedItem.detailIndex = detailIndex
        this.calculatorSelectedItem.stockOrOrder = stockOrOrder
      }
    },
    afterCalculatorValueChange(newValue) {
      console.log('afterCalculatorValueChange: '+newValue)
      if (this.calculatorSelectedItem.mounted) {
        const { index, detailIndex, stockOrOrder } = this.calculatorSelectedItem
        this.$store.commit(`normalVisit/${CALCULATE_PRODUCT_DETAIL_TOTAL}`, {
          index,
          detailIndex,
          stockOrOrder,
          newValue,
        })
      }
      setTimeout(() => { this.computed_qut() }, 200)

    },
    getTotalStockQty(totalStockQty){
      this.totalStockQty = totalStockQty
    },
    getTotalOrderQty(totalOrderQty){
      this.totalOrderQty = totalOrderQty
    },
    computed_qut(){
      var left_ipt = document.getElementsByClassName('left_iptval');
      let element = 0;
      for (let index = 0; index < left_ipt.length; index++) {
        if ( left_ipt[index].value > 0 ) {
          element += 1;
        }
      }
      this.totalStockQty = element
      

      var righ_iptval = document.getElementsByClassName('righ_iptval');
      let element2 = 0;
      for (let index = 0; index < righ_iptval.length; index++) {
        if ( righ_iptval[index].value > 0 ) {
          element2 += 1;
        }
      }
      this.totalOrderQty = element2
    }
  },
}



