// import axios from 'axios'
import {
  QUERY_DAILY_VISIT_PLAN,
  SET_SELECTED_VISIT_ELE,
  SET_APPLICATION_DETAIL_ELE,
  QUERY_UN_VISIT_STORE_STATUS,
  QUERY_ATTENDANCE_DAILY_INFO,
  QUERY_CLOCK_DETAIL,
  QUERY_EMP_INFO,
} from './mutationTypes'

const actions = {
  queryEmpInfo(context) { // getDailyVisitPlan
    return new Promise((resolve, reject) => {
      const functionName = 'getEmpInfo'
      this.$native.execute(functionName).then((resp) => { // success ,param:resp
        context.commit(QUERY_EMP_INFO, resp.body)
        resolve(resp)
      })
    })
  },
  queryDailyVisitPlan(context) { // getDailyVisitPlan
    const functionName = 'getDailyVisitPlan'
    this.$native.execute(functionName).then((resp) => { // success ,param:resp
      // if (resp.data.code === 'c200') {
      if (resp.code === 'c200') {
        // context.commit(QUERY_DAILY_VISIT_PLAN, resp.data.body)
        context.commit(QUERY_DAILY_VISIT_PLAN, resp.body)
      }
    }, () => { // fail ,param:error
      // alert('getVisitDetail fail')
    })
  },
  setSelectedVisitEle(context, selectedVisitEle) {
    context.commit(SET_SELECTED_VISIT_ELE, selectedVisitEle)
  },
  queryUnVisitStoreStatus(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getUnVisitStoreStatus'
      this.$native.execute(functionName).then((resp) => {
        if (resp.code === 'c200') {
          context.commit(QUERY_UN_VISIT_STORE_STATUS, resp.body)
          resolve()
        } else {
          reject()
        }
      })
    })
  },
  querySpecificDayVisitInfo(context, pickedDate) {
    return new Promise((resolve, reject) => {
      const functionName = 'getSpecificDayVisitInfo'
      const param = { planDate: pickedDate }
      this.$native.execute(functionName, param).then((resp) => {
        // this.dailyVisitPlan = resp.body.storeVisitInfoList
        // context.commit(QUERY_UN_VISIT_STORE_STATUS, resp.body)
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryOutServiceMonthlyInfo(context, pickedDate) {
    return new Promise((resolve, reject) => {
      const functionName = 'getOutServiceMonthlyInfo'
      const { year, month } = pickedDate
      this.$native.execute(
        functionName,
        {
          year,
          month,
        },
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryOutServiceDetail(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getOutServiceDetail'
      const outServiceSid = context.rootState.personal.selectedApplicationEle.outServiceSid
      this.$native.execute(
        functionName,
        { outServiceSid },
      ).then((resp) => {
        context.commit(SET_APPLICATION_DETAIL_ELE, resp.body)
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryAttributeInfoList(context, attributeType = 'OUTSERVICE') { // “EVENT”:活动类型  “OUTSERVICE”:报备类型 ，取申請類型
    return new Promise((resolve, reject) => {
      // const functionName = attributeType === 'OUTSERVICE' ? 'getAttributeInfoList' : 'getAttributeInfoListFOREVENT'
      const functionName = 'getAttributeInfoList'
      this.$native.execute(
        functionName,
        { attributeType },
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryClockDetail(context, pickedDate) { // “EVENT”:活动类型  “OUTSERVICE”:报备类型 ，取申請類型
    return new Promise((resolve, reject) => {
      const functionName = 'getClockDetail'
      this.$native.execute(
        functionName,
        { pickedDate },
      ).then((resp) => {
        context.commit(QUERY_CLOCK_DETAIL, resp.body)
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  submitOutService(context, param) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitOutService'
      this.$native.execute(
        functionName,
        param,
        // {
        //   submitType, // 0 新增, 1 修改
        //   outServiceSid,
        //   outServiceType,
        //   startTime,
        //   endTime,
        //   reason,
        //   photoSidList,
        //  },
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryAttendanceDailyInfo(context, pickedDate) {
    return new Promise((resolve, reject) => {
      const functionName = 'getAttendanceDailyInfo'
      this.$native.execute(
        functionName,
        { pickedDate },
      ).then((resp) => {
        context.commit(QUERY_ATTENDANCE_DAILY_INFO, resp.body)
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  removeVisitPlan(context, visitSid) {
    return new Promise((resolve, reject) => {
      const functionName = 'removeVisitPlan'
      this.$native.execute(
        functionName,
        { visitSid },
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  submitAddVisitPlanMultiStores(context, param) {
    return new Promise((resolve, reject) => {
      const functionName = 'addVisitPlanMultiStores'
      this.$native.execute(
        functionName,
        param,
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },
}

export default actions



