import {
  SHOW_NAV_BAR,
  HIDE_NAV_BAR,
  SET_NAV_BAR_MIDDLE_TITLE,
  // SET_NAV_BAR_LEFT_BUTTON,
  // SET_NAV_BAR_RIGHT_BUTTON,
  SET_NAV_BAR_LEFT_BUTTON_IMAGE_URL,
  SET_NAV_BAR_RIGHT_BUTTON_IMAGE_URL,
  SET_NAV_BAR_LEFT_BUTTON_TITLE,
  SET_NAV_BAR_RIGHT_BUTTON_TITLE,
  SET_NAV_BAR_RIGHT_BUTTON_LINK_TO,
  SET_NAV_BAR_LEFT_BUTTON_LINK_TO,
} from './mutationTypes'

const actions = {
  showNavBar(context) {
    context.commit(SHOW_NAV_BAR)
  },
  hideNavBar(context) {
    context.commit(HIDE_NAV_BAR)
  },
  setNavBarMiddleTitle(context, payload = {}) {
    context.commit(SET_NAV_BAR_MIDDLE_TITLE, payload)
  },
  setNavBarLeftBtn(context, payload = {}) {
    context.commit(SET_NAV_BAR_LEFT_BUTTON_TITLE, payload.title)
    context.commit(SET_NAV_BAR_LEFT_BUTTON_IMAGE_URL, payload.imageUrl)
    context.commit(SET_NAV_BAR_LEFT_BUTTON_LINK_TO, payload.linkTo)
  },
  setNavBarRightBtn(context, payload = {}) {
    context.commit(SET_NAV_BAR_RIGHT_BUTTON_TITLE, payload.title)
    context.commit(SET_NAV_BAR_RIGHT_BUTTON_IMAGE_URL, payload.imageUrl)
    context.commit(SET_NAV_BAR_RIGHT_BUTTON_LINK_TO, payload.linkTo)
  },
  // setNavBarRightBtnTitle(context, payload) {
  //   context.commit(SET_NAV_BAR_RIGHT_BUTTON_TITLE, payload)
  // },
  // setNavBarRightBtnImage(context, payload) {
  //   context.commit(SET_NAV_BAR_RIGHT_BUTTON_IMAGE_URL, payload)
  // },
  // setNavBarRightBtnLinkTo(context, payload) {
  //   context.commit(SET_NAV_BAR_RIGHT_BUTTON_LINK_TO, payload)
  // },
  // setNavBarLeftBtnTitle(context, payload) {
  //   context.commit(SET_NAV_BAR_LEFT_BUTTON_TITLE, payload)
  // },
  // setNavBarLeftBtnImage(context, payload) {
  //   context.commit(SET_NAV_BAR_LEFT_BUTTON_IMAGE_URL, payload)
  // },
  // setNavBarLeftBtnLinkTo(context, payload) {
  //   context.commit(SET_NAV_BAR_LEFT_BUTTON_LINK_TO, payload)
  // },
}

export default actions



