import SFAIcon from '@/assets/img/user_info/SFA.png'

export default {
  data () {
    return {
      sfaIcon: '',
      versionName: '',
    }
  },
  mounted () {
    this.sfaIcon = SFAIcon
    this.$navbar.versionInfoStyle()
    this.versionName = this.$route.params.versionName
  }
}



