import { mapGetters, mapActions, mapMutations } from 'vuex'
import RecordList from '../components/RecordList'
import MonthPicker from '../components/MonthPicker'
import Laydate from 'layui-laydate'
import '@/../static/laydate.css'

export default {
  data () {
    return {
      recordList: [],
    }
  },
  components: {
    RecordList,
    MonthPicker,
  },
  mounted () {
    this.$navbar.myApplicationRecordStyle()
  },
  // beforeRouteLeave(to, from, next) {
  //   if (to.path === '/my_application_detail') {
  //   } else {
  //     next()
  //   }    
  // },
  methods: {
    ...mapMutations('personal', [
      'SET_SELECTED_APPLICATION_ELE',
    ]),
    ...mapActions('personal', [
      'queryOutServiceMonthlyInfo',
      'queryOutServiceDetail',
		]),
    getYearMonth(data) { // 取得選擇的年月
      this.queryOutServiceMonthlyInfo(data).then((resp) => {
        this.recordList = resp.body
      })
    },
    cellHandler(index) {
      this.SET_SELECTED_APPLICATION_ELE(this.recordList[index])
      this.queryOutServiceDetail().then((resp) => {
        this.$router.push('my_application_detail')
      })
    },
  },
}



