import axios from 'axios'
import store from '@/store'
import device from '../device'
import promiseExtension from '../promise_extension'
import { ENETRESET } from 'constants';
import Vue from 'vue'

// promise 集合
let promises = {}
// 生成 uuid 保持 promise 唯一
const generateUUID = () => {
  let d = new Date().getTime()
  let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    // /[xy]/g, g 表示將每個 xy 都帶進來，
    // 圆括号可以匹配多个连续的字符，而一对方括号只能匹配单个字符
    let r = ((d + Math.random() * 16) % 16) | 0
    d = Math.floor(d / 16)
    // Math.floor() 函式會回傳無條件捨去後的最大整數。
    return (c == 'x' ? r : (r & 0x3) | 0x8).toString(16)
    // c 如果是 x ,, return r.toString(16)
    // c 如果不是 x ,, return (r & 0x3) | 0x8
  })
  return uuid
}

// 建立 promise，實際使用：this.$native.execute(functionName:'getStoreInfo',param:{ empId, empName})
const nativeExecute = (functionName, param) => {
// const execute = (functionName, param) => {
  const promise = new Promise((resolve, reject) => {
    const promiseId = generateUUID()
    promises[promiseId] = { resolve, reject } // 這行很重要，在全域的的陣列物件內開一個以 promiseid 為索引的， promise 的存放位置
    try {
      if (device.isIphone || device.isIpad) {
        window.webkit.messageHandlers.nativeFunctionHandler.postMessage({  // nativeFunctionHandler 是在 ios 端已經註冊好的 key，
          functionName,
          promiseId,
          param,
        })
      } else if (device.isAndroid) {
        Native.nativeFunctionHandler(  // Native 是在 android 端已經註冊好的 key，nativeFunctionHandler 是 android 的 webView 所在的 class 內部持有的方法
          functionName,
          promiseId,
          JSON.stringify(param)
        )
      } else {
        nativeExecute(functionName, param).then(resp => {
          promises[promiseId].resolve(resp)
        }).catch(e => { promises[promiseId].reject(e) })
      }
    } catch (exception) {
      promises[promiseId].reject()
    }
  })
  // return promiseExtension.setTimout(promise)
  return promise
}

// for test
const execute = (functionName, param) => {
// const nativeExecute = (functionName, param) => {
  const promise = new Promise((resolve, reject) => {
    const promiseId = generateUUID()
    console.log('functionName:')
    console.log(functionName)
    console.log('param:')
    console.log(param)
    promises[promiseId] = { resolve, reject } // 這行很重要，在全域的的陣列物件內開一個以 promiseid 為索引的， promise 的存放位置
    try {
      // 打本機json-server的資料
      // axios.defaults.baseURL = 'http://10.231.48.239:3000/'
      // axios.defaults.baseURL = 'https://pooouqmliisjax1k2bulbg-on.drv.tw/sfa_json_server/'
      axios.defaults.baseURL = '../../../static/json/'
      axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded'
      axios({
        method: 'get',
        url: `/${functionName}`,
        params: param,
      }).then((resp) => {
        if (resp.data.code === 'c200') {
          resolve(resp.data)
        } else if (resp.data.code === 'c110' || resp.data.code === 'c500') {
          reject(resp.data)
        } else {
          Vue.$vux.alert.show({
            title: '提示',
            content: `${resp.data.message}`,
          })
          reject(resp.data.message)
        }
      }).catch((error) => {
        reject(error)
      })
    } catch (exception) {
      reject(exception)
    }
  })
  // return promiseExtension.setTimout(promise)
  return promise
}


// 在 native 內部定義好的接收 native 的回摳，根據 promiseId 選出位於 promises 內部的對應 promise
const resolvePromise = (functionName, promiseId, resp) => {
  try {
    if (resp.code === 'c200') {
      promises[promiseId].resolve(resp)
    } else if (resp.message !== '') {
      if (resp.code === 'c110' || resp.code === 'c500' ) {
        promises[promiseId].reject(resp)
      } else {
        Vue.$vux.alert.show({
          title: '提示',
          content: `${resp.message}`,
        })
        promises[promiseId].reject(resp)
      }
    } else {
      promises[promiseId].reject(resp)
    }
  } catch (error) {
    Vue.$vux.alert.show({
      title: '提示',
      content: `${error}`,
    })
    promises[promiseId].reject(error)
  }
  // remove referenfe to stored promise
  delete promises[promiseId]
}

const plugin = {
  install(Vue) {
    Vue.mixin({
      mounted() {
        window.resolvePromise = resolvePromise // 暴露給 native 的接口
      }
    });
  },
  generateUUID,
  execute,
  nativeExecute,
}

export default plugin;
// happy colding ^_^ y Wilson


