import { mapGetters, mapActions, mapMutations } from 'vuex'
import CommonHeader from '@/components/commonHeader'
import MultiDatepicker from '@/pages/store/components/MultiDatepicker'

export default {
  components: {
    CommonHeader,
    MultiDatepicker,
  },
  data() {
    return {
      selectDate: [],
    }
  },
  computed: {
    ...mapGetters('store', [
      'getSelectedStoreDetail',
      'getSelectedMarketPlaceEle',
      'getSelectedListType',
    ]),
  },
  mounted() {
    this.$navbar.addStoreStyle()
    // 隐藏菜单栏
  },
  methods: {
    ...mapActions('store', [
      'submitAddVisitPlan',
    ]),
    getDate(date) { // 取得選擇的日期
      this.selectDate = Object.keys(date)
    },
    add() { // 添加到路線
      // eslint-disable-next-line
      // alert(this.selectDate)
      this.submitAddVisitPlan({
        expectedDate: this.selectDate, // 计划拜访日期 , 只能是今天之后 // [ "2018-03-21" , "2018-05-31" ]
      }).then((resp) => {
        this.$vux.alert.show({
          title: '提示',
          content: `添加路线成功`,
        })
        this.$router.push('store')
      })
      // }).catch((error) => {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `${error}`,
      //   })
      // })
    },
  },
}



