import SyncSuccessGif from '../../../assets/img/common/sync_finish.gif'
import SyncLoadingGif from '../../../assets/img/common/sync_loading.gif'
import SyncTipsIcon from '../../../assets/img/common/sync_tips.png'
import sjtb from '../../../assets/img/nav_bar/home_icon_sjtb@2x.png'

export default {
  data () {
    return {
      syncSuccessGif: SyncSuccessGif,
      syncLoadingGif: SyncLoadingGif,
      syncTipsIcon: SyncTipsIcon,
      isSyncSuccess: false,
      isSyncFailure: false,
      failureTotalCount: 0,
      failureInfos: {
      // {
      // "resourceName" : "拜访计划",
      // "recordQty" : "3"
      // },
      // {
      // "resourceName" : "位置信息",
      // "recordQty" : "2"
      // }
      },
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$parent.$off('navbarRightBtnClick') // 取消訂閱
    next()
  },
  mounted () {
    const empInfo = this.$store.getters['personal/getEmpInfo']
    const navBarMidTitle = this.$store.getters['navBar/getNavBarMiddleTitle']
    // BaiduMobStat.onEventWithAttributes('btn_syncData', '数据同步', 1, {'user':`${empInfo.loginId}`},{'page': `${navBarMidTitle}`})
    this.$navbar.syncDataStyle()
    const self = this
    setTimeout(() => {
      self.syncData()
    }, 500)
  },
  methods: {
    syncData() {
      if (this.$store.getters['navBar/getNavBarRightBtnTitle'] === '重新同步') {
        this.$navbar.syncDataStyle()
        this.isSyncFailure = false
        this.$parent.$off('navbarRightBtnClick') // 取消訂閱
      }
      const functionName = 'syncData'
      this.$store.$native.execute(
        functionName,
      ).then((resp) => {
        this.$store.dispatch('navBar/setNavBarLeftBtn', { title: '返回', imageUrl: '', linkTo: this.$navbar.syncDataPreviousPage })
        this.$store.dispatch('navBar/setNavBarMiddleTitle', '数据同步结果')
        this.isSyncSuccess = true
      }).catch((error) => {
        this.$store.dispatch('navBar/setNavBarLeftBtn', { title: '返回', imageUrl: '', linkTo: this.$navbar.syncDataPreviousPage })
        this.$store.dispatch('navBar/setNavBarMiddleTitle', '数据同步结果')

        if (this.$store.getters['navBar/getNavBarRightBtnTitle'] !== '重新同步') {
          this.$store.dispatch('navBar/setNavBarRightBtn', { title: '重新同步', imageUrl: sjtb, linkTo: '' })
          this.$parent.$on('navbarRightBtnClick',(param => { // 註冊訂閱
            this.syncData()
          }))
        }

        if (error.code === 'c500') {
          this.isSyncFailure = true
          this.failureInfos = error.body
          this.failureTotalCount =  this.sumDataMapReduce(this.failureInfos)
        }
      })     
    },
    sumDataMapReduce(arr) {
      return arr.map(el=>parseInt(el.recordQty)).reduce((a,b)=> a+b)
    },
  },
}



