const getters = {
  getDailyVisitPlan(state) {
    return state.dailyVisitPlan
  },
  getSelectedVisitEle(state) {
    return state.selectedVisitEle
  },
  getUnvisitStoreStatus(state) {
    return state.unvisitStoreStatus
  },
  getAbnormalVisitList(state) {
    return state.abnormalVisitList
  },
}

export default getters



