// import axios from 'axios'
import {
  QUERY_DAILY_VISIT_PLAN,
  SET_SELECTED_VISIT_ELE,
  QUERY_UN_VISIT_STORE_STATUS,
} from './mutationTypes'

const actions = {
  queryDailyVisitPlan(context) { // getDailyVisitPlan
    const functionName = 'getDailyVisitPlan'
    this.$native.execute(functionName).then((resp) => { // success ,param:resp
      // if (resp.data.code === 'c200') {
      if (resp.code === 'c200') {
        // context.commit(QUERY_DAILY_VISIT_PLAN, resp.data.body)
        context.commit(QUERY_DAILY_VISIT_PLAN, resp.body)
      }
    }, () => { // fail ,param:error
      // alert('getVisitDetail fail')
    })
  },
  setSelectedVisitEle(context, selectedVisitEle) {
    context.commit(SET_SELECTED_VISIT_ELE, selectedVisitEle)
  },
  queryUnVisitStoreStatus(context, visitSid) {
    return new Promise((resolve, reject) => {
      const functionName = 'getUnVisitStoreStatus'
      const param = { visitSid }
      this.$native.execute(functionName, param).then((resp) => {
        if (resp.code === 'c200') {
          context.commit(QUERY_UN_VISIT_STORE_STATUS, resp.body)
          resolve()
        } else {
          reject()
        }
      })
    })
  },
}

export default actions



