import { mapGetters, mapActions} from 'vuex'
import BlueTickList from '../components/BlueTickList'
import PhotoCollector from '@/components/PhotoCollector'
import SubmitClockInButton from './SubmitClockInButton'

export default {
  data () {
    return {
      customerPicked: false,
      photoArray: [],
      selectedItem: {},
    }
  },
  components: {
    BlueTickList,
    PhotoCollector,
    SubmitClockInButton,
  },
  computed: {
    ...mapGetters('attendance', [
      'getCustomerDepartureAddressList',
    ]),
  },
  mounted() {
    this.queryDepartureAddressList('1') // 0:营业所 1:经销商
  },
  methods: {
    ...mapActions('attendance', [
      'queryDepartureAddressList',
      'submitClockIn',
    ]),
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
		]),
    takePhoto(photoType) {
      // if (this.selectedItem.clockInName == undefined) {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `请先选择地点`,
      //   })
      //   return
      // }
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType, attendanceLocationName: this.selectedItem.clockInName }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    refereshData() {
      // this.queryVisitDetail().then(resp => {
      // })
    },
    getCustomerCheckIn (ele){
      //样式处理
      // this.customerPicked = index
      this.selectedItem = ele
    },
    // bottomBtnOnClick() {
    //   const { clockInAddressId, clockInAddress } = this.selectedItem
    //   const photoSidList = this.photoArray.map(photoEle => {
    //     return photoEle.photoSid
    //   })
    //   this.submitClockIn({ clockInAddressId, clockInAddress, photoSidList }).then(resp => {
    //     if (resp.code === 'c200') {
    //       this.$router.push('/home')
    //     }
    //   })
    // },
  }
}



