
const fillZero = n => (n > 9 ? n : `0${n}`)
const dateToStrYYYYmmDD = d => (`${d.getFullYear()}-${fillZero(d.getMonth() + 1)}-${fillZero(d.getDate())}`)
const dateToStrHHmm = d => (`${fillZero(d.getHours())}:${fillZero(d.getMinutes())}`)
const dateToStrYYYYmmDDhhMM = d => (`${dateToStrYYYYmmDD(d)} ${dateToStrHHmm(d)}`)
const isDateObj = d => (d instanceof Date)
const today = new Date()
// const todayAfterAHour = d => (today.setHours(today.getHours()))
// const isAfterTomorow = d => (new Date(`${d} 23:59`))
const isAfterToday = d => (d >= new Date())
const isBeforeTomorrow = d => (d < new Date()) // 今天如果大於 選擇的日期23:59 就 true
// const isBeforeTomorrow = d => (d < today.setDate(today.getDate() + 1))
// const isBeforeTomorrow = d => (new Date(today.setDate(today.getDate)))
const strToDate = str => (new Date(str.replace(/-/g, '/')))
// const strToDateNow = str => (new Date(`${str.replace(/-/g, '/')} `))
const strToDateEnd = str => (new Date(`${str.replace(/-/g, '/')} 23:59`))
const strToDateStart = str => (new Date(`${str.replace(/-/g, '/')} 01:00`))
// var newtime= new Date(arr[0], arr[1] - 1, arr[2], arr[3], arr[4], arr[5]);
// arr[0] 年
// arr[1] - 1 月
// arr[2] 日
// arr[3] 時
// arr[4] 分
// arr[5] 秒
const sumDataMapReduce = arr => (arr.map(el=>el.salary).reduce((a,b)=>a+b))
const plugin = {
  install(Vue) {
    const $vm = Vue
    if (!$vm.$filter) {
      $vm.prototype.$filter = {
        fillZero,
        dateToStrYYYYmmDD,
        dateToStrHHmm,
        dateToStrYYYYmmDDhhMM,
        isDateObj,
        strToDate,
        isAfterToday,
        isBeforeTomorrow,
        strToDateEnd,
        strToDateStart,
        sumDataMapReduce,
      }
    }
  },
}
export default plugin



