import {
  TAKE_PICTURE,
  QUERY_VISIT_DETAIL,
  QUERY_DISPLAY_TYPE,
  QUERY_STORE_PRODUCT_LIST,
} from './mutationTypes'

const actions = {
  scanAssistantQRCode(context, specialDisplayTypeSid = '') {
    return new Promise((resolve, reject) => {
      const visitSid = context.rootState.home.selectedVisitEle.visitSid
      const functionName = 'scanAssistantQRCode'
      this.$native.execute(
        functionName,
        {
          visitSid,
          specialDisplayTypeSid,
        },
      ).then((resp) => {
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  checkPhotoArrayCount(context, {self, photoArray}) {
    return new Promise((resolve, reject) => {
      if (!Array.isArray(photoArray)) {
        resolve()
      } else if (Array.isArray(photoArray) && photoArray.length < 5) {
        resolve()
      } else {
        self.$vux.alert.show({
          title: '提示',
          content: `已达拍照数量上限`,
        })
        reject()
      }
    })
  },
  takePicture(context, { photoType = '', specialDisplayTypeSid = '', attendanceLocationName = '' }) {
    return new Promise((resolve, reject) => {
      const functionName = 'takePicture'
      // const visitSid = specialDisplayTypeSid === '' ? context.rootState.home.selectedVisitEle.visitSid : ''
      const visitSid = context.rootState.home.selectedVisitEle.visitSid ? context.rootState.home.selectedVisitEle.visitSid : ''
      // 1：LOADING_HEAD 装车照片
      // 2：STORE_INFO 店招拍照
      // 3：DAILY_REPORT 实际基础陈列
      // 4：DISPLAY 实际特殊陈列  // 0608改 SPECIAL_DISPLAY
      // 5：UNUSUAL_VISIT 异常拜访拍照
      // 6：CLOCK_IN 出发拍照
      // 7：CLOCK_OUT 返抵拍照
      // 8：CUSTOMER_VISIT 拜访客户照片
      // 9：CUSTOMER_STOCK 库存盘点照片
      // 10：OUT_STORE 店招照片
      // 11：IN_STORE 店内照片
      const param = {
        visitSid,
        photoType, // photoDisplayType ,from: getVisitDetail
        specialDisplayTypeSid, // specialDisplayTypeSid ,from: getDisplayType
        attendanceLocationName,
      }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        if (resp.body == '' || resp.body == undefined) {
          reject()
        }
        // context.commit(TAKE_PICTURE, resp.data.body)
        context.commit(TAKE_PICTURE, resp.body)
        resolve(resp.body)
      }, () => { // fail ,param:error
        reject()
      })
    })
  },
  queryVisitDetail(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getVisitDetail'
      const param = { visitSid: context.rootState.home.selectedVisitEle.visitSid }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        // context.commit(QUERY_VISIT_DETAIL, resp.data.body)
        context.commit(QUERY_VISIT_DETAIL, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  queryDisplayType(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getDisplayType'
      const param = { visitSid: context.rootState.home.selectedVisitEle.visitSid }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        context.commit(QUERY_DISPLAY_TYPE, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  queryStoreProductList(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getStoreProductList'
      const storeId = context.rootState.home.selectedVisitEle.storeId
      const visitSid = context.rootState.home.selectedVisitEle.visitSid
      const param = { visitSid, storeId }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        let index = 0
        for (const productEle of resp.body.productStockOrderList) {
          productEle.index = index
          index += 1
          if (productEle === resp.body.productStockOrderList[0]) {
            productEle.isDetailVisible = true
          } else {
            productEle.isDetailVisible = false
          }
        }
        context.commit(QUERY_STORE_PRODUCT_LIST, resp.body)
        resolve(resp.body)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  submitStockAndOrder(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitStockAndOrder'
      const calculationStockOrderList = context.rootState.normalVisit.calculationStockOrderList
      this.$native.execute(functionName, calculationStockOrderList).then((resp) => { // success ,param:resp
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  // submitPhotoSpecialDisplayType(context) {
  //   return new Promise((resolve, reject) => {
  //     const functionName = 'setPhotoSpecialDisplayType'
  //     const param = {
  //       visitSid: context.rootState.home.selectedVisitEle.visitSid
  //       specialDisplayTypeSid,
  //       photoList,
  //     }
  //     this.$native.execute(functionName, param).then(resp => {
  //       resolve(resp)
  //     }).catch(e => {
  //       reject(e)
  //     })
  //   })
  // },
  submitFinishVisit(context, params) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitFinishVisit'
      const visitSid = context.rootState.home.selectedVisitEle.visitSid
      const storeId = context.rootState.home.selectedVisitEle.storeId
      const { isForce = '0', visitType = '1', visitStoreStatusCode = '', otherStoreStatusName = '', visitRemark = '', photoSidList = [] } = params
      const param = {
        visitSid,
        storeId,
        visitType,
        visitStoreStatusCode, // 正常拜訪可忽略
        otherStoreStatusName, // 正常拜訪可忽略
        visitRemark, // 正常拜訪可忽略
        photoSidList,
        isForce,
      }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  // submitGiveUpVisit(visitSid) {
  //   return new Promise((resolve, reject) => {
  //     const functionName = 'giveUpVisit'
  //     this.$native.execute(functionName, visitSid).then((resp) => { // success ,param:resp
  //       resolve(resp)
  //     }).catch((e) => { // fail ,param:error
  //       reject(e)
  //     })
  //   })
  // },
}

export default actions



