import { mapGetters, mapActions } from 'vuex'
import LeftLightStickPureTextCell from '@/components/LeftLightStickPureTextCell'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    LeftLightStickPureTextCell,
    PhotoCollector,
  },
  computed: {
    ...mapGetters('personal', [
      'getApplicationDetailEle',
    ]),
    textAreaValue: {
      get() {
        return this.textValue
      },
      set(newValue) {
        console.log(newValue)
        this.textValue = newValue
      },
    },
    photoSidList() {
      return this.photoArray.map((photoEle) => photoEle.photoSid)
    }
  },
  data () {
    return {
      detailEle: {},
      isClockCell: false,
      isEditable: false,
      textValue: '',
      photoArray: [],
    }
  },
  mounted() {
    this.$navbar.myApplicationDetailStyle()
    this.textAreaValue = this.getApplicationDetailEle.outServiceInfo.reason
    this.photoArray = [...this.getApplicationDetailEle.photoInfoList]
    this.detailEle = {...this.getApplicationDetailEle}
    const startTime = this.detailEle.outServiceInfo.startTime.substring(0, 10)
    const pickDate = this.$filter.strToDateEnd(startTime)
    if (this.$filter.isAfterToday(pickDate) && this.detailEle.outServiceInfo.outServiceStatus != 0) { // outServiceStatus: 0 為已取消, 1 為已提交
      this.isEditable = true
    }
    // if (this.$route.params.isClockCell == true) {
    //   this.isClockCell = true
    //   this.detailEle = this.getAttendanceDetailEle
    //   // this.detailEle = 
    // } else {
    //   this.detailEle = this.getApplicationDetailEle
    // }
  },
  methods: {
    ...mapActions('personal', [
      'queryOutServiceDetail',
      'submitOutService',
    ]),
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
		]),
    selectionOnClick(title, content, index) {

    },
    takePhoto(photoType) {
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    cancelBtnOnClick() {
      const self = this
      this.$vux.confirm.show({
        title: '提示',
        content: '确认后将作废此报备申请！',
        onConfirm () {
          self.submitCancelOutService()
        },
        onCancel () {
          console.log('cancel')
        },
      })
    },
    submitCancelOutService() {
      const functionName = 'submitCancelOutService'
      const { outServiceSid } = this.getApplicationDetailEle.outServiceInfo
      const param = { outServiceSid }
      this.$store.$native.execute(functionName, param).then((resp) => {
        this.$vux.alert.show({
          title: '提示',
          content: `取消成功`,
        })
        this.$router.push('my_application_record')
      }).catch((e) => {
        // this.$vux.alert.show({
        //   title: '提示',
        //   content: `取消失败,${e}`,
        // })
      })
    },
    modifyBtnOnClick() {
      const {
        outServiceSid,
        outServiceType,
        startTime,
        endTime,
      } = this.getApplicationDetailEle.outServiceInfo
      const param = {
        submitType: '1', // 0 新增, 1 修改
        outServiceSid,
        outServiceType,
        startTime,
        endTime,
        reason: this.textAreaValue,
        photoSidList: this.photoSidList,
      }
      // this.submitOutService(param).then((resp) => {
      //   if (resp.code === 'c200') {
      //     this.$router.push('/my_application_record')
      //   }
      // })
      this.$router.push({ name: 'my_application_request', params: { applicationDetailEle: this.getApplicationDetailEle, submitParam:param }})
    },
    selectorTextOnChange(textValue) {
      this.selectorReturnEle.content = textValue
    },
  },
}



