export default {
  mounted () {
    this.$navbar.mySettingStyle()
  },
  methods: {
    clearCache() {
      this.$router.push('/developing')
    },
    queryVersion() {
      const functionName = 'getVersionInfo'
      this.$store.$native.execute(
        functionName,
      ).then((resp) => {
        this.$router.push({ name:'version_info', params: { versionName: resp.body.nativeVersionName}})
      })
    },
    logout() {
      const self = this
      this.$vux.confirm.show({
        title: '提示',
        content: '确定退出登录？',
        onConfirm () {
          const functionName = 'logout'
          const isForce = '0'
          const param = { isForce }
          self.$store.$native.execute(
            functionName,
            param,
          ).then((resp) => {
            if (resp.code === 'c200') {
              self.$store.dispatch('navBar/setNavBarLeftBtn', { title: '', imageUrl: '', linkTo: '' })
              self.$router.push('login')
            }
          }).catch((error) => {
            if (error.code === 'c110') {
              setTimeout(() => {
                self.$vux.confirm.show({
                  title: '提示',
                  content: `${error.message}`,
                  onConfirm() {
                    // 強制送出
                    const functionName = 'logout'
                    const isForce = '1'
                    const param = { isForce }
                    self.$store.$native.execute(
                      functionName,
                      param,
                    ).then((resp) => {
                      self.$router.push('login')
                    })
                  },
                })
              }, 500)
            }
          })
        },
      })
    },
    syncData() {
      this.$navbar.syncDataPreviousPage = '/my_setting'
      this.$router.push('sync_data')    
    },
    uploadLogAndDB() {
      const functionName = 'uploadLogAndDB'
      this.$store.$native.execute(
        functionName,
      ).then((resp) => {
        if (resp.code === 'c200') {
          this.$vux.alert.show({
            title: '提示',
            content: `上传成功`,
          })
        }
      })      
    },
  }
}



