import Laydate from 'layui-laydate'
// import '@/../static/laydate.css'

export default {
  props: {
    initDateStr: {
      type: String,
      default: '',
    }
  },
  data() {
    return {
      selectDate: {},
    }
  },
  mounted() {
    this.today()
  },
  methods: {
    initCalendar() {
      const calendar = Laydate.render({
        elem: '#datePicker',
        theme: '#ffffff',
        position: 'static',
        // mark: this.selectDate,
        isInitValue: true, //是否允许填充初始值，默认为 true
        value: this.initDateStr, //必须遵循format参数设定的格式
        showBottom: false,
        done: (value, date) => { // 點選日期
          // Object.prototype.hasOwnProperty 確認物件是否有 this.selectDate 
          // if (Object.prototype.hasOwnProperty.call(this.selectDate, value)) {
          //   delete this.selectDate[value]
          //   // document.querySelector  //querySelector() 方法返回匹配指定 CSS 选择器元素的第一个子元素 。
          //   document.querySelector('.layui-this').textContent = date.date
          // } else {
          //   this.selectDate[value] = ''
          //   const addMark = `<span class="laydate-day-mark">${date.date}</span>`
          //   document.querySelector('.layui-this').innerHTML = addMark
          // }
          // calendar.config.mark = this.selectDate
          this.$emit('to-parent', value)
        },
      })
      const todayStr = this.$filter.dateToStrYYYYmmDD(new Date())
      this.$emit('to-parent',  (typeof this.initDateStr === 'string' && this.initDateStr !== '') ? this.initDateStr : todayStr)
    },
    today() { // 預設選擇今天
      const today = new Date()
      const year = today.getFullYear()
      const month = (today.getMonth() + 1) > 9 ? (today.getMonth() + 1) : `0${today.getMonth() + 1}`
      const date = today.getDate() > 9 ? today.getDate() : `0${today.getDate()}`
      this.selectDate[`${year}-${month}-${date}`] = ''
      this.initCalendar()
    },
  },
}



