const getters = {
  isNavBarVisible(state) {
    return state.isNavBarVisible
  },
  getNavBarMiddleTitle(state) {
    return state.middleTitle
  },
  getNavBarLeftBtnImageUrl(state) {
    return state.navBarLeftBtnImageUrl
  },
  getNavBarLeftBtnTitle(state) {
    return state.navBarLeftBtnTitle
  },
  getNavBarLeftBtnLinkTo(state) {
    return state.navBarLeftBtnLinkTo
  },
  getNavBarRightBtnImageUrl(state) {
    return state.navBarRightBtnImageUrl
  },
  getNavBarRightBtnTitle(state) {
    return state.navBarRightBtnTitle
  },
  getNavBarRightBtnLinkTo(state) {
    return state.navBarRightBtnLinkTo
  },
  getNavBarRightBtnEvent(state) {
    return state.navBarRightBtnEvent
  },
}

export default getters



