const getters = {
  getVisitDetail(state) {
    return state.visitDetail
  },
  getDisplayType(state) {
    return state.displayTypeList
  },
  getStoreProductList(state) {
    return state.storeProductList
  },
  getDoorWayPhotoInfoList(state) {
    return state.doorWayPhotoInfoList
  },
  getOtherPhotoInfoList(state) {
    return state.otherPhotoInfoList
  },
  getSpecialVisitPhotoInfoListArray(state) {
    return state.specialVisitPhotoInfoListArray
  },
  getUncategorizedPhotoInfo(state) {
    return state.uncategorizedPhotoInfo
  },
}

export default getters



