
import {
  QUERY_EVENT_LIST,
  QUERY_EVENT_DETAIL,
  SUBMIT_EVENT,
} from './mutationTypes'

const actions = {
  queryEventList(context, { year, month }) {
    return new Promise((resolve, reject) => {
      console.log(year)
      console.log(month)
      const functionName = 'getEventList'
      const param = { year, month }
      this.$native.execute(
        functionName,
        param,
      ).then((resp) => { // success ,param:resp
        console.log(resp)
        context.commit(QUERY_EVENT_LIST, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  queryEventDetail(context, eventSid) {
    return new Promise((resolve, reject) => {
      const functionName = 'getEventDetail'
      this.$native.execute(
        functionName,
        eventSid,
      ).then((resp) => { // success ,param:resp
        context.commit(QUERY_EVENT_DETAIL, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  submitEvent(context, param) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitEvent'
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        // context.commit(SUBMIT_EVENT, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
}

export default actions



