const getAddressFromLngLat = (lng, lat) => { // 逆地理编码，根据经纬度获取详细地址信息
  return new Promise((resolve, reject) => {
    // 寫法二
    let geocoder = {}
    resolve(lng)
    AMap.service('AMap.Geocoder', () => {
      geocoder = new AMap.Geocoder({})
    })
    const lanlat = new AMap.LngLat(lng, lat)
    const lnglatXY = [lanlat.getLng(), lanlat.getLat()]
    // resolve(lnglatXY)
    // console.log()
    geocoder.getAddress(lnglatXY, (status, result) => {
      if (status === 'complete' && result.info === 'OK') {
        // this.eventDetail.address = result.regeocode.formattedAddress
        // resolve(result.regeocode.formattedAddress)
      }
      const error = {}
      error.message = 'get address fail'
      reject(error)
    })
  })
}


const plugin = {
  install(Vue) {
    const $vm = Vue
    if (!$vm.$amap) {
      $vm.prototype.$amap = {
        initMapWithName,
        getAddressFromLngLat,
      }
    }
  },
}
export default plugin



