import CoverBgImg from '@/assets/img/cover_dialog/choose_bg.png'

export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    text: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      CoverBgImg,
    }
  },
  methods: {
    bgOnClick() {
      this.$emit('backgroundOnClick')
    },
    imgOnClick() {
      this.$emit('imgOnClick')
    },
    cancelButtonOnClick() {
      this.$emit('cancelButtonOnClick')
    },
    rightButtonOnClick() {
      this.$emit('rightButtonOnClick')
    },
    leftButtonOnClick() {
      this.$emit('leftButtonOnClick')
    },
  },
}



