import commonHeader from '@/components/commonHeader'
import checkInOption from '@/pages/attendance/components/CheckInOption'
import branchCheckIn from '@/pages/attendance/components/BranchCheckIn'
import customerCheckIn from '@/pages/attendance/components/CustomerCheckIn'
import otherCheckIn from '@/pages/attendance/components/OtherCheckIn'

export default {
  components: {
    commonHeader,
    checkInOption,
    branchCheckIn,
    customerCheckIn,
    otherCheckIn,
  },
  data() {
    return {
      title: '上班签到',
      branchCheckInShow: true,
      customerCheckInShow: false,
      otherCheckInShow: false,
    }
  },
  mounted() {
    this.$navbar.attendanceCheckInStyle()
  },
  methods: {
    getOption (option, index) {
      switch (index) {
        case 0:
          this.$refs.branchCheckIn.$refs.blue_tick_list.branchPicked = false // 將 list 的 checked 清除
          this.$refs.customerCheckIn.selectedItem = {}
          this.branchCheckInShow = true
          this.customerCheckInShow = false
          this.otherCheckInShow = false
          break
        case 1:
          this.$refs.customerCheckIn.$refs.blue_tick_list.branchPicked = false // 將 list 的 checked 清除
          this.$refs.branchCheckIn.selectedItem = {}
          this.branchCheckInShow = false
          this.customerCheckInShow = true
          this.otherCheckInShow = false
          break
        case 2:
          this.branchCheckInShow = false
          this.customerCheckInShow = false
          this.otherCheckInShow = true
          // 選擇其他地址出发，才開始定位
          this.$refs.otherCheckIn.loadmap()
          break
        default:
          this.branchCheckInShow = true
          this.customerCheckInShow = false
          this.otherCheckInShow = false
          break
      }
    },
  },
}



