// import Laydate from 'layui-laydate'
// import '@/../static/laydate.css'

export default {
  data() {
    return {
      selectMonth: '',
      today: '',
    }
  },
  mounted() {
    // this.today = new Date()
    // this.today.year = this.today.getFullYear()
    // this.today.month = this.today.getMonth()+1
    // this.today.selectMonth = `${this.today.year}-${this.$filter.fillZero(this.today.month)}`
    // this.initCalendar()
    this.initSelect()
  },
  methods: {
    // initCalendar() {
    //   Laydate.render({
    //     elem: '#picker',
    //     type: 'month',
    //     value: this.today.selectMonth,
    //     btns: ['confirm'],
    //     trigger: 'click',
    //     ready: () => {
    //       // 確認按鈕樣式
    //       document.querySelector('.laydate-btns-confirm').classList += ' btn bg-blue'
    //       // 清空日期表格的內容，可以減少高度
    //       document.querySelector('.layui-laydate-content table').textContent = ''
    //     },
    //     done: (value, data) => { // 點選月份
    //       this.today.selectMonth = value
    //       this.$emit('to-parent', data)
    //     },
    //   })
    //   this.$emit('to-parent', this.today)
    // },
    initSelect(){
      // 判斷日期
      const thistoday = new Date()
      const thisyear = thistoday.getFullYear()
      let thismoneth = (thistoday.getMonth() + 1) > 9 ? (thistoday.getMonth() + 1) : `0${thistoday.getMonth() + 1}`

      document.getElementById("thisMonth").innerHTML=`${thisyear}-${thismoneth}`

      thismoneth = thismoneth.substring(0,1) == 0 ? thismoneth.substring(1,2) : thismoneth
      thismoneth = parseInt(thismoneth)
      document.getElementById("thisMonth").value = `${thisyear}-${thismoneth}`
      let lastyear = (thismoneth - 1) < 1 ? thisyear - 1 : thisyear
      let lastmonth = thismoneth
      lastmonth = (lastmonth - 1) < 1 ? 12 : lastmonth - 1
      document.getElementById("lasMonth").value = `${lastyear}-${lastmonth}`

      let nextyear = (thismoneth + 1) > 12 ? thisyear + 1 : thisyear
      let nextmonth = thismoneth
      nextmonth = (thismoneth + 1) > 12 ? 1 : thismoneth + 1
      document.getElementById("nextMonth").value = `${nextyear}-${nextmonth}`
      
      lastmonth = (lastmonth.toString().length) <= 1 ? `0${lastmonth}` : lastmonth
      document.getElementById("lasMonth").innerHTML=`${lastyear}-${lastmonth}`;

      nextmonth = (nextmonth.toString().length) <= 1 ? `0${nextmonth}` : nextmonth
      document.getElementById("nextMonth").innerHTML=`${lastyear}-${nextmonth}`;

      this.chooseSelect()
    },
    chooseSelect(){
      const selvue = document.getElementById("select_month").value
      const year = selvue.split("-")[0]
      const month = selvue.split("-")[1]
      this.$emit('to-parent', {"year": year, "month": month})
    },
  },
}



