import { mapGetters, mapActions} from 'vuex'
import BlueTickList from '../components/BlueTickList'
import PhotoCollector from '@/components/PhotoCollector'
import SubmitClockInButton from './SubmitClockInButton'

export default {
  data () {
    return {
      // 营业所出发測試用data
      branchPicked: false,
      photoArray: [],
      selectedItem: {},
      showBlock: false
    }
  },
  components: {
    BlueTickList,
    PhotoCollector,
    SubmitClockInButton,
  },
  computed: {
    ...mapGetters('attendance', [
      'getOtherDepartureAddressList',
    ]),
  },
  mounted () {
    this.queryDepartureAddressList('0') // 0:营业所 1:经销商
  },
  methods: {
    ...mapActions('attendance', [
      'queryDepartureAddressList',
      'submitClockIn',
    ]),
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    clickMe () {
      this.showBlock = true
    },
    takePhoto(photoType) {
      // if (this.selectedItem.clockInName == undefined) {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `请先选择地点`,
      //   })
      //   return
      // }
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType, attendanceLocationName: this.selectedItem.clockInName }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    refereshData() {
    },
    listHandler(ele) {
      this.selectedItem = ele
    },
  }
}



