    // CommonHeader,
    StoreList,
    StoreFilter,
  },
  data() {
    return {
      STORE_LIST: 'store',
      SUGGESTED_LIST: 'suggested',
      storeList: [],
      pageIndex: 1,

      filterData: {
        type: 'store', // 目前選擇的篩選器
        options: {
          // 集團終端.篩選器選項
          forthName: '',
          storeName: '',
          storeOwner: '',
          // keyword: '',
          // 建議終端.篩選器選項
          bigCategory: '',
          midCategory: '',
          subCategory: '',
          keyword: ''
        }, // 目前選擇的篩選器選項
      },

      // type: STORE_LIST, // 目前選擇的篩選器
      // options: '', // 目前選擇的篩選器選項
      storeListData: [], // 終端清單
      storeId: '', // 點選的終端storeId
      searchText: '', // 搜索終端輸入文字
      noMore: false,
      selectedDate: {},
      listIsLoading: true,
      listNoData: false,
    }
  },
  // beforeRouteUpdate(to, from, next) {
  //   console.log(to)
  //   console.log(to.path)
  // },
  beforeRouteLeave(to, from, next) {
    this.CLEAR_STORE_LIST()
    next()
  },
  mounted() {
    this.$navbar.personalSelectStoreStyle()
    this.selectedDate = this.$route.params.selectedDate
    this.queryMarketPlaceListFromWeb()
    this.queryStoreListFromNative()

  },
  computed: {
    ...mapGetters('store', [
      'getStoreList',
      'getWechatMarketPlaceList',
      // 'getAdditionStoreList',
    ]),
    ...mapGetters('personal', [
      'getAdditionStoreList',
    ]),
    filteredList() {
      const tempList = this.getStoreList.filter((storeInfo) => {
        const isIncludingStoreName = storeInfo.storeName.includes(this.searchText)
        const isIncludingStoreOwner = storeInfo.storeOwner.includes(this.searchText)
        const isIncludingMobile = storeInfo.mobile.includes(this.searchText)
        const isIncludingPhone = storeInfo.phone.includes(this.searchText)
        const isIncludingAddress = storeInfo.address.includes(this.searchText)
        const isIncludingState = storeInfo.storeState.includes(this.searchText)
        return isIncludingStoreName || isIncludingStoreOwner || isIncludingMobile || isIncludingPhone || isIncludingAddress || isIncludingState
      })
      return tempList
    },
    reconstructionList() {
      const tempList = this.marketPlaceList.map((marketPlaceEle) => {
        return
      })
      return tempList
    }
  },
  methods: {
    ...mapActions('store', [
      'querySearchStore',
