      'queryWechatMarketPlace',
      'submitAddVisitPlan',
    ]),
    ...mapActions('personal', [
      'submitAddVisitPlanMultiStores',
    ]),
    ...mapMutations('store', [
      'SET_SELECTED_STORE_ELE',
      'SET_SELECTED_MARKET_PLACE_ELE',
      'SET_SELECTED_LIST_TYPE',
      'CLEAR_WECHAT_MARKET_PLACE_LIST',
      'REMARK_WECHAT_MARKET_PLACE_LIST',
    ]),
    ...mapMutations('loading', [
      'SETTING_LOADING_TEXT',
    ]),
    ...mapMutations('personal', [
      'ADD_STORE_LIST',
      'SUBTRACT_STORE_LIST',
      'CLEAR_STORE_LIST',
    ]),
    // searchKeyword() { // 輸入關鍵字後按enter
    //   // this.store.showOption = true
    //   // this.suggested.showOption = true
    //   // this.sendFilterData(this.searchType)
    // },
    dataParser(param) {
      return param.map((additionStore) => {
        const isMarketPlace = additionStore.poiId !== undefined
        const submitDataStruct = {
          visitSid: '',
          targetType: isMarketPlace ? 1 : 0, // 集團終端為0 , 建議終端為1
          storeId: isMarketPlace ? '' : additionStore.poiId,
          targetId: isMarketPlace ? additionStore.poiId : '',
          expectedDate: this.selectedDate,
        }
        return
      })
    },
    confirmBtnOnClick() {
      const targetList = this.getAdditionStoreList.map((additionStore) => {
        const isMarketPlace = additionStore.poiId !== undefined
        return {
          targetType: isMarketPlace ? '1' : '0', // 集團終端為0 , 建議終端為1
          storeId: isMarketPlace ? '' : additionStore.storeId,
          targetId: isMarketPlace ? additionStore.poiId : '',
          targetName: isMarketPlace ? additionStore.poiName : '',
          // targetOwner: isMarketPlace ? additionStore.poiId : '',
          phone: isMarketPlace ? additionStore.poiTel : '',
          address: isMarketPlace ? additionStore.poiRealAddress : '',

          poiTypeList: isMarketPlace ? additionStore.poiTypeList : '',
          districtName: isMarketPlace ? additionStore.districtName : '',
          companyName: isMarketPlace ? additionStore.companyName : '',
          branchId: isMarketPlace ? additionStore.branchId : '',
          branchName: isMarketPlace ? additionStore.branchName : '',
          marketName: isMarketPlace ? additionStore.marketName : '',
          smallMarketName: isMarketPlace ? additionStore.smallMarketName : '',
          wantForthName: isMarketPlace ? additionStore.townShip : '',
          longitude: isMarketPlace ? additionStore.longitude : '',
          latitude: isMarketPlace ? additionStore.latitude : '',
        }
      })
      const param = {
        planDate: Array.isArray(this.selectedDate) ? this.selectedDate : [this.selectedDate],
        targetList,
      }
      if (param.targetList.length === 0) {
        this.$vux.alert.show({
          title: '提示',
          content: `未選擇地點`,
        })
        return
      }
      this.submitAddVisitPlanMultiStores(param).then(() => {
        this.$router.push('my_visit')
      }).catch((err) => {
        let _this = this
        if (err.code === 'c207') {
          this.$vux.alert.show({
            title: '提示',
            content: `${err.message}`,
            /* 點擊確認後，返回拜访日历 */
            onHide () {
              _this.$router.push('my_visit')
            }
          })
        }
      })
    },
    cancelBtnOnClick(param) {
      this.$router.push('my_visit')
    },
    filterHandler(filterData) { // 取得篩選器選項
      this.filterData = filterData
      if (this.filterData.hasSearchCriteria === true) {
        if (filterData.type === this.STORE_LIST) {
          this.queryStoreListFromNative()
        } else { // data.type === 'suggested'
          this.queryMarketPlaceListFromWeb()
        }
      }
    },
    storeListItemOnClick(data) { // 取得選擇的store
      // this.ADD_STORE_LIST(data)
      // this.SUBTRACT_STORE_LIST(data)
    },
    marketPlaceListItemOnClick(data) {
      // this.ADD_STORE_LIST(data)
      // this.SUBTRACT_STORE_LIST(data)
    },
    marketPlaceListLoadMore() {
      if (!this.noMore) {        
        this.pageIndex += 1
        this.queryMarketPlaceListFromWeb(this.pageIndex)
      }
    },
    queryStoreListFromNative() {
      const { forthName, storeName, storeOwner, keyword } = this.filterData.options
      this.querySearchStore({
        forthName,
        storeName,
        storeOwner,
        keyword,
      }).then((respData) => {
        if (Array.isArray(respData.storeList) && respData.storeList.length == 0) {
          this.$vux.alert.show({
            title: '提示',
            content: `搜寻条件查无数据`,
          })
        }
      })
    },
    queryMarketPlaceListFromWeb(pageNum = 1) {
      if (pageNum === 1) {
        this.CLEAR_WECHAT_MARKET_PLACE_LIST()
        this.noMore = false
      } // 清空
      this.listIsLoading = true
      this.listNoData = false
      const { bigCategory, midCategory, subCategory, keyword } = this.filterData.options
      this.queryWechatMarketPlace({
        pageNum,
        poiType: keyword,
        poiType1: bigCategory,
        poiType2: midCategory,
        poiType3: subCategory,
      }).then((resp) => {
        this.listIsLoading = false
        this.REMARK_WECHAT_MARKET_PLACE_LIST(this.getAdditionStoreList)
        const respData = resp.data.data.data
        if (respData.list.length == 0) {
          this.listNoData = true
          this.$vux.alert.show({
            title: '提示',
            content: `搜寻条件查无数据`,
          })
          return
        }
        if (respData.pageNum == respData.pages) {
          this.$vux.alert.show({
            title: '提示',
            content: `所有资料已读取完毕`,
          })
          this.noMore = true
          return
        }
      }).catch((e) => {
        this.listIsLoading = false
        // this.$vux.alert.show({
        //   title: '提示',
        //   content: `${e}`,
        // })
      })
    },
    // getStoreList() { // 取得終端清單
    //   if (this.type === STORE_LIST) {
    //     this.storeListData = [{
    //       storeId: '7054411170',
    //       storeName: '宝宝超市',
