import { mapGetters, mapActions } from 'vuex'
import StoreInfoHeader from '@/components/StoreInfoHeader'
import LeftLightStickPureTextCell from '@/components/LeftLightStickPureTextCell'
import BlueTickSelector from '@/components/BlueTickSelector'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    StoreInfoHeader,
    LeftLightStickPureTextCell,
    BlueTickSelector,
    PhotoCollector,
  },
  data() {
    return {
      selectorReturnEle: {
        index: 0,
        title: '',
      },
      selectedAbnormalEle: {
        index: 0,
        statusCode: '',
        statusName: '',
        description: '',
        remark: '',
      },
      textAreaValue: '',
      photoArray: [],
      isSubmit: false,
    }
  },
  computed: {
    ...mapGetters('home', [
      'getUnvisitStoreStatus',
      'getAbnormalVisitList',
      'getSelectedVisitEle',
    ]),
  },
  beforeRouteLeave(to, from, next) {
    if ((to.path === '/home' || to.path === '/my_visit')  && !this.isSubmit) {
      const self = this  
      this.$vux.confirm.show({
        title: '提示',
        content: '确认返回将清除本次拜访纪录',
        onConfirm () {
          self.$parent.$off('navbarRightBtnClick') // 取消訂閱
          const functionName = 'giveUpVisit'
          const visitSid = self.getSelectedVisitEle.visitSid
          const param = { visitSid }
          self.$store.$native.execute(
            functionName,
            param,
          ).then((resp) => {
            next()
          }).catch(() => {
            next()
          })
        },
        onCancel () {
        },
      })
    } else {
      next()
    }
  },
  mounted() {
    this.$navbar.abnormalVisitStyle()
    if (this.$navbar.abnormalVisitPreviousPage === 'home') {
      this.$navbar.setLeftBtn('返回', '', '/home')
    } else if (this.$navbar.abnormalVisitPreviousPage === 'myVisit') {
      this.$navbar.setLeftBtn('返回', '', '/my_visit')
    }
    console.log("moundggggggg")
    
    
  },
  updated() {
    // 強制用js把打開圖片的div移到scorll的外層
    document.querySelector(".lighbox_area").childNodes.removeChild
    var itm_lightbox = document.querySelector(".photo_swiper-container")
    document.querySelector(".lighbox_area").appendChild(itm_lightbox)
  },
  methods: {
    ...mapActions('normalVisit', [
      'submitFinishVisit',
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    takePhoto(photoType) {
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    selectionOnClick(title, content, index) {
      this.selectorReturnEle.index = index
      this.selectorReturnEle.title = title
      this.selectorReturnEle.content = content
    },
    bottomBtnOnClick() {
      const validReturnEle = this.getUnvisitStoreStatus.find(status => { // find() 在部分 android 上有問題
        return this.selectorReturnEle.title === status.statusName
      })
      const photoSidList = this.photoArray.map(photoEle => {
        return photoEle.photoSid
      })
      if (this.selectorReturnEle.title === '') {
        this.$vux.alert.show({ title: '提示', content: `地點狀態為必填`})
        return
      }
      const isOtherStatus = this.selectorReturnEle.title === '其他'
      if (isOtherStatus && this.selectorReturnEle.content === '') {
        this.$vux.alert.show({ title: '提示', content: `地點狀態為其他，请简述状态`})
        return
      }
      if (this.textAreaValue === '') {
        this.$vux.alert.show({ title: '提示', content: `备注为必填`})
        return        
      }
      if (Array.isArray(photoSidList) && photoSidList.length < 1) {
        this.$vux.alert.show({ title: '提示', content: `照片为必填`})
        return        
      }
      this.submitFinishVisit({ // param{ visitType = '1', visitStoreStatusCode = '', otherStoreStatusName = '', visitRemark = ''}
        visitType: '2',
        visitStoreStatusCode: validReturnEle ? validReturnEle.statusCode : 99, // 如果沒找到相應的name 就判定為其他。
        otherStoreStatusName: isOtherStatus ? this.selectorReturnEle.content : '',
        visitRemark: this.textAreaValue,
        photoSidList,
      }).then((resp) => {
        // 提交成功，顯示提交結果
        this.isSubmit = true
        if (resp.code === 'c200') {
          let $this = this
          this.$vux.alert.show({
            title: '提示',
            content: '提交成功！',
            onHide(){
              // 跳轉至首頁
              if ($this.$navbar.abnormalVisitPreviousPage === 'myVisit') {
                $this.$router.push('/my_visit')
              } else {
                $this.$router.push('/home')
              }
            }
          })
        } else {
          this.$vux.alert.show({ title: '提示', content: '提交失敗！' })
          return
        }
      }).catch((error) => {
        this.$vux.alert.show({ title: '提示', content: '提交失敗！' })
        return
      })
    },
    selectorTextOnChange(textValue) {
      this.selectorReturnEle.content = textValue
    },

//     // Input - 异常拜访，状态为其他
// {
//  	"visitSid" : "18020511301618400122963000",	// 线外拜访
//  	"storeId" : "7055666220",
//  	"visitType" : "2",	// 2-异常拜访
//  	"visitStoreStatusCode" : "7",	// 7-其他
//  	"otherStoreStatusName" : "道路维修，无法前往",		// visitStoreStatusCode为7时必填
//  	"visitRemark" : "备注说明",
// }
  },
}



