import NP from 'number-precision'
const ua = navigator.userAgent
let mobileSys
let keybordHeight = '0'

/* 判斷手機系統 */
if (ua.indexOf('Mac') > -1) {
  mobileSys = 'ios'
} else if (ua.indexOf('Android') > -1) {
  mobileSys = 'android'
}

export default {
  props: {
    countNumber: {
      type: Number,
      default: 0,
    },
    cls: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      count: 0,
      isEditable: false,
      mobileSys: mobileSys,
      keybordHeight: keybordHeight,
      inputFocus: false,
    }
  },
  computed: {
    countNum: {
      get() {
        return this.count
        // return parseFloat(this.count).toFixed(0)
        // return Math.floor(this.count*100)/100
      },
      set(newValue) {
        // if (newValue < 0 || newValue == NaN || newValue === '') {
        //   newValue = 0
        //   return
        // }
        // if (this.isEditable) {
        //   this.$emit('beforeValueChange')
        // }
        // // let reg = /^[0-9]+(\.[0-9]{1,2})?$/
        // // const num = reg.test(newValue)
        // // newValue = Math.round(newValue * 100) / 100
        // newValue = parseFloat(newValue).toFixed(2)
        // this.count = newValue
        // if (this.isEditable) {
        //   // this.$emit('afterValueChange', parseFloat(newValue))
        //   this.$emit('afterValueChange', newValue)
        // }
        if (this.isEditable) {
          this.$emit('beforeValueChange')
        }
        this.count = newValue
      }
    }, 
  },
  mounted() {
    this.countNum = this.countNumber
  },
  methods: {
    /* 通知父組件所點的input type */
    clickInput(type) {
      this.$emit('to-parent-click', type)
    },
    onFocus() {
      this.isEditable = true
      this.$emit('onFocus')
    },
    onBlur() {
      this.isEditable = false
    },
    minusBtnOnClick(cls) {
      this.isEditable = true
      // this.countNum -= 1
      // this.countNum = parseFloat(this.countNum) - 1
      this.countNum = NP.minus((this.countNum), 1)
      this.countNum = this.countNum < 0 ? 0 : this.countNum
      this.$emit('afterValueChange', this.countNum)
      this.isEditable = false
      // this.$emit('minusBtnOnClick')
    },
    plusBtnOnClick(cls) {
      this.isEditable = true
      // this.countNum += 1
      // this.countNum = parseFloat(this.countNum) + 1
      this.countNum = NP.plus((this.countNum), 1)
      this.$emit('afterValueChange', this.countNum)
      this.isEditable = false
      // this.$emit('plusBtnOnClick')
    },
    oninput(e) {
      // 通过正则过滤小数点后两位
      e.target.value = (e.target.value.match(/^\d*(\.?\d{0,2})/g)[0]) || null
      this.count = e.target.value
      e.target.value = e.target.value == "" ? 0 : e.target.value
      
      this.$emit('afterValueChange', e.target.value)
    }
  },
}



