import axios from 'axios'
import Vue from 'vue'
import promiseExtension from '../../../plugins/promise_extension'
import {
  QUERY_SEARCH_STORE,
  QUERY_STORE_DETAIL,
  QUERY_WECHAT_MARKET_PLACE,
  QUERY_FORTH_CITY_LIST,
  QUERY_WECHAT_MARKET_PLACE_WITH_ID,
  QUERY_WECHAT_MARKET_PLACE_DEFAULT_CACHE,
  // QUERY_POI_LEVEL,
} from './mutationTypes'

import UnselectedIcon from '@/assets/img/common/choose_n.png'
import SelectedIcon from '@/assets/img/common/choose_y.png'

const actions = {
  querySearchStore(context, { forthName = '', keyword = '', storeName = '', storeOwner = '' }) { // 於 native 獲取 集團終端
    return new Promise((resolve, reject) => {
      const functionName = 'searchStore'
      this.$native.execute(
        functionName,
        {
          forthName,
          keyword,
          storeName,
          storeOwner,
          pageSize: '',
          pageNumber: '',
        },
      ).then((resp) => {
        context.commit(QUERY_SEARCH_STORE, resp.body)
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryForthCityList(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getForthCityList'
      this.$native.execute(
        functionName,
      ).then((resp) => {
        context.commit(QUERY_FORTH_CITY_LIST, resp.body)
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryStoreDetail(context, inputStoreId = '') {
    return new Promise((resolve, reject) => {
      const functionName = 'getStoreDetail'
      const storeId = inputStoreId !== '' ? inputStoreId : context.rootState.store.selectedStoreEle.storeId
      this.$native.execute(
        functionName,
        { storeId: inputStoreId ? inputStoreId : storeId },
      ).then((resp) => {
        context.commit(QUERY_STORE_DETAIL, resp.body)
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  queryStorePerformance(context, inputStoreId = '') {
    return new Promise((resolve, reject) => {
      const functionName = 'getStorePerformance'
      const storeId = inputStoreId !== '' ? inputStoreId : context.rootState.store.selectedStoreEle.storeId
      this.$native.execute(
        functionName,
        {
          storeId, // 若為建議終端，則填入PoiId
          prodLv3Id: '', // 产品Lv3编码 // 保留此參數，可不填
        },
      ).then((resp) => {
        // context.commit(QUERY_STORE_DETAIL, resp.body)
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  submitAddStoreFromSuggestedPoi(context, poiId = '') {
    return new Promise((resolve, reject) => {
      const functionName = 'addStoreFromSuggestedPoi'
      this.$native.execute(
        functionName,
        {
          poiId,
        },
      ).then((resp) => {
        resolve(resp.body)
      }).catch((e) => {
        reject(e)
      })
    })
  },
  submitAddVisitPlan(context, { visitSid = '', expectedDate = '', inputParam }) {
    return new Promise((resolve, reject) => {
      // const storeId = inputStoreId !== '' ? inputStoreId : context.rootState.store.selectedStoreEle.storeId
      const functionName = 'addVisitPlan'
      const isStoreList = context.rootState.store.selectedListType === 'store'
      const selectedMarketPlaceItem = context.rootState.store.selectedMarketPlaceEle

      const targetType = isStoreList ? 0 : 1
      const storeId = isStoreList ? context.rootState.store.selectedStoreEle.storeId : ''
      const targetId = isStoreList ? '' : selectedMarketPlaceItem.poiId
      const param = {
        visitSid, // 拜访任务编码 , yyMMddhhmmss+Randon(999)+工号 , //先空字串
        targetType, // 0 : 集团终端  1 : 建议终端
        storeId, // 集团终端编号
        targetId, // 即建议终端编号 // 這要從 searchStore.storeList 給出來，還是 getSelectedStoreDetail ?

        targetName: isStoreList ? '' : selectedMarketPlaceItem.poiName,
        // targetOwner: isStoreList ? '' : context.rootState.store.selectedMarketPlaceEle.poiId,
        phone: isStoreList ? '' : selectedMarketPlaceItem.poiTel,
        address: isStoreList ? '' : selectedMarketPlaceItem.poiRealAddress,

        poiTypeList: isStoreList ? '' : selectedMarketPlaceItem.poiTypeList,
        districtName: isStoreList ? '' : selectedMarketPlaceItem.districtName,
        companyName: isStoreList ? '' : selectedMarketPlaceItem.companyName,
        branchId: isStoreList ? '' : selectedMarketPlaceItem.branchId,
        branchName: isStoreList ? '' : selectedMarketPlaceItem.branchName,
        marketName: isStoreList ? '' : selectedMarketPlaceItem.marketName,
        smallMarketName: isStoreList ? '' : selectedMarketPlaceItem.smallMarketName,
        wantForthName: isStoreList ? '' : selectedMarketPlaceItem.townShip,
        longitude: isStoreList ? '' : selectedMarketPlaceItem.longitude,
        latitude: isStoreList ? '' : selectedMarketPlaceItem.latitude,

        expectedDate: Array.isArray(expectedDate) ? expectedDate : [expectedDate], // 计划拜访日期 , 只能是今天之后 // [ "2018-03-21" , "2018-05-31" ]
      }
      this.$native.execute(
        functionName,
        inputParam ? inputParam : param 
      ).then((resp) => {
        resolve(resp)
      }).catch((e) => {
        reject(e)
      })
    })
  },

  /**
   * 以下皆為對外部 server 取資料
   */
  queryPoiLevel(context, { requestLV1 = '', requestLV2 = '' }) { // 於 web 獲取 建議終端
    const promise = new Promise((resolve, reject) => {
      const { loginId } = context.rootState.personal.empInfo
      axios({
        method: 'get',
        // url: 'http://qywx-test.want-want.com/WechatMarketplace/poiLevelForSfa',
        url: 'http://wwhd.want-want.com/gaodeService/poiLevel',
        // url: 'http://10.231.17.50:4000/getPoiLevel',
        // timeout: 10000,
        headers: { 'source': 'SFA4.0' },
        params: {
          // userId: '80005418',
          userId: loginId,
          requestLV1: requestLV2 !== '' ? '' : requestLV1,
          requestLV2,
        },
      }).then((resp) => {
        resolve(resp)
      }).catch((error) => {
        reject(error)
      })
    })
    return promise
    // return promiseExtension.setTimout(promise)
  },
  queryWechatMarketPlace(context, { pageNum = 0, poiType = '', poiType1 = '', poiType2 = '', poiType3 = '' }) { // 於 web 獲取 建議終端
    const isDefaultCondition = (poiType === '' && poiType1 === '' && poiType2 === '' && poiType3 === '' && pageNum == 1)
    const defaultPlaceList = context.rootState.store.defaultMarketPlaceList
    if (isDefaultCondition && Array.isArray(defaultPlaceList) && defaultPlaceList.length > 10 ) { // cache 機制
      context.commit(QUERY_WECHAT_MARKET_PLACE, defaultPlaceList)
      return
    }
    const promise = new Promise((resolve, reject) => {
      const {
        loginId,
        // companyId,
        branchId,
      } = context.rootState.personal.empInfo
      axios({
        method: 'post',
        // url: 'http://qywx-test.want-want.com/WechatMarketplace/sfa', // 測試
        url: 'http://wwhd.want-want.com/gaodeService/sfa',
        // url: 'http://10.231.17.50:4000/wechatMarketplace', // 本機
        // timeout: 10000,
        headers: { 'source': 'SFA4.0' },
        params: {
          // userId: '80005418',
          // company: 'W11',
          // branch: '1101',
          userId: loginId,
          // company: companyId, // 2018/07/02, 不能帶，帶了沒資料。可以用 C19 或是 W19 測試。
          branch: branchId,
          poiCategory: '1', // 0:全部 1:高德 2:集团
          pageSize: 30,
          pageNum,
          poiType, // 關鍵字
          poiType1, // 設施大類
          poiType2, // 設施中類
          poiType3, // 設施小類
        },
      }).then((resp) => {
        const formatPromiseEle = context.dispatch('formatWechatMarketPlaceList',{ storeList:resp.data.data.data.list, pageNum})
        formatPromiseEle.then((storeList) => { // dispatch 返回的是一個 promise 物件
          if (isDefaultCondition) {
            context.commit(QUERY_WECHAT_MARKET_PLACE_DEFAULT_CACHE, storeList)
            context.commit(QUERY_WECHAT_MARKET_PLACE, storeList)
          } else {
            context.commit(QUERY_WECHAT_MARKET_PLACE, storeList)
          }
        })
        resolve(resp)
      }).catch((error) => {
        reject(error)
      })
    })
    return promise
    // return promiseExtension.setTimout(promise)
  },
  formatWechatMarketPlaceList(context, {storeList, pageNum}) {
    const tempList = storeList.map((markePlaceItem) => {
      const tempItem = markePlaceItem
      // for store.storeList
      tempItem.sIndex = this.$native.generateUUID()
      tempItem.imageUrl = UnselectedIcon // for personal.SelectStore.StoreList mutibleSelect
      tempItem.storeName = markePlaceItem.poiName
      tempItem.mobile = markePlaceItem.poiTel
      tempItem.address = markePlaceItem.poiRealAddress
      // for storeDetail
      tempItem.regingName = markePlaceItem.districtName
      tempItem.standardMarketName = markePlaceItem.marketName
      tempItem.wantForthName = markePlaceItem.townShip
      // 'http://ditu.amap.com/place/B0FFH9VHIT', >> B0FFH9VHIT
      const strArray = markePlaceItem.poiId.split('/')
      const poiId = strArray[strArray.length - 1]
      tempItem.poiId = poiId
      tempItem.storeId = poiId
      return tempItem
    })
    return tempList
  },
  queryWechatMarketPlaceWithId(context, { poiId = '' }) { // 於 web 獲取 建議終端
    const promise = new Promise((resolve, reject) => {
      const { loginId } = context.rootState.personal.empInfo
      axios({
        method: 'post',
        // url: 'http://qywx-test.want-want.com/WechatMarketplace/sfa',
        url: 'http://wwhd.want-want.com/gaodeService/sfa',
        // url: 'http://10.231.17.50:4000/wechatMarketplace',
        // timeout: 10000,
        params: {
          // userId: '80005418',
          userId: loginId,
          poiId,
        },
        headers: { 'source': 'SFA4.0' },
      }).then((resp) => {
        context.commit(QUERY_WECHAT_MARKET_PLACE_WITH_ID, resp.data.data.data.list[0])
        resolve(respData)
      }).catch((error) => {
        reject(error)
      })
    })

    return promise
    // return promiseExtension.setTimout(promise)
  },
}

export default actions



