import {
  SHOW_FOOT_BAR,
  HIDE_FOOT_BAR,
} from './mutationTypes'

const mutations = {
  [SHOW_FOOT_BAR](state) {
    state.isFootBarVisible = true
  },
  [HIDE_FOOT_BAR](state) {
    state.isFootBarVisible = false
  },
}

export default mutations



