// import CommonHeader from '@/components/commonHeader'
import StoreList from '@/pages/campaign/components/StoreList'
import StoreFilter from '@/pages/store/components/StoreFilter'
import store from '../../../store';
import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
  components: {
    // CommonHeader,
    StoreList,
    StoreFilter,
  },
  data() {
    return {
      STORE_LIST: 'store',
      SUGGESTED_LIST: 'suggested',
      storeList: [],
      pageIndex: 1,

      filterData: {
        type: 'store', // 目前選擇的篩選器
        options: {
          // 集團終端.篩選器選項
          forthName: '',
          storeName: '',
          storeOwner: '',
          // keyword: '',
          // 建議終端.篩選器選項
          bigCategory: '',
          midCategory: '',
          subCategory: '',
          keyword: ''
        }, // 目前選擇的篩選器選項
      },
      // type: STORE_LIST, // 目前選擇的篩選器
      // options: '', // 目前選擇的篩選器選項
      storeListData: [], // 終端清單
      storeId: '', // 點選的終端storeId
      searchText: '', // 搜索終端輸入文字
      noMore: false,
      listIsLoading: true,
      listNoData: false,
    }
  },
  // beforeRouteUpdate(to, from, next) {
  //   console.log(to)
  //   console.log(to.path)
  // },
  mounted() {
    this.$navbar.campaignSelectStoreStyle()
    this.queryMarketPlaceListFromWeb()
    this.queryStoreListFromNative()
  },
  computed: {
    ...mapGetters('store', [
      'getStoreList',
      'getWechatMarketPlaceList',
    ]),
    ///
    filteredList() {
      const tempList = this.getStoreList.filter((storeInfo) => {
        const isIncludingStoreName = storeInfo.storeName.includes(this.searchText)
        const isIncludingStoreOwner = storeInfo.storeOwner.includes(this.searchText)
        const isIncludingMobile = storeInfo.mobile.includes(this.searchText)
        const isIncludingPhone = storeInfo.phone.includes(this.searchText)
        const isIncludingAddress = storeInfo.address.includes(this.searchText)
        const isIncludingState = storeInfo.storeState.includes(this.searchText)
        return isIncludingStoreName || isIncludingStoreOwner || isIncludingMobile || isIncludingPhone || isIncludingAddress || isIncludingState
      })
      return tempList
    },
    reconstructionList() {
      const tempList = this.marketPlaceList.map((marketPlaceEle) => {
        return
      })
      return tempList
    }
  },
  // beforeRouteLeave(to, from, next) { // 帶選擇的store返回上頁
  //   if (to.path ==='/store_detail') { // 查看終端詳情
  //     // to.query.storeType = this.filterData.type
  //     // to.query.storeId = this.storeId
  //     if (this.filterData.type === this.STORE_LIST) {
  //       this.queryStoreDetail().then(()=>{ next() })
  //     } else {
  //       next()
  //     }
  //   }
  //   else { next() }
  //   // next()
  // },
  methods: {
    ...mapActions('store', [
      'querySearchStore',
      'queryStoreDetail',
      'queryWechatMarketPlace',
    ]),
    ...mapMutations('store', [
      'SET_SELECTED_STORE_ELE',
      'SET_SELECTED_MARKET_PLACE_ELE',
      'SET_SELECTED_LIST_TYPE',
      'CLEAR_WECHAT_MARKET_PLACE_LIST',
    ]),
    // searchKeyword() { // 輸入關鍵字後按enter
    //   // this.store.showOption = true
    //   // this.suggested.showOption = true
    //   // this.sendFilterData(this.searchType)
    // },
    filterHandler(filterData) { // 取得篩選器選項
      this.filterData = filterData
      if (this.filterData.hasSearchCriteria === true) {
        if (filterData.type === this.STORE_LIST) {
          this.queryStoreListFromNative()
        } else { // data.type === 'suggested'
          this.queryMarketPlaceListFromWeb()
        }
      }
    },
    storeListItemOnClick(data) { // 取得選擇的store
      this.SET_SELECTED_STORE_ELE(data)
      this.SET_SELECTED_LIST_TYPE(this.filterData.type)
      this.$router.push({name: 'create_campaign', params:{ selectedData: data, type: 'STORE' }})
    },
    marketPlaceListItemOnClick(data) {
      this.SET_SELECTED_MARKET_PLACE_ELE(data)
      this.SET_SELECTED_LIST_TYPE(this.filterData.type)
      this.$router.push({name: 'create_campaign', params:{ selectedData: data, type: 'MARKET_PLACE' }})
    },
    marketPlaceListLoadMore() {
      if (!this.noMore) {        
        this.pageIndex += 1
        this.queryMarketPlaceListFromWeb(this.pageIndex)
      }
    },
    queryStoreListFromNative() {
      const { forthName, storeName, storeOwner, keyword } = this.filterData.options
      this.querySearchStore({
        forthName,
        storeName,
        storeOwner,
        keyword,
      }).then((respData) => {
        if (Array.isArray(respData.storeList) && respData.storeList.length == 0) {
          this.$vux.alert.show({
            title: '提示',
            content: `搜寻条件查无数据`,
          })
        }
      })
    },
    queryMarketPlaceListFromWeb(pageNum = 1) {
      
      if (pageNum === 1) {
        this.CLEAR_WECHAT_MARKET_PLACE_LIST()
        this.noMore = false
      } // 清空
      this.listIsLoading = true
      this.listNoData = false
      const { bigCategory, midCategory, subCategory, keyword } = this.filterData.options
      this.queryWechatMarketPlace({
        pageNum,
        poiType: keyword,
        poiType1: bigCategory,
        poiType2: midCategory,
        poiType3: subCategory,
      }).then((resp) => {
        this.listIsLoading = false
        const respData = resp.data.data.data
        if (respData.list.length == 0) {
          this.listNoData = true
          this.$vux.alert.show({
            title: '提示',
            content: `搜寻条件查无数据`,
          })
          return
        }
        if (respData.pageNum == respData.pages) {
          this.$vux.alert.show({
            title: '提示',
            content: `符合搜寻条件的所有资料已加载完成`,
          })
          this.noMore = true
          return
        }
      }).catch((e) => {
        this.listIsLoading = false
      })
    },
  },
}



