import { mapGetters, mapActions} from 'vuex'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    PhotoCollector,
  },
  data() {
    return {
      // photo type
      DOOR_WAY_PHOTO: 'DOOR_WAY',
      OTHER_PHOTO: 'OTHER',
      // doorWayPhotoInfoList: [],
      // otherPhotoInfoList: [],
    }
  },
  computed: {
    ...mapGetters('home', [
      'getSelectedVisitEle',
		]),
    ...mapGetters('normalVisit', [
      'getVisitDetail',
      'getDoorWayPhotoInfoList',
      'getOtherPhotoInfoList',
		]),
  },
  mounted() {
    // this.refereshData()
  },
  methods: {
    ...mapActions('normalVisit', [
      'queryVisitDetail',
		]),
    takePhoto(photoType) {
      this.takePicture({ photoType }).then(resp=> {
        if (resp.code === 'c200') {
          this.refereshData()
        }
      })
    },
    refereshData() {
      this.queryVisitDetail().then(resp => {
        // if (resp.code === 'c200') {
        //   for (const visitPhotoEle of this.getVisitDetail.visitPhotoInfo) {
        //     if (visitPhotoEle.photoDisplayType === this.DOOR_WAY_PHOTO) {
        //       this.doorWayPhotoInfoList = visitPhotoEle.photoInfoList
        //     }
        //     else if (visitPhotoEle.photoDisplayType === this.OTHER_PHOTO) {
        //       this.otherPhotoInfoList = visitPhotoEle.photoInfoList
        //     }
        //   }
        // }
      })
    },
  },
}



