import PhotoCollector from '@/components/PhotoCollector'
import { mapActions } from 'vuex'
import SubmitClockInButton from './SubmitClockInButton'

export default {
  data () {
    return {
      amap: '',
      addressInfo: '定位中...',
      photoArray: [],
      selectedItem: {},
    }
  },
  components: {
    PhotoCollector,
    SubmitClockInButton,
  },
  methods: {
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    ...mapActions('attendance', [
      'submitClockOut',
    ]),
    takePhoto(photoType) {
      // if (this.addressInfo === '定位中...' || this.addressInfo === '定位失败') {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `请先选择地点`,
      //   })
      //   return
      // }
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType, attendanceLocationName: this.addressInfo }).then(resp=> {
          this.photoArray.push(resp)
        })
      })
    },
    // bottomBtnOnClick() {
    //   // const { clockInAddressId, clockInAddress } = this.selectedItem
    //   const photoSidList = this.photoArray.map(photoEle => {
    //     return photoEle.photoSid
    //   })
    //   // this.submitClockIn({ clockInAddressId, clockInAddress, photoSidList }).then(resp => {
    //   this.submitClockOut({ photoSidList }).then(resp => {
    //     this.$router.push('/home')
    //   })
    // },
    loadmap(){
      this.addressInfo = '定位中...'
      let mapObj = new AMap.Map('map-container', {
        zoom: 10
      })
      const self = this
      mapObj.plugin(['AMap.Geolocation'], function () {
        let geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //  是否使用高精度定位，默认:true
          timeout: 10000, //  超过10秒后停止定位，默认：无穷大
          maximumAge: 0, // 定位结果缓存0毫秒，默认：0
          convert: true, // 自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //  显示定位按钮，默认：true
          buttonPosition: 'LB',  // 定位按钮停靠位置，默认：'LB'，左下角
          buttonOffset: new AMap.Pixel(10, 20), //  定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
          showMarker: true, //  定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true, //  定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true,  //  定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true  //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
        })
        mapObj.addControl(geolocation)
        geolocation.getCurrentPosition()
        //  返回定位信息
        AMap.event.addListener(geolocation, 'complete', (result) => {
          mapObj.setCenter(result.position)
          self.regeocoder(result.position.lng, result.position.lat)
        })
        //  返回定位出错信息
        AMap.event.addListener(geolocation, 'error', (result) => {
          self.addressInfo = '定位失败'
        })
      })
    },
    regeocoder(lng, lat) { // 逆地理编码，根据经纬度获取详细地址信息
      // 寫法二
      let geocoder
      AMap.service('AMap.Geocoder', () => {
        geocoder = new AMap.Geocoder({})
      })
      const lanlat = new AMap.LngLat(lng, lat)
      const lnglatXY = [lanlat.getLng(), lanlat.getLat()]
      geocoder.getAddress(lnglatXY, (status, result) => {
        if (status === 'complete' && result.info === 'OK') {
          this.addressInfo = result.regeocode.formattedAddress
        } else {
          this.addressInfo = '定位失败'
        }
      })
    },
  }
}



