import {
  QUERY_EVENT_LIST,
  QUERY_EVENT_DETAIL,
  SET_CREATE_CAMPAIGN_TEMP_ELE,
  SET_EVENT_DETAIL_DESCRIPTION,
} from './mutationTypes'

const mutations = {
  [QUERY_EVENT_LIST](state, payload) {
    state.eventList = payload
  },
  [QUERY_EVENT_DETAIL](state, payload) {
    state.eventDetailEle = payload
  },
  [SET_EVENT_DETAIL_DESCRIPTION](state, payload) {
    state.eventDetailEle.description = payload
  },
  [SET_CREATE_CAMPAIGN_TEMP_ELE](state, payload) {
    state.createCampaignTempEle = payload
  },
}

export default mutations



