import { mapGetters, mapActions} from 'vuex'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    PhotoCollector,
  },
  computed: {
    ...mapGetters('normalVisit', [
      'getVisitDetail',
      'getSpecialVisitPhotoInfoListArray',
      'getUncategorizedPhotoInfo',
    ]),
  },
  data() {
    return {
      // photo type
      SPECIAL_DISPLAY_PHOTO: 'SPECIAL_DISPLAY',
      specialVisitPhotoInfoListArray: [],
      uncategorizedPhotoInfo: []
    }
  },
  mounted() {
  },
  methods: {
    ...mapActions('normalVisit', [
      'queryVisitDetail',
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
      'scanAssistantQRCode', // 1 param: specialDisplayTypeSid = ''
		]),
    takePhoto(photoType, specialDisplayTypeSid, specialVisitPhotoInfoList) {
      this.checkPhotoArrayCount({self:this, photoArray:specialVisitPhotoInfoList}).then(() => {
        this.takePicture({ photoType, specialDisplayTypeSid}).then(resp=> {
          this.queryVisitDetail().then(resp => {
            if (specialDisplayTypeSid === '') {
              this.$router.push('/temp_photo_collection')
            }
          })
        })
      })
    },
    scanQR(specialDisplayTypeSid) {
      this.scanAssistantQRCode(specialDisplayTypeSid).then((resp) => {
        this.refereshData()
      }).catch(() => {
        this.refereshData()
      })
    },
    refereshData() {
      this.queryVisitDetail().then(resp => {})
    },
  },
}



