/* eslint-disable */
import { mapGetters, mapActions, mapMutations } from 'vuex'
import LeftLightStickPureTextCell from '@/components/LeftLightStickPureTextCell'
import PhotoCollector from '@/components/PhotoCollector'
import commonSelect from '@/components/commonSelect'

let keybordHeight = '0'

export default {
  components: {
    commonSelect,
    LeftLightStickPureTextCell,
    PhotoCollector,
  },
  data() {
    return {
      // title: '报备申请',
      // selectedTypeList: ['req-请假 1', 'req-请假 2', 'req-请假 3' ],
      selectedType: '请选择', // 报备类型
      attributeInfoList: [],
      photoArray: [],

      submitType: 0,
      applicationData: {
        outServiceSid: '', // '18020214105088611100122963',
        outServiceType: 0,
        outServiceTypeName: '', // '请假',
        outServiceStatus: 0,
        outServiceStatusName: '', // '已提交',
        reason: '',
        startTime: '', // 2018-05-23 08:30,
        endTime: '', // 2018-05-23 12:00,
      },
      keybordHeight: keybordHeight,
    }
  },
  computed: {
    attributeInfoTypeList() {
      return this.attributeInfoList.map((typeEle) => typeEle.name)
    },
    selectedTypeEle() {
      // return this.attributeInfoList.find((typeEle) => typeEle.name === this.selectedType ) // android 會爆錯
      let validEle = {}
      this.attributeInfoList.forEach((typeEle) => {
        if (typeEle.name === this.selectedType) {
          validEle = typeEle
        }
      })
      return validEle
    },
    photoSidList() {
      return this.photoArray.map((photoEle) => photoEle.photoSid)
    },
  },
  mounted() {
    window.getAndroidStatus = this.androidKeyboard
    this.$navbar.myApplicationRequestStyle()
    this.queryAttributeInfoList().then((resp) => {
      this.attributeInfoList = resp.body
    })
    this.photoArray = []
    const detailEle = this.$route.params.applicationDetailEle
    if (detailEle != undefined) {
      this.submitType = 1 // 修改
      this.$store.dispatch('navBar/setNavBarMiddleTitle', '修改报备')
      this.photoArray = [...detailEle.photoInfoList]
      this.applicationData = { ...detailEle.outServiceInfo }
      this.selectedType = this.applicationData.outServiceTypeName
      this.$refs.typeSelector.selectedStr = this.selectedType
      /* 移除下拉框樣式 */
      this.$refs.typeSelector.editable = true
    } else {
      this.submitType = 0 // 新增
    }
  },
  methods: {
    ...mapActions('personal', ['queryAttributeInfoList', 'submitOutService']),
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    androidKeyboard(height, pageUrl) {
      let page = document.getElementById('app')
      if (height === '0') {
        // 鍵盤收起
        page.style.top = '0'
      } else {
        page.style.top = '-' + 0.5 * height + 'px'
      }
    },
    isValidPhotoArray(array) {
      return Array.isArray(array) && array.length > 0
    },
    getSelectStr(data) {
      console.log(this.submitType)
      this.selectedType = data
    },
    showPicker(type) {
      const self = this
      this.$vux.datetime.show({
        cancelText: '取消',
        confirmText: '确定',
        format: 'YYYY-MM-DD HH:mm',
        onConfirm(val) {
          const pickedDate = self.$filter.strToDate(val)
          let end = self.$filter.strToDate(self.applicationData.endTime)
          let start = self.$filter.strToDate(self.applicationData.startTime)
          let now = new Date().toLocaleDateString()
          /* 申請時間昨日以前時 */
          if (Date.parse(new Date(pickedDate).toLocaleDateString()) < Date.parse(now)) {
            self.$vux.alert.show({
              title: '提示',
              content: `不可以申请早于今天的报备`,
            })
            return
          }
          if (type === 'start') {
            if (end != '' && end < pickedDate) {
              self.invalidTimeAlert()
              return
            }
            // self.applicationData.startTime = pickedDate
            self.applicationData.startTime = val
          } else if (type === 'end') {
            /* 30 分鐘限制 */
            let timeLimit = 30 * 60 * 1000
            if (start != '' && start > pickedDate) {
              self.invalidTimeAlert()
              return
            } else if (new Date(start).getTime() === new Date(pickedDate).getTime()) {
              /* 報備申請開始及結束時間不可相同 */
              self.invalidTimeAlert()
              return
            } else if (Date.parse(pickedDate) - Date.parse(start) < timeLimit) {
              /* 结束时间-开始时间小于半小时 */
              self.$vux.alert.show({
                title: '提示',
                content: `报备时间至少为30分钟`,
              })
              return
            }
            // self.applicationData.endTime = pickedDate
            self.applicationData.endTime = val
          }
        },
      })
    },
    invalidTimeAlert() {
      this.$vux.alert.show({
        title: '提示',
        content: `结束时间需大于开始时间`,
      })
    },
    takePhoto(photoType) {
      this.checkPhotoArrayCount({ self: this, photoArray: this.photoArray }).then(() => {
        this.takePicture({ photoType }).then((resp) => {
          this.photoArray.push(resp)
        })
      })
    },
    cancelBtnOnClick() {
      this.$router.go(-1)
    },
    confirmBtnOnClick() {
      if (this.selectedTypeEle.name == undefined) {
        this.$vux.alert.show({
          title: '提示',
          content: `尚未选择报备类型`,
        })
        return
      }
      if (this.applicationData.startTime === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择开始时间`,
        })
        return
      }
      if (this.applicationData.endTime === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择结束时间`,
        })
        return
      }
      if (this.applicationData.reason === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请填写报备原因`,
        })
        return
      }
      if (
        this.selectedTypeEle.name !== '请假' &&
        Array.isArray(this.photoSidList) &&
        this.photoSidList.length < 1
      ) {
        this.$vux.alert.show({
          title: '提示',
          content: `除“请假”外，其余报备需拍摄照片`,
        })
        return
      }
      const param = {
        submitType: this.submitType, // 0 新增, 1 修改
        outServiceSid: this.submitType === 1 ? this.applicationData.outServiceSid : '',
        outServiceType: this.selectedTypeEle.code,
        startTime: this.applicationData.startTime,
        endTime: this.applicationData.endTime,
        reason: this.applicationData.reason,
        photoSidList: this.photoSidList,
      }
      this.submitOutService(param).then((resp) => {
        if (resp.code === 'c200') {
          this.$router.push('/my_application_record')
        }
      })
    },
  },
}



