
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'



export default {
props: {
swiperList: {
type: Array,
default: () => [],
},
isVisible: {
type: Boolean,
default: false,
},
swiperIndex: {
type: Number,
default: 0,
},
},

data() {
return {

}
},
components: {
Swiper,
SwiperItem,
},
computed: {
inputList() {
return this.swiperList.map((photoEle) => {
return {
url: '',
img: photoEle.photoUrl,
title: '',
}
})
},
swiperTitle() {
return `${this.swiperIndex + 1}/${this.swiperList.length}`
},
},
mounted() {

},
methods: {
onSwipe(index) {

console.log('this.swiperTitle')
console.log(this.swiperTitle)
console.log(index)
this.$emit('onSwipe', index)
},
backBtnOnClick() {
console.log(this.swiperTitle)
this.$emit('backBtnOnClick')
},
},
}



