import { mapGetters, mapActions} from 'vuex'
import StoreInfoHeader from '@/components/StoreInfoHeader'

export default {
  data() {
    return {
      topTabItems: [
        {
          tabTitle: '拜訪資訊',
          tabClass: 'general_photo_collection',
          tabTo: '/check_visit/check_info',
        },
        {
          tabTitle: '收集照片',
          tabClass: 'general_photo_collection',
          tabTo: '/check_visit/check_general_photo',
        },
        {
          tabTitle: '特別陳列',
          tabClass: 'special_photo_collection',
          tabTo: '/check_visit/check_special_photo',
        },
        {
          tabTitle: '庫存訂單',
          tabClass: 'launch_order',
          tabTo: '/check_visit/check_order',
        },
        {
          tabTitle: '地點信息',
          tabClass: 'store_info',
          tabTo: '/check_visit/check_store_info',
        },
      ],
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$parent.$off('navbarRightBtnClick') // 取消訂閱
    next()
  },
  mounted() {
    this.$navbar.checkVisitStyle()
    if (this.$navbar.checkInfoPreviousPage === 'home') {
      this.$navbar.setLeftBtn('返回', '', '/home')
    } else if (this.$navbar.checkInfoPreviousPage === 'myVisit') {
      this.$navbar.setLeftBtn('返回', '', '/my_visit')
    }
    // navbarRightBtnListener ...
    const self = this
    this.$parent.$once('navbarRightBtnClick',(param => { // 註冊訂閱
        // self.submitFinishVisit({
        //   visitType: '1',
        //   visitStoreStatusCode: '',
        //   otherStoreStatusName: '',
        //   visitRemark: '',
        // })
    }))
  },
  components: {
    StoreInfoHeader,
  },
  computed: {    
    ...mapGetters('normalVisit', [
      'getVisitDetail',
    ]),
    ...mapGetters('home', [
      'getSelectedVisitEle',
    ]),
  },
  methods: {
    ...mapActions('normalVisit', [
      'submitFinishVisit',
    ]),
  },
}



