  },
  computed: {
    ...mapGetters('personal', [
      'getEmpInfo',
    ]),
  },
  mounted() {
    this.$navbar.personalStyle()
    this.$navbar.myVisitPreviousPage = 'personal'
    this.queryEmpInfo().then((resp) => {})
  },
  methods: {
    ...mapActions('personal', [
      'queryEmpInfo',
		]),
  }
}



