import { mapGetters, mapActions} from 'vuex'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    PhotoCollector,
  },
  data () {
    return {
      // SPECIAL_DISPLAY_PHOTO: 'SPECIAL_DISPLAY',
      uncategorizedPhotoInfo: {},
      // photoArray: [],
      photoArrayPool: []
    }
  },
  computed: {
    ...mapGetters('normalVisit', [
      'getVisitDetail',
      'getSpecialVisitPhotoInfoListArray',
      'getUncategorizedPhotoInfo'
    ]),
    photoArray() {
      if (Array.isArray(this.getUncategorizedPhotoInfo.photoInfoList) && this.getUncategorizedPhotoInfo.photoInfoList.length > 0) {
        return [...this.photoArrayPool,...this.getUncategorizedPhotoInfo.photoInfoList]
      } else {
        return this.photoArrayPool
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    // this.$parent.$off('navbarRightBtnClick') // 取消訂閱
    if (to.path === '/normal_visit/special_photo_collection') {
      // giveUpSpecialDisplayPicture
      const self = this
      this.$vux.confirm.show({
        title: '提示',
        content: '确认返回将清除本次拍摄的照片',
        onConfirm () {
          let photoSidList = []
          if (Array.isArray(self.photoArray) && self.photoArray.length > 0) {
            photoSidList = self.photoArray.map((photoInfo) => {
              return photoInfo.photoSid
            })
          }
          const param = { photoSidList, visitSid: self.getVisitDetail.visitSid }
          const functionName = 'giveUpSpecialDisplayPicture'
          self.$store.$native.execute(
            functionName,
            param,
          ).then((resp) => {
            self.queryVisitDetail()
            next()
          }).catch(() => {
            self.queryVisitDetail()
            next()
          })
        },
        onCancel () { },
      })
    } else {
      next()
    }
  },
  mounted () {
    this.$navbar.tempPhotoCollectionStyle()
    // const self = this
    // this.$parent.$on('navbarRightBtnClick',(param => { // 註冊訂閱
    //   self.$vux.alert.show({
    //     title: '提示',
    //     content: `上传成功`,
    //   })
    // }))

  },
  methods: {
    ...mapActions('normalVisit', [
      'queryVisitDetail',
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    takePhoto(photoType, specialDisplayTypeSid) {
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({photoType, specialDisplayTypeSid}).then( resp => {
          this.photoArrayPool.push(resp)
        })
      })
    },
    selectPhotoType() {
      this.$router.push({name: 'select_photo_type', params:{ photoArray: this.photoArray }})
    }
  }
}



