import {
  QUERY_DAILY_VISIT_PLAN,
  SET_SELECTED_VISIT_ELE,
  QUERY_UN_VISIT_STORE_STATUS,
} from './mutationTypes'

const mutations = {
  [QUERY_DAILY_VISIT_PLAN](state, payload) {
    state.dailyVisitPlan = payload
  },
  [SET_SELECTED_VISIT_ELE](state, payload) {
    state.selectedVisitEle = payload
  },
  [QUERY_UN_VISIT_STORE_STATUS](state, payload) {
    const abnormalVisitList = payload.map((abnormalType) => {
      return abnormalType.statusName
    })
    state.unvisitStoreStatus = payload
    state.abnormalVisitList = abnormalVisitList
  },

}

export default mutations



