import Vue from 'vue'
import Vuex from 'vuex'
// vuex's plugins
// import VuexPersistence from 'vuex-persistedstate'
// self plugins
import native from '@/plugins/native'
import footBarModule from '@/plugins/footbar/module'
import navBarModule from '@/plugins/navbar/module'
// page
import homeModule from '@/pages/home/module'
import normalVisitModule from '@/pages/normalVisit/module'
import attendanceMoudule from '@/pages/attendance/module'
import pesonalMoudule from '@/pages/personal/module'
import checkVisitMoudule from '@/pages/checkVisit/module'
import storeMoudule from '@/pages/store/module'
import campaignMoudule from '@/pages/campaign/module'

Vue.use(Vuex)

// const vuexLocal = new VuexPersistence({
//   storage: window.localStorage,
// })

const store = new Vuex.Store({
  strict: process.env.NODE_ENV === 'development',
  modules: {
    footBar: footBarModule,
    navBar: navBarModule,
    home: homeModule,
    personal: pesonalMoudule,
    normalVisit: normalVisitModule,
    checkVisit: checkVisitMoudule,
    attendance: attendanceMoudule,
    store: storeMoudule,
    campaign: campaignMoudule,
  },
  // plugins: [vuexLocal],
  // native,
  // plugins: [vuexLocal, native.execute],
})
store.$native = native // 將 native 給插進 store
export default store



