export default {
  data () {
    return {
      optionList: ['营业所出发', '经销商处出发', '其他地址出发'],
      navbarActive: 0
    }
  },
  methods: {
    getOption (val, listIndex) {
      // 樣式處理
      this.navbarActive = listIndex
      this.$emit('to-parent-option', val, listIndex)
    }
  }
}



