import { mapGetters, mapActions, mapMutations } from 'vuex'
import DatePicker from '../components/DatePicker'
import '@/../static/laydate.css'

export default {
  data () {
    return {
      attdcInfo: {},
      // outServiceList: [],
      pickedDate: {},
    }
  },
  components: {
    DatePicker,
  },
  computed: {
    ...mapGetters('personal', [
      'getAttendanceDailyInfo',
      'getMyAttendanceSelectedDate',
    ]),
  },
  beforeRouteLeave(to, from, next) {
    // to.swiperList = this.photoArray
    // this.SET_MY_ATTENDANCE_SELECTED_DATE(this.pickedDate)
    next()
  },
  mounted () {
    this.$navbar.myAttendanceStyle()
  },
  methods: {
    ...mapActions('personal', [
      'queryAttendanceDailyInfo',
      'queryOutServiceDetail',
      'queryClockDetail',
    ]),
    ...mapMutations('personal', [
      'SET_SELECTED_APPLICATION_ELE',
      'SET_SELECTED_ATTENDANCE_ELE',
      'SET_SELECTED_OUT_SERVICE_ELE',
      'SET_MY_ATTENDANCE_SELECTED_DATE',
    ]),
    pickerHandler(date) {
      this.SET_MY_ATTENDANCE_SELECTED_DATE(date)
      // this.pickedDate = date
      this.queryAttendanceDailyInfo(date).then((resp) => {
        this.attdcInfo = resp.body
        // this.outServiceList = resp.body.outServiceList
      })
    },
    clockCellOnClick() {
      this.SET_SELECTED_ATTENDANCE_ELE(this.getAttendanceDailyInfo)
      this.queryClockDetail(this.attdcInfo.date).then((resp) => {
        this.$router.push('my_attendance_info')
      })
    },
    outServiceCellOnClick(item) {
      // this.SET_MY_ATTENDANCE_SELECTED_DATE(this.pickedDate)
      // this.SET_SELECTED_OUT_SERVICE_ELE(item)
      // this.$router.push('my_application_detail')
      // console.log('outServiceCellOnClick')
      // console.log(item)
      this.SET_SELECTED_APPLICATION_ELE(item)
      this.queryOutServiceDetail().then((resp) => {
        this.$router.push('my_application_detail')
      })
    },
  }
}



