import Highcharts from 'highcharts'

export default {
  props: {
    chartSeriesData: {
      type: Object,
      default() {
        return {}
      },
    },
  },
  data() {
    return {
      quantityData: {
        category: 'quantity',
        seriesData: [],
        seriesColor: '#7bcfbb',
        yAxisTitle: '(数量/箱)',
        xCategories: [],
        max: '',
      },
      moneyData: {
        category: 'money',
        seriesData: [],
        seriesColor: '#c0754d',
        yAxisTitle: '(金额/元)',
        xCategories: [],
        max: '',
      },
      show: '',
    }
  },
  methods: {
    getSeriesData() {
      this.quantityData.seriesData = this.chartSeriesData.quantitySeriesData
      this.quantityData.xCategories = this.chartSeriesData.quantityXCategories
      this.quantityData.max = this.chartSeriesData.quantityMax
      this.moneyData.seriesData = this.chartSeriesData.moneySeriesData
      this.moneyData.xCategories = this.chartSeriesData.moneyXCategories
      this.moneyData.max = this.chartSeriesData.moneyMax
      this.chart(this.quantityData)
    },
    chart(chartData) {
      this.show = chartData.category
      Highcharts.chart('chart', {
        chart: {
          type: 'spline',
          // marginTop: 34,
          margin: [34, 20, 30, 50],
          style: {
            borderBottomRightRadius: '5px',
            borderBottomLeftRadius: '5px',
          },
        },
        title: {
          text: '',
        },
        xAxis: { // X軸
          categories: chartData.xCategories,
          labels: {
            format: '{value}月',
            style: {
              color: '#666666',
              fontSize: '11px',
            },
          },
          tickLength: 0,
        },
        legend: { // 圖例
          enabled: false,
        },
        yAxis: { // Y軸
          title: {
            text: chartData.yAxisTitle,
            align: 'high',
            rotation: 0,
            y: -16,
            offset: 10,
            gridLineColor: '#f3f3f3',
            style: {
              color: '#666666',
              fontSize: '9px',
            },
          },
          labels: {
            format: '{value}',
            style: {
              color: '#666666',
              fontSize: '10px',
              textOverflow: 'none', // 防止字段過長時被省略部分內容
            },
            align: 'right',
          },
          offset: -5,
          tickAmount: 6, // 坐标轴上的刻度总数
          tickPixelInterval: 20, // 刻度之间间隔的像素值
          max: chartData.max,
        },
        series: [{
          data: chartData.seriesData,
        }],
        plotOptions: { // 数据列配置
          series: {
            color: chartData.seriesColor,
            lineWidth: 1,
            marker: { // 標記樣式和形狀
              radius: 2.5,
            },
            dataLabels: { // 是否在標記上方顯示數據值
              enabled: true,
              format: '{y}',
              align: 'left',
              crop: false,
              overflow: 'none',
              style: {
                color: chartData.seriesColor,
                textOutline: '0px',
                fontSize: '11px',
                fontWeight: 'normal',
              },
            },
            enableMouseTracking: false, // 是否针对某个数据列关闭或启用鼠标跟踪行为，包括数据提示框、数据点及数据列点击事件等行为
          },
        },
        credits: {
          enabled: false,
        },
      })
    },
  },
}



