/* eslint-disable */
import { mapGetters, mapActions } from 'vuex'

export default {
  data (){
    return {
      date: '', // 顯示日期
      day: '', // 顯示星期
      time: null,
      title: '考勤',
    }
  },
  beforeRouteEnter (to, from, next) {
    /* 避免點擊過快，進入頁面時鐘為空白 */
    next((vm)=>{
      vm.showDateAndDay()
    })
  },
  mounted() {
    this.$navbar.attendanceStyle()
    this.queryClockInOutInfo()
  },
  computed: {
    ...mapGetters('attendance', [
      'getClockInOutInfo',
    ]),
    isCheckedIn() {
      /* 檢查今日签到时间 */
      console.log(this.getClockInOutInfo)
      if (!this.getClockInOutInfo.clockInTime || !this.getClockInOutInfo.clockInTime.trim()) {
        return false // 藍色
      } else {
        return true // 變灰
      }
    },
    checkCIAddrName() {
      /* 檢查今日签到地点 */
      if (this.getClockInOutInfo.clockInAddressName === ('' || null || undefined) ) {
        return false
      } else {
        return true
      }
    },
    checkCIAddr() {
      /* 檢查今日签到地址 */
      if (this.getClockInOutInfo.clockInAddress === ('' || null || undefined) ) {
        return false
      } else {
        return true
      }
    },
    isCheckedOut() {
      if (!this.getClockInOutInfo.clockOutTime || !this.getClockInOutInfo.clockOutTime.trim()) {
        return false // 藍色
      } else {
        return true // 變灰
      }
    },
  },
  methods: {
    ...mapActions('attendance', [
      'queryClockInOutInfo',
    ]),
    ...mapActions('personal', [
      'queryClockDetail',
    ]),
    /* 顯示日期 & 星期 */
    showDateAndDay(){
      let day_list = ['日', '一', '二', '三', '四', '五', '六']
      let date = new Date()
      let dateStr = date.toISOString().substring(0, 10)
      let day = date.getDay()
      console.log('date : ' + dateStr)
      console.log('today is : 星期' + day_list[day])
      this.date = dateStr
      this.day = day_list[day]

      // canvas 設置
      /*获取画布元素*/
      let dom = document.getElementById('clock')
      /*获得2d执行环境*/
      let ctx = dom.getContext('2d')
      /*定义半径为宽度的一半*/
      let r = 150 / 2
      /*缩放比例*/
      let rem = 150 / 200
      // this.time = setInterval(() => {
        this.drawClock( ctx, r, rem)
      // }, 1000)
    },
    drawClockBackground(ctx, r, rem) {
      /*画时钟的背景图*/
      /*一开始先把画布的初始状态保存下来*/
      ctx.save();
      /*将画布的中心原点由(0,0)变成(r,r)*/
      ctx.translate(r,r);
      /*开始绘画*/
      ctx.beginPath();
      /*定义画笔的宽度*/
      ctx.lineWidth = 12*rem;
      /*绘制一个圆*/
      ctx.arc(0,0,r-5*rem,0,2*Math.PI,false);
      /*进行填充*/
      ctx.strokeStyle = 'rgba(96, 207, 255, 0.7)'
      ctx.stroke()
      
      /*画时钟上的所显示的数字*/
      let hourNumber = [3, '' , '',6, '', '' ,9, '' , '' ,12, '' , ''];//12个小时数
      ctx.font = 12 *rem +'px Arial';//定义字体大小和样式
      ctx.textAlign='center';//将文本内容居中对齐
      ctx.textBaseline='middle';//将文本基线设置为中线
      hourNumber.forEach(function(number,i){
        let rad = 2*Math.PI/12*i;//每个数字的弧度
        /*求出数字存放位置的x和y坐标*/
        let x = Math.cos(rad)*(r - 25*rem);
        let y = Math.sin(rad)*(r - 25*rem);
        /*填充数字*/
        ctx.fillStyle = 'rgb(96, 207, 255)'
        ctx.fillText(number,x,y)
      })
    },
    /* 时针的绘制 */
    drawHour(hour, minute, ctx, r, rem){
      /*再次保存当前画布的状态*/
      ctx.save();
      ctx.beginPath();
      let rad = 2 * Math.PI / 12 * hour;
      let mrad = 2 * Math.PI / 12 /60 * minute;
      ctx.rotate(rad+mrad);//旋转当前绘画
      ctx.lineWidth = 6;
      ctx.lineCap = 'round';
      ctx.moveTo(0,0);
      ctx.lineTo(0,-35);
      ctx.stroke();
      /*返回最近的一次SAVE的画布状态*/
      ctx.restore();
    },
    /* 分针的绘制 */
    drawMinute(minute, ctx, r, rem){
      ctx.save();
      ctx.beginPath();
      let rad = 2 * Math.PI / 60 * minute;
      ctx.rotate(rad);
      ctx.lineWidth = 6;
      ctx.lineCap = 'round';
      ctx.moveTo(0,0);
      ctx.lineTo(0, -45);
      ctx.stroke();
      ctx.restore();
    },
    drawClock(ctx, r, rem){
      /*动态改变的时候，每一次都要先将之前的矩形画布清除掉，然后重新绘制*/
      ctx.clearRect(0, 0, 150, 150);
      let now = new Date();
      let hour = now.getHours();
      let minute = now.getMinutes();
      console.log('now : ' + now)
      this.drawClockBackground(ctx, r, rem);//背景
      this.drawHour(hour, minute, ctx, r, rem);//小时
      this.drawMinute(minute, ctx, r, rem);//分钟
      /*恢复到一开始save的那个画布状态，进行重新绘制*/
      ctx.restore();
    },
    /* 点击下班签到 */
    checkOutPage (isCheckedOut){
      if (this.isCheckedIn && !isCheckedOut) {
        this.$router.push('checkOut')
      } else if (!this.isCheckedIn && !isCheckedOut) {
        this.$vux.alert.show({
          title: '提示',
          content: '上班签到未记录，禁止下班签到！',
        })
      } else {
        this.$vux.alert.show({
          title: '提示',
          content: `已有签退资料`,
        })
      }
    },
    /* 点击上班签到 */
    checkInPage (isCheckedIn){
      if (!isCheckedIn) {
        this.$router.push({name: 'barnch_check_in'})
      } else {
        this.$vux.alert.show({
          title: '提示',
          content: `已有签到资料`,
        })
      }
    },
    /* 点击查看签到详情 */
    MyAttendanceInfoPage (){
      this.queryClockDetail(this.date).then((resp) => {
        this.$router.push('my_attendance_info')
      })
      // }).catch((error) => {
      //   this.$vux.alert.show({
      //     title: '提示',
      //     content: `${error}`,
      //   })
      // })

      // console.log('MyAttendanceInfoPage')
      // this.$router.push({
      //   name:'meAttendanceInfo',
      //     // query:{
      //     //   'customerId':this.$route.query.customerId
      //     // }
      // });
    },
    /* 点击报备申请 */
    // meAttendanceRequestPage (){
    //   console.log('meAttendanceRequestPage')
    //   this.$router.push({
    //     name:'meAttendanceRequest',
    //       // query:{
    //       //   'customerId':this.$route.query.customerId
    //       // }
    //   });
    // }
  }
}



