import {
  SHOW_NAV_BAR,
  HIDE_NAV_BAR,
  SET_NAV_BAR_MIDDLE_TITLE,
  // SET_NAV_BAR_LEFT_BUTTON,
  // SET_NAV_BAR_RIGHT_BUTTON,
  SET_NAV_BAR_LEFT_BUTTON_IMAGE_URL,
  SET_NAV_BAR_LEFT_BUTTON_TITLE,
  SET_NAV_BAR_LEFT_BUTTON_LINK_TO,
  SET_NAV_BAR_RIGHT_BUTTON_IMAGE_URL,
  SET_NAV_BAR_RIGHT_BUTTON_TITLE,
  SET_NAV_BAR_RIGHT_BUTTON_LINK_TO,
  SET_NAV_BAR_RIGHT_BUTTON_EVENT,
} from './mutationTypes'

const mutations = {
  [SHOW_NAV_BAR](state) {
    state.isNavBarVisible = true
  },
  [HIDE_NAV_BAR](state) {
    state.isNavBarVisible = false
  },
  [SET_NAV_BAR_MIDDLE_TITLE](state, payload) {
    // BaiduMobStat.onPageEnd(state.middleTitle)
    state.middleTitle = payload
    // BaiduMobStat.onPageStart(state.middleTitle)
  },
  // [SET_NAV_BAR_LEFT_BUTTON](state, payload) {
  //   state.navBarLeftButton = payload
  // },
  // [SET_NAV_BAR_RIGHT_BUTTON](state, payload) {
  //   state.navBarRightButton = payload
  // },
  [SET_NAV_BAR_LEFT_BUTTON_IMAGE_URL](state, payload) {
    state.navBarLeftBtnImageUrl = payload
  },
  [SET_NAV_BAR_LEFT_BUTTON_TITLE](state, payload) {
    state.navBarLeftBtnTitle = payload
  },
  [SET_NAV_BAR_LEFT_BUTTON_LINK_TO](state, payload) {
    state.navBarLeftBtnLinkTo = payload
  },
  [SET_NAV_BAR_RIGHT_BUTTON_IMAGE_URL](state, payload) {
    state.navBarRightBtnImageUrl = payload
  },
  [SET_NAV_BAR_RIGHT_BUTTON_TITLE](state, payload) {
    state.navBarRightBtnTitle = payload
  },
  [SET_NAV_BAR_RIGHT_BUTTON_LINK_TO](state, payload) {
    state.navBarRightBtnLinkTo = payload
  },
  [SET_NAV_BAR_RIGHT_BUTTON_EVENT](state, payload) {
    state.navBarRightBtnEvent = payload
  },
}

export default mutations



