import {
  SET_SELECTED_VISIT_ELE,
  SET_SELECTED_APPLICATION_ELE,
  SET_APPLICATION_DETAIL_ELE,
  QUERY_VISIT_DETAIL,
  QUERY_STORE_PRODUCT_LIST,
  QUERY_ATTENDANCE_DAILY_INFO,
  SET_SELECTED_ATTENDANCE_ELE,
  SET_SELECTED_OUT_SERVICE_ELE,
  QUERY_CLOCK_DETAIL,
  QUERY_EMP_INFO,
  ADD_STORE_LIST,
  SUBTRACT_STORE_LIST,
  CLEAR_STORE_LIST,
  SET_MY_VISIT_SELECTED_DATE,
  SET_MY_ATTENDANCE_SELECTED_DATE,
} from './mutationTypes'

import UnselectedIcon from '@/assets/img/common/choose_n.png'
import SelectedIcon from '@/assets/img/common/choose_y.png'

const mutations = {
  [QUERY_EMP_INFO](state, payload) {
    state.empInfo = payload
  },
  [SET_MY_ATTENDANCE_SELECTED_DATE](state, payload) {
    state.myAttendanceSelectedDate = payload
  },
  [SET_MY_VISIT_SELECTED_DATE](state, payload) {
    state.myVisitSelectedDate = payload
  },
  [ADD_STORE_LIST](state, store) {
    const tempStore = store
    tempStore.imageUrl = SelectedIcon
    state.additionStoreList.push(store)
    const additionStoreSet = new Set([...state.additionStoreList])
    state.additionStoreList = [...additionStoreSet]
  },
  [SUBTRACT_STORE_LIST](state, store) {
    const tempStore = store
    tempStore.imageUrl = UnselectedIcon
    const additionStoreSet = new Set([...state.additionStoreList])
    additionStoreSet.delete(store)
    state.additionStoreList = [...additionStoreSet]
  },
  [CLEAR_STORE_LIST](state) {
    state.additionStoreList = []
  },
  [SET_SELECTED_VISIT_ELE](state, payload) {
    state.selectedVisitEle = payload
  },
  [SET_SELECTED_APPLICATION_ELE](state, payload) {
    state.selectedApplicationEle = payload
  },
  [SET_APPLICATION_DETAIL_ELE](state, payload) {
    state.applicationDetailEle = payload
  },
  [SET_SELECTED_ATTENDANCE_ELE](state, payload) {
    state.selectedAttendanceEle = payload
  },
  [SET_SELECTED_OUT_SERVICE_ELE](state, payload) {
    state.selectedOutServiceEle = payload
  },
  [QUERY_CLOCK_DETAIL](state, payload) {
    state.attendanceDetailEle = payload
  },
  [QUERY_ATTENDANCE_DAILY_INFO](state, payload) {
    state.attendanceDailyInfo = payload
    state.attendanceDailyInfo.outServiceList = payload.outServiceList.map((outServiceItem) => {
      const tempItem = outServiceItem
      const startTime = outServiceItem.startTime.substring(11,16)
      const endTime = outServiceItem.endTime.substring(11,16) // '2018-05-23 12:00'
      const timeIntervalStr = `${startTime} - ${endTime}`  // '08:30 - 12:00', // from mutation DIY
      tempItem.timeIntervalStr = timeIntervalStr
      return tempItem
    })
  },
  [QUERY_VISIT_DETAIL](state, payload) {
    state.visitDetail = payload
    state.specialVisitPhotoInfoListArray = [] // specialVisit 本身是多個 List 的集合，所以在push之前要先清空
    const DOOR_WAY_PHOTO = 'DOOR_WAY'
    const OTHER_PHOTO = 'OTHER'
    const SPECIAL_DISPLAY_PHOTO = 'SPECIAL_DISPLAY'
    for (const visitPhotoEle of payload.visitPhotoInfo) {
      if (visitPhotoEle.photoDisplayType === DOOR_WAY_PHOTO) {
        state.doorWayPhotoInfoList = visitPhotoEle.photoInfoList
      }
      else if (visitPhotoEle.photoDisplayType === OTHER_PHOTO) {
        state.otherPhotoInfoList = visitPhotoEle.photoInfoList
      } else if (visitPhotoEle.photoDisplayType === SPECIAL_DISPLAY_PHOTO) {
        if (visitPhotoEle.specialDisplayTypeSid === '') {
          state.uncategorizedPhotoInfoList = visitPhotoEle.photoInfoList
        } else {
          state.specialVisitPhotoInfoListArray.push(visitPhotoEle)
        }
      }
    }
  },
  /* eslint-disable no-param-reassign */
  [QUERY_STORE_PRODUCT_LIST](state, payload) {
    state.storeProductList.totalStockQty = parseInt(payload.totalStockQty, 0)
    state.storeProductList.totalOrderQty = parseInt(payload.totalOrderQty, 0)
    state.storeProductList.productStockOrderList = payload.productStockOrderList
    for (const productEle of state.storeProductList.productStockOrderList) {
      productEle.subStockQty = parseInt(productEle.subStockQty, 0)
      productEle.subOrderQty = parseInt(productEle.subOrderQty, 0)
      for (const productDetailItem of productEle.productList) {
        productDetailItem.stockQty = parseInt(productDetailItem.stockQty, 0)
        productDetailItem.orderQty = parseInt(productDetailItem.orderQty, 0)
      }
    }
  },
  /* eslint-disable no-param-reassign */
}

export default mutations



