import Login from '@/pages/login/views/Login'
import Home from '@/pages/home/views/Home'
import SyncData from '@/pages/home/views/SyncData'
import AbnormalVisit from '@/pages/home/views/AbnormalVisit'
import PhotoSwiper from '@/components/PhotoSwiper'

import NormalVisit from '@/pages/normalVisit/views/NormalVisit'
import GeneralPhotoCollection from '@/pages/normalVisit/views/GeneralPhotoCollection'
import SpecialPhotoCollection from '@/pages/normalVisit/views/SpecialPhotoCollection'
import LaunchOrder from '@/pages/normalVisit/views/LaunchOrder'
import StoreInfo from '@/pages/normalVisit/views/StoreInfo'
import SelectPhotoType from '@/pages/normalVisit/views/SelectPhotoType'
import TempPhotoCollection from '@/pages/normalVisit/views/TempPhotoCollection'

import Store from '@/pages/store/views/Store'
import StoreDetail from '@/pages/store/views/StoreDetail'
import AddRoute from '@/pages/store/views/AddRoute'

import Campaign from '@/pages/campaign/views/Campaign'
import CampaignDetail from '@/pages/campaign/views/CampaignDetail'
import CreateCampaign from '@/pages/campaign/views/CreateCampaign'
import CampaignSelectStore from '@/pages/campaign/views/SelectStore'

import Attendance from '@/pages/attendance/views/Attendance'
import AttendanceCheckIn from '@/pages/attendance/views/checkIn'

import AttendanceCheckOut from '@/pages/attendance/views/checkOut'
import BranchCheckIn from '@/pages/attendance/components/BranchCheckIn'
import CustomerCheckIn from '@/pages/attendance/components/CustomerCheckIn'
import OtherCheckIn from '@/pages/attendance/components/OtherCheckIn'

import Personal from '@/pages/personal/views/Personal'
import MyVisit from '@/pages/personal/views/MyVisit'
import MySetting from '@/pages/personal/views/MySetting'
import MyAttendance from '@/pages/personal/views/MyAttendance'
import MyApplicationRecord from '@/pages/personal/views/MyApplicationRecord'
import MyApplicationDetail from '@/pages/personal/views/MyApplicationDetail'
import MyAttendanceInfo from '@/pages/personal/views/MyAttendanceInfo'
import MyApplicationRequest from '@/pages/personal/views/MyApplicationRequest'
import PersonalSelectStore from '@/pages/personal/views/SelectStore'
import VersionInfo from '@/pages/personal/views/VersionInfo'

import CheckVisit from '@/pages/checkVisit/views/CheckVisit'
import CheckInfo from '@/pages/checkVisit/views/CheckInfo'
import CheckGeneralPhoto from '@/pages/checkVisit/views/CheckGeneralPhoto'
import CheckSpecialPhoto from '@/pages/checkVisit/views/CheckSpecialPhoto'
import CheckOrder from '@/pages/checkVisit/views/CheckOrder'
import CheckStoreInfo from '@/pages/checkVisit/views/CheckStoreInfo'
// 功能開發中
import Developing from '../components/Developing'

const routes = [
  { path: '/login', name: 'login', component: Login },
  { path: '/home', name: 'home', component: Home },
  { path: '/sync_data', name: 'syncDataPage', component: SyncData },
  { path: '/abnormal_visit', component: AbnormalVisit },
  {
    path: '/normal_visit',
    component: NormalVisit,
    children: [
      { path: 'general_photo_collection', name: 'general_photo_collection', component: GeneralPhotoCollection },
      { path: 'special_photo_collection', name: 'special_photo_collection', component: SpecialPhotoCollection },
      { path: 'launch_order', name: 'launch_order', component: LaunchOrder },
      { path: 'store_info', name: 'store_info', component: StoreInfo },
    ],
  },
  {
    path: '/check_visit',
    component: CheckVisit,
    children: [
      { path: 'check_general_photo', name: 'check_general_photo', component: CheckGeneralPhoto },
      { path: 'check_special_photo', name: 'check_special_photo', component: CheckSpecialPhoto },
      { path: 'check_order', name: 'check_order', component: CheckOrder },
      { path: 'check_store_info', name: 'check_store_info', component: CheckStoreInfo },
      { path: 'check_info', name: 'check_info', component: CheckInfo },
    ],
  },
  { path: '/select_photo_type', name: 'select_photo_type', component: SelectPhotoType },
  { path: '/temp_photo_collection', component: TempPhotoCollection },
  { path: '/personal', name: 'personal', component: Personal },
  { path: '/my_attendance_info', name: 'my_attendance_info', component: MyAttendanceInfo },
  { path: '/my_application_request', name: 'my_application_request', component: MyApplicationRequest },
  { path: '/my_visit', name: 'my_visit', component: MyVisit },
  { path: '/my_setting', component: MySetting },
  { path: '/my_attendance', component: MyAttendance },
  { path: '/my_application_record', name: 'my_application_record', component: MyApplicationRecord },
  { path: '/my_application_detail', name: 'my_application_detail', component: MyApplicationDetail },
  { path: '/personal_select_store', name: 'personal_select_store', component: PersonalSelectStore },

  { path: '/store', name: 'store', component: Store },
  { path: '/store_detail', name: 'store_detail', component: StoreDetail },
  { path: '/store_addroute', name: 'store_addroute', component: AddRoute },
  // {
  //   path: '/store',
  //   component: Store,
  //   children: [{
  //     path: '/store/detail',
  //     component: StoreDetail,
  //     children: [
  //       { path: '/store/detail/add', component: AddRoute },
  //     ],
  //   }],
  // },
  { path: '/campaign', name: 'campaign', component: Campaign },
  { path: '/campaign_select_store', name: 'campaign_select_store', component: CampaignSelectStore },
  { path: '/campaign_detail', name: 'campaign_detail', component: CampaignDetail },
  { path: '/create_campaign', name: 'create_campaign', component: CreateCampaign },
  { path: '/attendance', name: 'attendance', component: Attendance },
  {
    path: '/checkIn',
    name: 'checkIn',
    component: AttendanceCheckIn,
    children: [
      { path: 'barnch_check_in', name: 'barnch_check_in', component: BranchCheckIn },
      { path: 'customer_check_in', name: 'customer_check_in', component: CustomerCheckIn },
      { path: 'other_check_in', name: 'other_check_in', component: OtherCheckIn },
    ],
  },
  { path: '/checkOut', name: 'checkOut', component: AttendanceCheckOut },
  { path: '/photo_swiper', name: 'photo_swiper', component: PhotoSwiper },
  { path: '/version_info', name: 'version_info', component: VersionInfo },
  // 功能開發中
  { path: '/developing', name: 'developing', component: Developing },

  { path: '', redirect: '/login' },
  { path: '*', redirect: '/login' },
]

export default routes



