export default {
  data() {
    return {
      selectMonth: '',
      showMonthPicker: false,
      today: {},
    }
  },
  mounted() {
    // this.thisMonth()
    this.initSelect()
  },
  methods: {
    // initCalendar() {
    //   Laydate.render({
    //     elem: '#monthPicker',
    //     type: 'month',
    //     value: this.selectMonth,
    //     btns: ['confirm'],
    //     // trigger: 'click',
    //     position: 'static',
    //     ready: () => {
    //       // 確認按鈕樣式
    //       document.querySelector('.laydate-btns-confirm').classList += ' btn bg-blue'
    //       // 清空日期表格的內容，可以減少高度
    //       document.querySelector('.layui-laydate-content table').textContent = ''
    //     },
    //     done: (value, data) => { // 點選月份
    //     console.log(data)
    //       this.selectMonth = value
    //       const { year, month } = data
    //       this.$emit('to-parent', this.selectMonth, year, month)
    //       this.showMonthPicker = false
    //     },
    //   })
    //   const { year, month } = this.today
    //   console.log('to-parent', this.selectMonth, year, parseInt(month))
    //   this.$emit('to-parent', this.selectMonth, year, parseInt(month))
    // },
    // thisMonth() { // 預設選擇目前月份
    //   this.today = new Date()
    //   const year = this.today.getFullYear()
    //   const month = (this.today.getMonth() + 1) > 9 ? (this.today.getMonth() + 1) : `0${this.today.getMonth() + 1}`
    //   this.today.month = month
    //   this.today.year = year
    //   this.selectMonth = `${year}-${month}`
    //   this.initCalendar()
    // },
    initSelect(){
      // 判斷日期
      const thistoday = new Date()
      const thisyear = thistoday.getFullYear()
      let thismoneth = (thistoday.getMonth() + 1) > 9 ? (thistoday.getMonth() + 1) : `0${thistoday.getMonth() + 1}`
      document.getElementById("thisMonth").innerHTML=`${thisyear}-${thismoneth}`
      thismoneth = thismoneth.substring(0,1) == 0 ? thismoneth.substring(1,2) : thismoneth
      document.getElementById("thisMonth").value = `${thisyear}-${thismoneth}`
      let lastyear = (thismoneth - 1) < 1 ? thisyear - 1 : thisyear
      let lastmonth = (thismoneth - 1) < 1 ? 12 : thismoneth - 1
      document.getElementById("lasMonth").value = `${lastyear}-${lastmonth}`
      lastmonth = (lastmonth.toString().length) <= 1 ? `0${lastmonth}` : lastmonth
      document.getElementById("lasMonth").innerHTML=`${lastyear}-${lastmonth}`;
      this.chooseSelect()
    },
    chooseSelect(){
      const selvue = document.getElementById("select_month").value
      const year = selvue.split("-")[0]
      const month = selvue.split("-")[1]
      this.$emit('to-parent', selvue, year, month)
    },
  },
}



