import { mapGetters, mapActions, mapMutations } from 'vuex'
// import Laydate from 'layui-laydate'
import '@/../static/laydate.css'
import DatePicker from '../components/DatePicker'
import CoverDialog from '@/pages/home/components/CoverDialog'

const CHECK_VISIT_PATH = '/check_visit'
export default {
  components: {
    DatePicker,
    CoverDialog,
  },
  data () {
    return {
      pickedDateStr: '',
      pickedDateEnd: {},
      pickedDateStart: {},
      dailyVisitPlan: [],
      respInfo: {},
      planVisitCount: '',
      temporaryVisitCount: '',
      actualVisitCount: '',
      actualPlanVisitCount: '',
      actualTemporaryVisitCount: '',
      coverDialogVisible: false,
      NORMAL: 'normal',
      ABNORMAL: 'abnormal',
      selectedStoreInfo: {},
    }
  },
  computed: {
    ...mapGetters('personal', [
      // 'getSelectedVisitEle',
      'getMyVisitSelectedDate',
    ]),
    isBeforeTomorrow() {
      return this.$filter.isBeforeTomorrow(this.pickedDateStart)
    },
    isAfterToday() {
      return this.$filter.isAfterToday(this.pickedDateEnd)
    },
    isToday() {
      return this.$filter.dateToStrYYYYmmDD(new Date()) === this.pickedDateStr
    },
    unfinishedVisits() {
      let unfinishedVisit = []
      this.dailyVisitPlan.forEach((visit) => {
        if (visit.visitStatus == 1) {
          unfinishedVisit.push(visit)
        }
      })
      return unfinishedVisit
    }
  },
  mounted () {
    this.$navbar.myVisitStyle()
    this.$navbar.checkInfoPreviousPage = 'myVisit'
    this.$navbar.normalVisitPreviousPage = 'myVisit'
    this.$navbar.abnormalVisitPreviousPage = 'myVisit'
    if (this.$navbar.myVisitPreviousPage === 'home') {
      this.$navbar.setLeftBtn('返回', '', '/home')
    } else if (this.$navbar.myVisitPreviousPage === 'personal') {
      this.$navbar.setLeftBtn('返回', '', '/personal')
    }
    const self = this
    this.$parent.$on('navbarRightBtnClick',(param => { // 註冊訂閱
      if (self.isAfterToday) {
        self.$router.push({name:'personal_select_store', params:{selectedDate:self.pickedDateStr}})
      } else {
        self.$vux.alert.show({
          title: '提示',
          content: `请选择今天之后的日期`,
        })
      }
    }))
  },
  beforeRouteLeave(to, from, next) {
    this.$parent.$off('navbarRightBtnClick') // 取消訂閱
    next()
  },
  methods: {
    ...mapActions('store', [
      'queryStoreDetail',
      'queryStorePerformance',
      'queryWechatMarketPlaceWithId',
    ]),
    ...mapActions('personal', [
      'querySpecificDayVisitInfo',
      'removeVisitPlan',
		]),
    ...mapActions('home', [
      'setSelectedVisitEle',
      'queryUnVisitStoreStatus',
		]),
    ...mapActions('normalVisit', [
      'queryStoreProductList',
      'queryVisitDetail',
    ]),
    ...mapMutations('store', [
      'SET_SELECTED_LIST_TYPE',
    ]),
    ...mapMutations('personal', [
      'SET_MY_VISIT_SELECTED_DATE',
    ]),
    pickerHandler(pickedDateStr) {
      this.SET_MY_VISIT_SELECTED_DATE(pickedDateStr)
      this.pickedDateStr = pickedDateStr
      this.pickedDateEnd = this.$filter.strToDateEnd(pickedDateStr) // 為選中的日期 填入 hour & minute 23:59
      this.pickedDateStart = this.$filter.strToDateStart(pickedDateStr) // 為選中的日期 填入 hour & minute 01:00
      this.querySpecificDayVisitInfo(pickedDateStr).then((resp) => {
        this.dailyVisitPlan = resp.body.storeVisitInfoList
        this.planVisitCount = resp.body.planVisitCount
        this.temporaryVisitCount = resp.body.temporaryVisitCount
        this.actualVisitCount = resp.body.actualVisitCount
        this.actualPlanVisitCount = resp.body.actualPlanVisitCount
        this.actualTemporaryVisitCount = resp.body.actualTemporaryVisitCount
        this.$nextTick(() => {
          //滚动条置顶
          this.$refs.scroller.reset({top: 0})
        })
      })
    },
    cellHandler(selectedStoreInfo) {
      if (selectedStoreInfo.visitStatus == 0 && !this.isAfterToday) { return } // 0：今天以前，顯示 當日未拜访
      if (this.isAfterToday && !this.isToday) {
        this.$vux.alert.show({
          title: '提示',
          content: `任务未开始，不能进行拜访`,
        })
        return
      }
      const hadUnfinished = Array.isArray(this.unfinishedVisits) && this.unfinishedVisits.length > 0 // 1：拜访中
      if (hadUnfinished) {
        const isUnfinishedItem = selectedStoreInfo.storeId == this.unfinishedVisits[0].storeId
        if (!isUnfinishedItem) {
          this.$vux.alert.show({
            title: '提示',
            content: `你之前在 ${this.unfinishedVisits[0].storeName} 拜访未完成，请完成拜访`,
          })
          return
        }
      }
      this.selectedStoreInfo = selectedStoreInfo
      this.SET_SELECTED_LIST_TYPE('store')
      if (this.selectedStoreInfo.targetType == 1) { // 0:集团终端, 1:建议终端
        this.SET_SELECTED_LIST_TYPE('suggested')
        this.queryWechatMarketPlaceWithId({ poiId: this.selectedStoreInfo.storeId }).then((resp) => {
        }).catch((e) => {
          // this.$vux.alert.show({
          //   title: '提示',
          //   content: `${e}`,
          // })
        })
      }
      this.setSelectedVisitEle(this.selectedStoreInfo)
      if (this.selectedStoreInfo.visitStatus == 2 || this.selectedStoreInfo.visitStatus == 3) { //2：已拜访 3：有訂單
        this.queryVisitData(this.selectedStoreInfo.storeId).then(() => {
          this.$router.push('/check_visit/check_info') // 到 checkVisit
        })
      } else if (selectedStoreInfo.visitStatus == 1) { // 1：拜访中
         if (selectedStoreInfo.visitType == 1) { // 1 正常拜訪，2 異常拜訪
           this.coverDialogHandler(this.NORMAL)
         } else {
           this.coverDialogHandler(this.ABNORMAL)
         }
      } else {
        this.coverDialogVisible = true
      }
    },
    coverDialogHandler(visitType) {
      this.coverDialogVisible = false
      if (visitType === this.NORMAL) {
        this.queryVisitData(this.selectedStoreInfo.storeId).then(() => {
          this.$router.push('normal_visit/general_photo_collection')
        })
      } else if (visitType === this.ABNORMAL) {
        this.queryUnVisitStoreStatus(this.selectedStoreInfo.visitSid).then(() => {
          this.$router.push('abnormal_visit')
        })
      }
    },
    showCoverDialog(visible, homePageInfo) {
      this.coverDialogVisible = visible
    },
    queryVisitData(storeId) {
      return new Promise((resolve, reject) => {
        Promise.all([
          this.queryVisitDetail(),
          this.queryStoreProductList(),
          this.queryStoreDetail(storeId),
          this.queryStorePerformance(storeId),
        ]).then(() => {
          resolve()
        }).catch(() => {
          reject()
        })
      })
    },
    deleteBtnTouchUpInside(selectedEle) {
      const self = this
      this.$vux.confirm.show({
        title: '提示',
        content: '是否确认要删除此拜访任务',
        onConfirm () {
          // const removeVisitPlanResp = this.removeVisitPlan(selectedEle.visitSid).then(resp => resp)
          self.removeVisitPlan(selectedEle.visitSid).then((resp) => {
            if (resp.code === 'c200') {
              self.pickerHandler(self.pickedDateStr)
            }
          })
        },
        // onCancel() { },
      })
    }
  }
}



