import { mapGetters, mapActions, mapMutations } from 'vuex'
import StoreInfoHeader from '@/components/StoreInfoHeader'
import LeftLightStickPureTextCell from '@/components/LeftLightStickPureTextCell'
import BlueTickSelector from '@/components/BlueTickSelector'
import TopTabBar from '@/components/TopTabBar'

export default {
  components: {
    StoreInfoHeader,
    LeftLightStickPureTextCell,
    BlueTickSelector,
    TopTabBar,
  },
  data() {
    return {
      isSubmit: false,
      topTabItems: [
        {
          tabTitle: '收集照片',
          tabClass: 'general_photo_collection',
          tabTo: '/normal_visit/general_photo_collection',
        },
        {
          tabTitle: '特別陳列',
          tabClass: 'special_photo_collection',
          tabTo: '/normal_visit/special_photo_collection',
        },
        {
          tabTitle: '庫存訂單',
          tabClass: 'launch_order',
          tabTo: '/normal_visit/launch_order',
        },
        {
          tabTitle: '地點資訊',
          tabClass: 'store_info',
          tabTo: '/normal_visit/store_info',
        },
      ],
    }
  },
  computed: {
    ...mapGetters('normalVisit', [
      'getVisitDetail',
    ]),
    ...mapGetters('home', [
      'getSelectedVisitEle',
    ]),
  },
  beforeRouteLeave(to, from, next) {
    if ((to.path === '/home' || to.path === '/my_visit')) {
      const self = this  
      if (!this.isSubmit) {
        this.$vux.confirm.show({
          title: '提示',
          content: '确认返回将清除本次拜访记录',
          onConfirm () {
            self.$parent.$off('navbarRightBtnClick') // 取消訂閱
            const functionName = 'giveUpVisit'
            const visitSid = self.getSelectedVisitEle.visitSid
            const param = { visitSid }
            self.$store.$native.execute(
              functionName,
              param,
            ).then((resp) => {
              next()
            }).catch(() => {
              next()  
            })
          },
          onCancel () {
          },
        })        
      } else {
        this.$parent.$off('navbarRightBtnClick') // 取消訂閱
        next()
      }
    } else {
      this.$parent.$off('navbarRightBtnClick') // 取消訂閱
      next()
    }
  },
  mounted() {
    this.$navbar.normalVisitStyle()
    if (this.$navbar.normalVisitPreviousPage === 'home') {
      this.$navbar.setLeftBtn('返回', '', '/home')
    } else if (this.$navbar.normalVisitPreviousPage === 'myVisit') {
      this.$navbar.setLeftBtn('返回', '', '/my_visit')
    }

    // navbarRightBtnListener ...
    const self = this
    this.$parent.$on('navbarRightBtnClick',(param => { // 註冊訂閱
      // 送出庫存訂單
      self.submitStockAndOrder().then(() => {
        self.CLEAR_PRODUCT_DETAIL_TOTAL()
          // 送出完成拜訪
        self.submitFinishVisit({
          visitType: '1',
          visitStoreStatusCode: '',
          otherStoreStatusName: '',
          visitRemark: '',
          isForce: '0',
        }).then(() => {
          self.$vux.confirm.show({
            title: '提示',
            content: `是否确认完成拜访?`,
            onConfirm() {
              self.forceSubmitFinishVisit(self)
            },
          })
        }).catch((error) => {
          // 錯誤訊息處理
          if (error.body != null &&  typeof error.body.dealType === 'string' && error.body.dealType === 'REDIRECT') {
            if (error.body.dealValue === 'VISIT_PHOTO') { // 照片採集
              self.$router.push('/normal_visit/general_photo_collection')
            } else if (error.body.dealValue === 'VISIT_SD') { // 特陳檢核
              self.$router.push('/normal_visit/special_photo_collection')
            }
          }
          // 強制送出
          if (error.code === 'c110') {
            setTimeout(() => {
              self.$vux.confirm.show({
                title: '提示',
                content: `${error.message}`,
                onConfirm() {
                  self.forceSubmitFinishVisit(self)
                },
              })
            }, 500)
          }
        })
      })

    }))
  },
  methods: {
    ...mapActions('normalVisit', [
      'submitFinishVisit',
      'submitStockAndOrder',
    ]),
    ...mapMutations('store', [
      'SET_SELECTED_MARKET_PLACE_ELE',
      'SET_SELECTED_LIST_TYPE',
    ]),
    ...mapMutations('normalVisit', [
      'CLEAR_PRODUCT_DETAIL_TOTAL',
    ]),
    forceSubmitFinishVisit(self) {
      return new Promise ((resolve, reject) => {
        self.submitFinishVisit({
          visitType: '1',
          visitStoreStatusCode: '',
          otherStoreStatusName: '',
          visitRemark: '',
          isForce: '1',
        }).then(() => {
          self.isSubmit = true
          self.$vux.alert.show({
            title: '提示',
            content: `拜访完成`,
            onHide(){
              self.$router.push('/home')
            }
          })
          resolve()
        }).catch((error) => {
          reject(error)
        })
      })
    }
  },
}



