import UnselectedIcon from '@/assets/img/common/choose_n.png'
import SelectedIcon from '@/assets/img/common/choose_y.png'

export default {
  props: {
    selectionStrArray: {
      type: Array,
      default: () => [],
    },
    haveBottomTextArea: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    textAreaValue: {
      get() {
        return this.textValue
      },
      set(newValue) {
        this.textValue = newValue
        this.$emit('selectorTextOnChange', this.textValue)
      },
    },
  },
  data() {
    return {
      selectionObjArray: [],
      textValue: '',
    }
  },
  mounted() {
    for (const selectionStr of this.selectionStrArray) {
      const selectionObj = {}
      selectionObj.title = selectionStr
      selectionObj.imageUrl = UnselectedIcon
      this.selectionObjArray.push(selectionObj)
    }
    // default: ['终端休业','终端转行','临时关门','其他'],
  },
  methods: {
    cleanAllTick(array) {
      // return array.map( (selectionObj) => { selectionObj.imageUrl = UnselectedIcon } )
      for (const selectionObj of array) {
        selectionObj.imageUrl = UnselectedIcon
      }
      return array
    },
    selectorHandler(contentType, title, content, index) {
      this.selectionObjArray = this.cleanAllTick(this.selectionObjArray)
      if (contentType === 'selectionTitle') {
        this.selectionObjArray[index].imageUrl = SelectedIcon
      } else if (contentType === 'selectionMsg') {
        this.selectionObjArray[this.selectionObjArray.length - 1].imageUrl = SelectedIcon
      }
      this.$emit('itemOnClick', title, content, index)
    },
  },
}



