import { 
  QUERY_SEARCH_STORE,
  SET_SELECTED_STORE_ELE,
  QUERY_STORE_DETAIL,
  QUERY_WECHAT_MARKET_PLACE,
  SET_SELECTED_MARKET_PLACE_ELE,
  QUERY_FORTH_CITY_LIST,
  QUERY_POI_LEVEL,
  CLEAR_WECHAT_MARKET_PLACE_LIST,
  SET_SELECTED_LIST_TYPE,
  QUERY_WECHAT_MARKET_PLACE_WITH_ID,
  QUERY_WECHAT_MARKET_PLACE_DEFAULT_CACHE,
  REMARK_WECHAT_MARKET_PLACE_LIST,
} from './mutationTypes'

import UnselectedIcon from '@/assets/img/common/choose_n.png'
import SelectedIcon from '@/assets/img/common/choose_y.png'

const mutations = {
  [QUERY_SEARCH_STORE](state, payload) {
    let index = 0
    const tempList = payload.storeList.map((storeInfo) => {
      const tempItem = Object.assign({}, storeInfo)
      tempItem.sIndex = index
      index += 1
      tempItem.imageUrl = UnselectedIcon // for personal.SelectStore.StoreList mutibleSelect
      return tempItem
    })
    state.storeList = tempList
    state.pageInfo = payload.pageInfo
  },
  [SET_SELECTED_STORE_ELE](state, payload) {
    state.selectedStoreEle = payload
  },
  [QUERY_FORTH_CITY_LIST](state, payload) {
    const cityStrList = payload.map((item) => {
      return item.forthCityName
    })
    state.forthCityList = cityStrList
  },
  [QUERY_STORE_DETAIL](state, payload) {
    state.storeDetailEle = payload
  },
  [QUERY_POI_LEVEL](state, payload) {
    // state.storeDetailEle = payload
  },
  [QUERY_WECHAT_MARKET_PLACE_DEFAULT_CACHE](state, payload) {
    state.defaultMarketPlaceList = payload
  },
  [QUERY_WECHAT_MARKET_PLACE](state, storeList) {
    // 累加 或是 直接賦值
    if (Array.isArray(state.marketPlaceList) && state.marketPlaceList.length > 1) {
      state.marketPlaceList = [...state.marketPlaceList, ...storeList]
    } else {
      state.marketPlaceList = storeList
    }
  },
  [QUERY_WECHAT_MARKET_PLACE_WITH_ID](state, storeItem) {
    // state.marketPlaceList = payload.map((markePlaceItem) => {
    // 將 wechat market place 接回來的資料 多擴增 讓列表頁能使用的 資訊
    const tempItem = {...storeItem}
    // for store.storeList
    // tempItem.sIndex = `${pageNum}${index}`
    // tempItem.imageUrl = UnselectedIcon // for personal.SelectStore.StoreList mutibleSelect
    // tempItem.storeName = markePlaceItem.poiName
    // tempItem.mobile = markePlaceItem.poiTel
    // tempItem.address = markePlaceItem.poiRealAddress
    // for storeDetail
    tempItem.regingName = storeItem.districtName
    tempItem.standardMarketName = storeItem.marketName
    // 'http://ditu.amap.com/place/B0FFH9VHIT', >> B0FFH9VHIT
    const strArray = storeItem.poiId.split('/')
    const poiId = strArray[strArray.length - 1]
    tempItem.poiId = poiId
    // 累加 或是 直接賦值
    state.selectedMarketPlaceEle = tempItem
  },
  [REMARK_WECHAT_MARKET_PLACE_LIST](state, payload) {
    const additionStoreList = payload
    if (Array.isArray(additionStoreList) && additionStoreList.length > 0) {
      state.marketPlaceList.forEach((wechatMarketPlace) => { // for personal_select_store 的 mutibleSelect
        additionStoreList.forEach((additionMarketPlace) => {
          if (wechatMarketPlace.poiId != undefined && wechatMarketPlace.poiId === additionMarketPlace.poiId) {
            wechatMarketPlace.imageUrl = SelectedIcon
          }
        })
      })
    }
  },
  [CLEAR_WECHAT_MARKET_PLACE_LIST](state) {
    state.defaultMarketPlaceList.forEach((marketPlace) => {
      marketPlace.imageUrl = UnselectedIcon
    })
    state.marketPlaceList = []
  },
  [SET_SELECTED_MARKET_PLACE_ELE](state, payload) {
    state.selectedMarketPlaceEle = payload
  },
  [SET_SELECTED_LIST_TYPE](state, payload) {
    state.selectedListType = payload
  },
}

export default mutations



