import { mapGetters, mapActions} from 'vuex'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    PhotoCollector,
  },
  data() {
    return {
      // photo type
      DOOR_WAY_PHOTO: 'DOOR_WAY',
      OTHER_PHOTO: 'OTHER',
      // doorWayPhotoInfoList: [],
      // otherPhotoInfoList: [],
    }
  },
  computed: {
    ...mapGetters('home', [
      'getSelectedVisitEle',
		]),
    ...mapGetters('normalVisit', [
      'getVisitDetail',
      'getDoorWayPhotoInfoList',
      'getOtherPhotoInfoList',
		]),
  },
  mounted() {
    // this.refereshData()
  },
  methods: {
    ...mapActions('normalVisit', [
      'queryVisitDetail',
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    takePhoto(photoType) {
      const typePhotoArray = photoType === this.DOOR_WAY_PHOTO ? this.getDoorWayPhotoInfoList : this.getOtherPhotoInfoList
      this.checkPhotoArrayCount({self:this, photoArray:typePhotoArray}).then(() => {
        this.takePicture({ photoType }).then(resp=> {
          this.queryVisitDetail().then(resp => {})
        })
      })
    },
  },
}



