import { mapGetters, mapActions, mapMutations } from 'vuex'
import CommonHeader from '@/components/commonHeader'
import PhotoCollector from '@/components/PhotoCollector'

const ua = navigator.userAgent
// let mobileSys
let keybordHeight = '0'

/* 判斷手機系統 */
// if (ua.indexOf('Mac') > -1) {
//   mobileSys = 'ios'
// } else if (ua.indexOf('Android') > -1) {
//   mobileSys = 'android'
// }

export default {
  components: {
    CommonHeader,
    PhotoCollector,
  },
  data() {
    return {
      detailType: '', // 活動狀態(新增add、已有資料save)
      eventTypeList: [], // 活動類型清單
      readOnly: false, // 限制僅能讀取
      selectStore: {}, // 新增活動點"門店類型"後選擇的終端資訊
      eventDetail: {
        eventSid: '', // 活動sid(新增活動為空值)
        type: '', // EventType(门店活动0、其他活动1)
        storeId: '',
        storeName: '',
        target_type: '', // 客户类型(集團終端0、建議終端1)
        address: '', // 活動地址
        description: '', // 活動內容文字
        photoList: [
          {
            photoSid: '',
            photoUrl: '',
          },
        ], // 照片清單
      },
      tempPhotoArray: [],
      radioEventType: '',
      selectedStoreData: {
          // 建議終端
        // poiTel: '',
        // poiTypeList: '',
        // districtName: '',
        // companyName: '',
        // branchId: '',
        // branchName: '',
        // marketName: '',
        // smallMarketName: '',
        // wantForthName: '',
        // longitude: '',
        // latitude: '',
          // 建議終端
        // address
        // imageUrl
        // mobile
        // phone
        // sIndex
        // storeId
        // storeName
        // storeOwner
        // storeState
      },
      // selectedStoreType: '',
      isSubmit: false,
      // mobileSys: mobileSys,
      keybordHeight: keybordHeight,
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.path !== '/campaign_select_store') {
      if (this.isSubmit) {
        this.SET_CREATE_CAMPAIGN_TEMP_ELE({}) // 將暫存設空
        next()
      } else {
        const self = this
        this.$vux.confirm.show({
          title: '提示',
          content: '活动记录未保存，<br>返回/取消将被清除。<br>确认要返回/取消吗？',
          onConfirm() {
            self.SET_CREATE_CAMPAIGN_TEMP_ELE({}) // 將暫存設空
            next()
          },
          onCancel() {},
        })
      }
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters('campaign', ['getCreateCampaignTempEle']),
    showStoreName() {
      if (this.selectedStoreData.storeName === ('' || undefined)) {
        return false
      } else {
        return true
      }
    },
  },
  mounted() {
    window.getAndroidStatus = this.androidKeyboard
    this.$navbar.createCampaignStyle()
    this.detailType = this.$route.query.detailType
    this.getEventTypeList()
    if (this.$route.params.selectedData) {
      this.selectedStoreData = { ...this.$route.params.selectedData }
      // this.selectedStoreType = this.$route.params.type
      this.eventDetail.address = this.$route.params.selectedData.address
      this.eventDetail.target_type = this.$route.params.type
      this.radioEventType = '0'
    }
    if (this.getCreateCampaignTempEle.tempPhotoArray !== undefined) {
      this.tempPhotoArray = [...this.getCreateCampaignTempEle.tempPhotoArray]
      this.eventDetail.description = this.getCreateCampaignTempEle.description
    }
  },
  methods: {
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    ...mapActions('personal', ['queryAttributeInfoList']),
    ...mapActions('campaign', ['submitEvent']),
    ...mapMutations('campaign', ['SET_CREATE_CAMPAIGN_TEMP_ELE']),
    takePhoto(photoType) {
      // 新增照片
      if (this.readOnly) {
        return
      }
      this.checkPhotoArrayCount({ self: this, photoArray: this.tempPhotoArray }).then(() => {
        this.takePicture({ photoType }).then((resp) => {
          this.tempPhotoArray.push(resp)
        })
      })
    },
    androidKeyboard(height, pageUrl) {
      let page = document.getElementById('app')
      if (height === '0') {
        // 鍵盤收起
        page.style.top = '0'
      } else {
        page.style.top = '-' + 0.2 * height + 'px'
      }
    },
    getEventTypeList() {
      // 取得活動類型清單
      this.queryAttributeInfoList('EVENT').then((resp) => {
        this.eventTypeList = resp.body
      })
    },
    radioCheck(eventTypeName) {
      // 選擇活動類型
      this.eventTypeName = eventTypeName
      const self = this
      if (eventTypeName === '门店活动') {
      this.radioEventType = '0'
      const tempEle = {
        tempPhotoArray: this.tempPhotoArray,
        description: this.eventDetail.description,
      }
      this.SET_CREATE_CAMPAIGN_TEMP_ELE(tempEle) // 暫存已填資料
      this.$router.push('/campaign_select_store')
      } else if (eventTypeName !== '门店活动') {
        this.selectedStoreData = {}
        this.radioEventType = '1'
        this.eventDetail.address = '地址为现场定位地址不可修改'
        this.loadmap()
      }
    },
    /* 點擊定位不准确, 檢查是否已選擇活动类型 */
    getLocation() {
      if (this.radioEventType === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择活动类型`,
        })
        this.eventDetail.address = ''
        return
      } else {
        this.loadmap()
      }
    },
    loadmap() {
      // 加載Amap
      this.eventDetail.address = '定位中...'
      const self = this
      const mapObj = new AMap.Map('amapBlock')
      mapObj.plugin(['AMap.Geolocation'], () => {
        const geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //  是否使用高精度定位，默认:true
          timeout: 10000, //  超过10秒后停止定位，默认：无穷大
          maximumAge: 0, // 定位结果缓存0毫秒，默认：0
          convert: true, // 自动偏移坐标，偏移后的坐标为高德坐标，默认：true
        })
        geolocation.getCurrentPosition()
        // 返回定位信息
        AMap.event.addListener(geolocation, 'complete', (result) => {
          this.regeocoder(result.position.lng, result.position.lat)
        })
        // 返回定位出错信息
        AMap.event.addListener(geolocation, 'error', (result) => {
          self.eventDetail.address = '定位失败'
        })
      })
    },
    regeocoder(lng, lat) {
      // 逆地理编码，根据经纬度获取详细地址信息
      // 寫法二
      let geocoder
      AMap.service('AMap.Geocoder', () => {
        geocoder = new AMap.Geocoder({})
      })
      const lanlat = new AMap.LngLat(lng, lat)
      const lnglatXY = [lanlat.getLng(), lanlat.getLat()]
      geocoder.getAddress(lnglatXY, (status, result) => {
        if (status === 'complete' && result.info === 'OK') {
          this.eventDetail.address = result.regeocode.formattedAddress
          // this.geocoder_CallBack(result)
        } else {
          this.eventDetail.address = '定位失败'
        }
      })
    },
    btnCancel() {
      // 取消按鈕
      this.$router.push('/campaign')
    },
    btnSubmit() {
      
      // 確認按鈕
      // 活動類型若沒選擇，提醒'请选择活动类型'
      if (this.radioEventType === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择活动类型`,
        })
        return
      }
      // 活動類型若沒選擇，提醒'请选择活动类型'
      if (
        this.radioEventType === '1' &&
        (
          this.eventDetail.address === '定位失败' ||
          this.eventDetail.address === '地址为现场定位地址不可修改' ||
          this.eventDetail.address === '定位中...'
        )
        // this.eventDetail.address === ('定位失败' || '地址为现场定位地址不可修改' || '定位中...')
      ) {
        this.$vux.alert.show({
          title: '提示',
          content: `请等候定位`,
        })
        return
      }
      // 活動內容應為必填欄位，未填寫提醒“请填写活动内容”
      if (this.eventDetail.description === '') {
        this.$vux.alert.show({
          title: '提示',
          content: `请填写活动内容`,
        })
        return
      }
      const photoList = this.tempPhotoArray.map((photo) => photo.photoSid)
      if (Array.isArray(photoList) && photoList.length < 1) {
        this.$vux.alert.show({
          title: '提示',
          content: `未拍摄照片`,
        })
        return
      }
      if (this.radioEventType === '0') { // 判斷勾選定位方式, 定位 or 選擇終端
        const isFromStore = this.eventDetail.target_type === 'STORE' // 判斷是哪種選擇終端, 集團 or 建議
        const {
          storeId = '',
          storeName = '',
          address = '',
          poiId = '',
          poiName = '',
          // 建議終端
          poiTel = '',
          poiTypeList = '',
          districtName = '',
          companyName = '',
          branchId = '',
          branchName = '',
          marketName = '',
          smallMarketName = '',
          wantForthName = '',
          longitude = '',
          latitude = '',
        } = this.selectedStoreData
        const param = {
          submitType: 0, // 提交類型: 新增為0 , 修改為1
          type: this.radioEventType, // 活動類型: 門店活動為0 , 其他活動為1
          target_type: isFromStore ? '0' : '1', // 終端類型: 集團終端為0 , 建議終端為1
          eventSid: '',
          storeId: isFromStore ? storeId : poiId,
          storeName: isFromStore ? storeName : poiName,
          address: this.radioEventType == 0 ? address : this.eventDetail.address,
          poiTel,
          poiTypeList,
          districtName,
          companyName,
          branchId,
          branchName,
          marketName,
          smallMarketName,
          wantForthName,
          longitude,
          latitude,          
          description: this.eventDetail.description,
          photoList,
        }
        if (this.allParamSettle(param)) {
          this.submitEventWithParam(param)
        } else {
          this.$vux.alert.show({
            title: '提示',
            content: `请等候定位`,
          })
        }
      } else if (this.radioEventType === '1') {
        const param = {
          submitType: 0, // 提交類型: 新增為0 , 修改為1
          type: this.radioEventType, // 活動類型: 門店活動為0 , 其他活動為1
          target_type: '', // 終端類型: 集團終端為0 , 建議終端為1
          eventSid: '',
          storeId: '',
          storeName: '',
          address: this.eventDetail.address,
          description: this.eventDetail.description,
          photoList,
        }
        this.submitEventWithParam(param)
      }
    },
    submitEventWithParam(param) {
      this.submitEvent(param).then((resp) => {
        if (resp.code === 'c200') {
          let $this = this
          this.$vux.alert.show({
            title: '提示',
            content: '提交成功！',
            onHide() {
              // 跳轉至活動
              $this.isSubmit = true
              $this.$router.push('campaign')
            },
          })
        } else {
          this.$vux.alert.show({ title: '提示', content: '提交失敗！' })
          return
        }
      })
    },
    allParamSettle(param) {
      const typeSettle = param.type !== ''
      const storeIdSettle = param.storeId !== ''
      const storeNameSettle = param.storeName !== ''
      const targetTypeSettle = param.target_type !== ''
      const addressSettle =
        param.address !== '' &&
        param.address !== '定位失败' &&
        param.address !== '定位中...' &&
        param.address !== '地址为现场定位地址不可修改'
      const storeEventParamSettle =
        typeSettle && storeIdSettle && storeNameSettle && targetTypeSettle && addressSettle
      const otherEventParamlSettle = typeSettle && addressSettle
      if (this.radioEventType == 0 && !storeEventParamSettle) {
        // 活動類型: 門店活動為0 , 其他活動為1
        return false
      } else if (!otherEventParamlSettle) {
        return false
      }
      return true
    },
    checkInputValue(value) {
      // 檢查輸入資料，將&"'<>轉成編碼
      let returnValue = ''
      if (value) {
        returnValue = value
          .trim()
          .replace(/&/g, '&#38;')
          .replace(/"/g, '&#34;')
          .replace(/'/g, '&#39;')
          .replace(/</g, '&#60;')
          .replace(/>/g, '&#62;')
      }
      return returnValue
    },
  },
}



