/* eslint-disable */
export default {
  // props: ['select-list'],
  props: {
    selectList: {
      type: Array,
      default: () => [],
    },
    showSelectAll: {
      type: Boolean,
      default: false
    },
  },
  data (){
    return {
      selectShow: true,
      selectedStr: '请选择',
      editable: false
    }
  },
  mounted () {
    // this.selectedStr = this.selectedString
  },
  methods: {
    /* 下拉选单開啟或關閉 */
    openSelector() {
      if (this.$parent.submitType === 1) {
        return
      } else {
        this.selectShow = !this.selectShow
      }
    },
    /*  所点击的下拉选单选项 */
    getSelectValue (str){
      /* 寫法 2 */
      this.selectedStr = str
      this.$emit('to-parent-select', this.selectedStr)
    }
  }
}



