import Vue from 'vue'
import VueRouter from 'vue-router'

import routes from './routes'
import store from '../store'

Vue.use(VueRouter)

const router = new VueRouter({
  // mode: 'history',
  routes,
})

router.beforeEach((to, from, next) => {
  // console.log('router.beforeEach')
  // console.log(typeof store.getters['navBar/getNavBarMiddleTitle'])
  // console.log(store.getters['navBar/getNavBarMiddleTitle'])
  next()

  // login page
  if (to.path === '/login') {
    store.dispatch('footBar/hideFootBar')
  }

  // nav bar left side, back button and foot bar
  if (to.path === '/home' || to.path === '/attendance' || to.path === '/store' || to.path === '/campaign' || to.path === '/personal') {
    store.dispatch('footBar/showFootBar')
  } else {
    store.dispatch('footBar/hideFootBar')
  }
})

router.afterEach(() => {
  // console.log('router.afterEach')
  // console.log(typeof store.getters['navBar/getNavBarMiddleTitle'])
  // console.log(store.getters['navBar/getNavBarMiddleTitle'])
})
export default router



