import { mapGetters, mapActions, mapMutations } from 'vuex'
import CommonHeader from '@/components/commonHeader'
import StoreInfo from '@/pages/store/components/StoreInfo'
import OrderRunChart from '@/pages/store/components/OrderRunChart'

export default {
  components: {
    CommonHeader,
    StoreInfo,
    OrderRunChart,
  },
  data() {
    return {
      STORE_LIST: 'store',
      SUGGESTED_LIST: 'suggested',

      storeType: '', // 終端類型(集團store/建議suggested)
      storeId: '', // 終端id
      storeInfo: {}, // 終端詳細資料
      chartSeriesData: {},
    }
  },
  computed: {
    ...mapGetters('store', [
      'getSelectedStoreDetail',
      'getSelectedMarketPlaceEle',
      'getSelectedListType',
    ]),
    ...mapGetters('normalVisit', [
      'getVisitDetail',
    ]),
  },
  mounted() {
    const isSroreList = this.getSelectedListType === this.STORE_LIST
    this.storeInfo = isSroreList ? this.getSelectedStoreDetail : this.getSelectedMarketPlaceEle

    // this.storeInfo = this.getSelectedStoreDetail
    // const param = isSroreList ? '' : this.storeInfo.poiId
    this.queryStorePerformance(this.getVisitDetail.storeId).then((resp) => {
      this.chartSeriesData = this.reconstructChartEle(resp)
      this.$nextTick(() => {
        this.$refs.OrderRunChart.getSeriesData() // 呼叫元件更新資料的方法
      })
    })
  },
  methods: {
    ...mapActions('store', [
      'queryStorePerformance',
      'submitAddStoreFromSuggestedPoi'
    ]),
    sliceChartEle(chartEle) { // 將各筆資料打平成數組
      const {MaxValue, performanceType} = chartEle
      const years = []
      const months = []
      const performanceNumbers = []
      chartEle.performanceList.forEach((ele) => {
        years.push(ele.year)
        months.push(ele.month)
        performanceNumbers.push(parseInt(ele.performanceNumber))
      })
      return {years, months, performanceNumbers, MaxValue, performanceType}
    },
    reconstructChartEle(resp) { // 合併 amount 和 qty 成為 chartSeriesData
      const chartArray = resp.map((ele) => {
        return this.sliceChartEle(ele)
      })
      return {
        quantitySeriesData: chartArray[0].performanceNumbers,
        quantityXCategories: chartArray[0].months,
        quantityMax: chartArray[0].MaxValue,
        moneySeriesData: chartArray[1].performanceNumbers,
        moneyXCategories: chartArray[1].months,
        moneyMax: chartArray[1].MaxValue,
      }
    },
    addScheduleBtnOnClick() {
      this.$router.push('/store_addroute')
    },
    addMarketPlaceBtnOnClick() {
      // const { poiId } = this.storeInfo
      // this.submitAddStoreFromSuggestedPoi(poiId)
      this.$router.push('/developing')
    },
    editPencilClick() {
      this.$router.push('/developing')
    },
  },
}



