import { mapMutations } from 'vuex'
import UnselectedIcon from '@/assets/img/common/choose_n.png'
import SelectedIcon from '@/assets/img/common/choose_y.png'
import LoadingFailureImg from '../../../assets/img/common/loading_failure@2x.gif'
import LoadingImg from '../../../assets/img/common/loading2.gif'

export default {
  props: {
    list: {
      type: Array,
      default() {
        return []
      },
    },
    enablePullDown: {
      type: Boolean,
      default: false,
    },
    isLoading: {
      type: Boolean,
      default: true,
    },
    isNoData: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      pullupDefaultConfig: {
        content: '',
        pullUpHeight: 60,
        height: 40,
        autoRefresh: false,
        downContent: '释放后加载',
        upContent: '',
        loadingContent: '加載中...',
        clsPrefix: 'xs-plugin-pullup-'
      },
      loadingImg: '',
      loadingFailureImg: '',
    }
  },
  computed: {
    storeList: {
      // set() {}, // 無用
      get() { // 為了讓 scroller 在每次 list 被重置時，能自動將瀏覽位置回到最頂端
        this.$nextTick(()=>{
          if (this.$refs.scrollerBottom != undefined && this.$refs.scrollerBottom._xscroll != undefined && Array.isArray(this.list) && this.list.length == 0) {
            this.$refs.scrollerBottom.reset({ top: 0 })
          }
        })
        return this.list
      },
    }
  },
  mounted () {
    this.loadingFailureImg = LoadingFailureImg
    this.loadingImg = LoadingImg
  },
  methods: {
    ...mapMutations('personal', [
      'ADD_STORE_LIST',
      'SUBTRACT_STORE_LIST',
    ]),
    addSlash(mobile, phone) { // 若同時有電話及手機，以 / 區隔
      let result = ''
      if (mobile && phone) {
        result = `${mobile} / ${phone}`
      } else {
        result = mobile || phone
      }
      return result
    },
    sendStoreId(store) { // 傳送點選的store
      if (store.imageUrl === SelectedIcon) {
        this.SUBTRACT_STORE_LIST(store)
      } else {
        this.ADD_STORE_LIST(store)
      }
    },
    loadMore() {
      this.$emit('load-more')
      this.$refs.scrollerBottom.donePullup()
    },
  },
}



