import PhotoSwiper from '@/components/PhotoSwiper'
import ScanIcon from '@/assets/img/common/scan@2x.png'

export default {
  components: {
    PhotoSwiper,
    // VueGallery,
  },
  props: {
    photoArray: {
      type: Array,
      default: () => [],
    },
    collectorTitle: {
      type: String,
      default: '',
    },
    collectorDescription: {
      type: String,
      default: '',
    },
    readOnly: {
      type: Boolean,
      default: false,
    },
    useBottomGreyLine: {
      type: Boolean,
      default: false,
    },
    isShowScanAssistantName: { // 0:隱藏, 1:顯示
      type: String,
      default: '0',
    },
    scanAssistantName: {
      type: String,
      default: '',
    },
    isLinkToScanQRCode: { // 0:否, 1:是
      type: String,
      default: '0',
    }
  },
  data() {
    return {
      galleryIndex: 0,
      // swiperImages: [],
      swiperIndex: 0,
      swiperIsVisible: false,
      scanIcon: ScanIcon,
    }
  },
  computed: {
    galleryImages() {
      return this.photoArray.map(photoEle => photoEle.photoUrl )
    },
  },
  mounted() {
  },
  methods: {
    scanQRBtnOnClick(isLinkToScanQRCode) {
      if (isLinkToScanQRCode == 1) {
        this.$emit('scanQRBtnOnClick')
      }
    },
    addPicButtonOnClick() {
      this.$emit('addPicButtonOnClick', false)
    },
    imgOnClick(index) {
      this.swiperIndex = index
      this.swiperIsVisible = true
      // this.$emit('imgOnClick', photoInfo)
      // this.$router.push('photo_swiper')
    },
    swiperOnSwipe(index) {
      this.swiperIndex = index
    },
  },
  beforeRouteLeave(to, from, next) {
    // to.swiperList = this.photoArray
    next()
  },
}



