import { mapGetters, mapActions} from 'vuex'
import PhotoCollector from '@/components/PhotoCollector'

export default {
  components: {
    PhotoCollector,
  },
  computed: {
    ...mapGetters('normalVisit', [
      'getDisplayType',
      'getVisitDetail',
    ]),
  },
  data() {
    return {
      SPECIAL_DISPLAY_PHOTO: 'SPECIAL_DISPLAY',
      uncategorizedPhotoInfo: {},
      selectedType: {},
      displayTypeArray: [],
      photoInfoList: [],
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.path === '/normal_visit/special_photo_collection') {
      if (this.selectedType.specialDisplayTypeSid == undefined) {
        this.$vux.alert.show({
          title: '提示',
          content: `请选择特陈类别`,
        })
        return
      }
      this.setPhotoSpecialDisplayType()
      if (this.selectedType.isNeedScanQRCode == 1) {
        this.scanAssistantQRCode(this.selectedType.specialDisplayTypeSid).then((resp) => {
          this.queryVisitDetail()
          next()
        }).catch(() => {
          this.queryVisitDetail()
          next()
        })      
      } else {
        next()
      }
    } else {
      next()
    }
  },
  mounted() {
    this.$navbar.selectPhotoTypeStyle()
    // this.refereshData()
    this.photoInfoList = this.$route.params.photoArray
    this.queryDisplayType().then((resp) => {
      // if (resp.code == 'c200') {
      this.displayTypeArray = this.getDisplayType
      // }
    })
  },
  methods: {
    ...mapActions('normalVisit', [
      'queryDisplayType',
      'queryVisitDetail',
      'checkPhotoArrayCount',
      'scanAssistantQRCode', // 1 param: specialDisplayTypeSid = ''
    ]),
    selectType(index, type) {
      this.displayTypeArray = this.displayTypeArray.map((typeItem) => {
        if (typeItem === type) {
          typeItem.isSelected = true
          this.selectedType = typeItem
        } else {
          typeItem.isSelected = false
        }
        return typeItem
      })
    },
    refereshData() {
      this.queryVisitDetail().then((resp) => {
        if (resp.code === 'c200') {
          for (const visitPhotoEle of this.getVisitDetail.visitPhotoInfo) {
            if (visitPhotoEle.photoDisplayType === this.SPECIAL_DISPLAY_PHOTO) {
              if (visitPhotoEle.specialDisplayTypeSid === '') {
                console.log(visitPhotoEle)
                this.uncategorizedPhotoInfo = visitPhotoEle
              }
            }
          }
        }
      })
    },
    setPhotoSpecialDisplayType() {
      return new Promise((resolve, reject) => {
        let photoList = []
        if (Array.isArray(this.photoInfoList) && this.photoInfoList.length > 0) {
          photoList = this.photoInfoList.map((photoInfo) => {
            return photoInfo.photoSid
          })
        }
        const { visitSid } = this.getVisitDetail
        const specialDisplayTypeSid = this.selectedType.specialDisplayTypeSid
        const functionName = 'setPhotoSpecialDisplayType'
        const param = {
          visitSid,
          specialDisplayTypeSid,
          photoList,
        }
        this.$store.$native.execute(functionName, param).then((resp) => {
          this.queryVisitDetail()
          resolve(resp)
        }).catch((e) => {
          this.queryVisitDetail()
          reject(e)
        })
      })
    },
  },
}



