import {
  QUERY_CLOCK_IN_OUT_EMP_INFO,
  // SUBMIT_CLOCK_IN,
  // QUERY_DEPARTURE_ADDRESS_LIST,
  // SUBMIT_CLOCK_OUT,
  QUERY_BRANCH_DEPARTURE_ADDRESS_LIST,
  QUERY_CUSTOMER_DEPARTURE_ADDRESS_LIST,
} from './mutationTypes'

const actions = {
  queryClockInOutInfo(context) {
    return new Promise((resolve, reject) => {
      const functionName = 'getClockInOutInfo'
      this.$native.execute(functionName).then((resp) => { // success ,param:resp
        context.commit(QUERY_CLOCK_IN_OUT_EMP_INFO, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  submitClockIn(context, { clockInAddressId, clockInAddress, photoSidList }) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitClockIn'
      const param = {
        clockInAddressId, // '32_0011009093_0012006570',
        clockInAddress, // '湖南省郴州地区永兴县便江镇坳头上街40号',
        photoSidList, // ['18020214323329688800122963', '']
      }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        // context.commit(SUBMIT_CLOCK_IN, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  queryDepartureAddressList(context, departureType) {
    return new Promise((resolve, reject) => {
      const functionName = 'getDepartureAddressList'
      const param = {
        departureType, // 0:营业所 1:经销商
      }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        if (Array.isArray(resp.body) && resp.body.length > 0) {
          let index = departureType === '1' ? 0 : 1000
          resp.body.forEach((addressEle) => {
            addressEle.index = index
            index += 1
          })
        }
        if (departureType === '1') {
          context.commit(QUERY_CUSTOMER_DEPARTURE_ADDRESS_LIST, resp.body)
        } else if (departureType === '0') {
          context.commit(QUERY_BRANCH_DEPARTURE_ADDRESS_LIST, resp.body)
        }
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
  submitClockOut(context, photoSidList) {
    return new Promise((resolve, reject) => {
      const functionName = 'submitClockOut'
      const param = {
        photoSidList, // 签到照片ID list
      }
      this.$native.execute(functionName, param).then((resp) => { // success ,param:resp
        // context.commit(SUBMIT_CLOCK_OUT, resp.body)
        resolve(resp)
      }).catch((e) => { // fail ,param:error
        reject(e)
      })
    })
  },
}

export default actions



